import React, { useState } from "react";
import Config from "../../../../config";
import CountryCodes from "../sub-elements/CountryCode"
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Language from "../../Language";

const Phone = (props) => {
    const lang = React.useContext(Language);

    const { item , originalItem } = props;
    const { defaultValue, countryCodeGetJson, disabled, readOnly, inputIcon, phoneCodes, phoneCodeControl, labelText, fieldSize, placeholder, cssClasses, fieldWidth } = item.payload;

    const [value, setValue] = useState(defaultValue);

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
    }    

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${value != "" ? `mf-active` : ``}  ${inputIcon ? `mf-with-icon` : ``}  `}  >
                <div className={"mf-input-group " + ((fieldWidth!="full" && fieldWidth!= "large") ?"mf-flex-initial":"")}>
                    {inputIcon && <div className="mf-input-group-prepend">
                        <span className="mf-input-group-text" >
                            <FontAwesomeIcon icon={['fas', inputIcon]} />
                        </span>
                    </div>}
                    {phoneCodeControl &&
                        <>
                            {countryCodeGetJson ? (
                                <select
                                    style={{ flex: "0 0 200px" }}
                                    className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}  ${Config.width[fieldWidth]}`}
                                    disabled={disabled}
                                    id={item.id} >
                                    <option value="">{lang["Please Select"]}</option>
                                    {
                                        phoneCodes.map((item, index) =>
                                            <option value={item.phoneCode} key={index}> {item.name + " ("+item.phoneCode+")"} </option>
                                        )
                                    }
                                </select>
                            ) : <CountryCodes cssClasses={cssClasses} disabled={disabled} fieldWidth={Config.width[fieldWidth]} fieldSize={Config.size[fieldSize]} id={item.id} />
                            }
                        </>
                    }
                    <input
                        type='text'
                        value={value}
                        onChange={(e) => setValue(e.target.value)}
                        placeholder={placeholder}
                        readOnly={readOnly}
                        disabled={disabled}
                        className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
                        id={item.id}
                    />
                    {item.theme.startsWith("material") && labelText != "" && <span style={phoneCodeControl ? { left: inputIcon ? 250 : 210 } : {}} className="mf-material-label">{labelText}</span>}

                </div>
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout >
    );
}

export default Phone;

