import React from "react";
import { Radio, Input } from "antd";
import Language from '../Language';

const ButtonOnClick = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div className="mf-part-item">
                <span className="mf-part-title"  style={{marginRight:"5px"}}>Button Onclick or Href</span>
                <Radio.Group value={props.eventType} onChange={(e) => props.handleChange("eventType", e.target.value)}>
                    <Radio value="onclick">Onclick</Radio>
                    <Radio value="href">Href</Radio>
                </Radio.Group>
                </div>
                {
                    props.eventType == "onclick" && 
                    <>
                        <span className="mf-part-title" style={{marginRight:"5px"}}>Onclick Event</span>
                        <Input value={props.buttonOnclick}
                            placeholder="return myfunction();"
                            onChange={(e) => props.handleChange("buttonOnclick", e.target.value)}
                    />
                </>
                }
                {
                    props.eventType == "href" &&
                    <>
                        <span className="mf-part-title" style={{marginRight:"5px"}}>Href Link</span>
                        <Input value={props.buttonHref}
                            placeholder="https://magicform3.com"
                            onChange={(e) => props.handleChange("buttonHref", e.target.value)}
                        />
                        <div className="mf-part-item">
                <span className="mf-part-title" style={{marginRight:"5px"}}>{lang['Link Forwarding']}</span>
                            <Radio.Group value={props.forwardType} onChange={(e) => props.handleChange("forwardType", e.target.value)}>
                                <Radio value="thisTab">{lang['This Tab']}</Radio>
                                <Radio value="newTab">{lang['New Tab']}</Radio>
                            </Radio.Group>
                        </div>
                    </>
                }
        </>
    )
}
export default ButtonOnClick;