import React from "react";
import { Select, Form, Radio } from "antd";
import Language from '../Language';

const { Option } = Select
const HeadingTags = (props) => {

    const lang = React.useContext(Language);
    return (
        <div class="mf-part-item">
            <div class="mf-part-title">{lang['Heading Size']}</div>
            <div>
                <Radio.Group buttonStyle="solid" onChange={(e) => props.handleChange("element", e.target.value)} value={props.element}>
                    <Radio.Button value="h1">H1</Radio.Button>
                    <Radio.Button value="h2">H2</Radio.Button>
                    <Radio.Button value="h3">H3</Radio.Button>
                    <Radio.Button value="h4">H4</Radio.Button>
                    <Radio.Button value="h5">H5</Radio.Button>
                    <Radio.Button value="h6">H6</Radio.Button>
                </Radio.Group>
            </div>
        </div>
    )
}

export default HeadingTags;