import React from "react";
import ImageUpload from '../../../components/ImageUpload';
import NewProductOption from "./NewProductOption";
import { Icon, Input, InputNumber, Button, Checkbox,Card } from "antd";
import axios from 'axios';
import Language from '../Language';

class NewProduct extends React.Component{

    state = {
        products:[],
        product:{},
        sampleProduct:{id:null, name:"", price:0, views:[], description:"", quantity:false, options:[]},
        newProductOption:0,
        editOptionIndex:-1,
        lang:{}
    }

    constructor(props) {
        super(props);

        if(props.products[props.productIndex] == undefined){
            this.state.product = this.state.sampleProduct
        }else{
           this.state.product = props.products[props.productIndex]
        }
      }

    componentDidMount = () => {
        this.setState({lang:this.context})
    }
 
    handleChange = (name, value) => {
        const newProduct = this.state.product
        
        if(name == "views"){
            let images = []
            value.forEach(element => {
                images.push(element.url)
            });
            newProduct[name] = images
            this.setState({product:newProduct})
        }else if (name == "options") {
            newProduct.options.push(value)
            this.setState({product:newProduct})
        }else{
            newProduct[name] = value
            this.setState({product:newProduct})
        }
    }

    addProduct = async () => {
        const newProducts =  this.props.products
        const id = await this.saveProduct();
        this.state.product.id = id
        newProducts.push(this.state.product)
       
        this.props.handleChange("products",newProducts)
        this.props.setNewProductShow(0)
    }

    saveProduct = async () => {
        const productData = encodeURIComponent(JSON.stringify(this.state.product));
        const result = await axios.post(this.props.baseUrl+"admin-ajax.php", "action=magicform_insert_product&name="+this.state.product.name+"&product="+productData)
        return result.data.data.id
    }

    updateProduct = async () => {
        const productData = encodeURIComponent(JSON.stringify(this.state.product));
        await axios.post(this.props.baseUrl+"admin-ajax.php", "action=magicform_update_product&name="+this.state.product.name+"&product="+productData+"&id="+this.state.product.id)
    }

    editProduct = () => {
        let newProducts = this.props.products
        newProducts[this.state.productIndex] =  this.state.product

        this.updateProduct();
        this.props.handleChange("products",newProducts)
        this.props.setNewProductShow(0)
    }

    deleteOption = (index) => {
        const newProduct = this.state.product
        newProduct.options = newProduct.options.filter((item,i) => i!==index)
        this.setState({product:newProduct})
    }
    
    saveProductOption = (value) => {
        this.setState({newProductOption:value})
    }

    handleEditOption = (index, option) => {
        const newProduct = this.state.product
        newProduct.options[index] = option
        this.setState({product:newProduct})
        this.setState({editOptionIndex:-1})
    }

    editProductOption = (index) => {
        this.setState({editOptionIndex:index})
        this.setState({newProductOption:1})
    }

    render() {
        return (
            !this.state.newProductOption?(
            <>
                <h5><a onClick={() => this.props.setNewProductShow(0)} style={{ display: "inline-block", marginRight: 10 }}><Icon type="left-circle"></Icon></a>{this.state.lang['New Product']}</h5>
                <div className="mf-part-item">
                    <div className="mf-part-title">{this.state.lang['Product Name']}</div>
                    <div><Input defaultValue={this.state.product.name} placeholder={this.state.lang['Product Name']} onChange={(e)=> this.handleChange("name", e.target.value)}/></div>
                </div>
                <div className="mf-part-item">
                    <div className="mf-part-title">{this.state.lang['Price']+":    "} </div>
                    <div><InputNumber defaultValue={this.state.product.price} onChange={(value)=> this.handleChange("price", value)}/></div>
                </div>
                <div className="mf-part-item">
                    <div className="mf-part-title">{this.state.lang['Description']}</div>
                    <div><Input defaultValue={this.state.product.description} placeholder={this.state.lang['Description']} onChange={(e)=> this.handleChange("description", e.target.value)}/></div>
                </div>
                <div className="mf-part-item">
                <div className="mf-part-title">{this.state.lang['Views']+"  "} </div> 
                <div><ImageUpload isMultiple={true}  buttonName={"Upload Image"} onSelect={val => this.handleChange("views", val)} /></div>
                    
                </div>
                <div className="mf-part-item">
                    <div className="mf-part-title">{this.state.lang['Quantity']}</div>
                    <div>
                        <Checkbox checked={this.state.product.quantity} placeholder={this.state.lang['Quantity']} onChange={(e)=> this.handleChange("quantity", e.target.checked)}/>
                    </div>
                </div>
                <div className="mf-part-item">
                    <div className="mf-part-title">{this.state.lang['Add Product Option']}</div>
                    <div> <Button onClick={()=> this.setState({newProductOption:1})}> {this.state.lang['Add Option']} </Button></div>
                </div>
                {
                    this.props.editable?
                    <div className="mf-part-item">
                        <Button type="primary" icon="edit" onClick={() => this.editProduct()}>{this.state.lang['Edit Product']}</Button>
                    </div>
                    :<div className="mf-part-item">
                        <Button type="primary" icon="plus-circle" onClick={() => this.addProduct()}>{this.state.lang['Add Product']}</Button>
                    </div>
                }
                <div>{
                        this.state.product.options.map( (item,index) => 
                            <Card
                                style={{ width: 300, height:150, marginTop:5}}
                            actions={[
                            <Icon type="edit" key="edit" onClick={()=> this.editProductOption(index) }/>,
                            <Icon type="close-circle" onClick={() => this.deleteOption(index)}/>
                            ]}
                            >
                            <div style={{ display: "flex", flexDirection: "column"}}>
                                    <label>{item.label}</label>
                                    <label>{item.items.length}  {this.state.lang['options']}</label>
                            </div>
                            </Card>
                        )
                    }
                </div>
            </>
            ):(
                <NewProductOption options={this.state.product.options} handleEditOption={this.handleEditOption} editOptionIndex={this.state.editOptionIndex}  saveProductOption={this.saveProductOption} handleChange={this.handleChange} />
            )
        )
    }

}
NewProduct.contextType = Language;
export default NewProduct;