import React from "react";
import { InputNumber, Form } from "antd";
import Language from '../Language';


const RatingConfigure = (props) => {
    const lang = React.useContext(Language);

    const updateStar = (name, value) => {
        let stars = []
        for (let i = 0; i < value; i++) {
            stars.push( {
                value: i+1,
                id: "star",
                name: "star "
            })
        }
        props.handleChange(name, stars);
    }

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Star Count']}</span>
            <InputNumber
            style={{width:"100%"}}
                value={props.value.length}
                onChange={(value) => updateStar('ratings', value)} />
        </div>
    )
}

export default RatingConfigure;