
import React from "react";
import { Radio, Form, Input } from "antd";
import Language from '../App/Language';
const Privacy = (props) => {
    const lang = React.useContext(Language);
    const { gdpr, gdprText, csrfToken } = props.settings;

    return (
        <>
            <h1>{lang['Privacy']}</h1>
            <Form.Item colon={false} label={lang['Enable GDPR compliance checkbox']}>
                <Radio.Group value={gdpr} onChange={e => props.handleChange("gdpr", e.target.value)}>
                    <Radio value={true}>{lang['Yes']}</Radio>
                    <Radio value={false}>{lang['No']}</Radio>
                </Radio.Group>
            </Form.Item>
            {gdpr && 
            <Form.Item colon={false} label={lang['GDPR compliance message']} extra={lang['This text will be used for GDPR compliance checkbox']}>
                <Input value={gdprText} onChange={e => props.handleChange("gdprText", e.target.value)}></Input>
                <div style={{ marginTop: 10 }}></div>
            <label> {lang['Privacy policy is provided by WordPress']}.{lang['You can change it']}.</label><a href="options-privacy.php" >{lang['Privacy Policy']}</a> 
            </Form.Item>
            }
            <hr/>
            <Form.Item colon={false} label={lang['Enable CSRF Token']} extra={lang['Cross-site request forgery (also known as CSRF) is a web security vulnerability that allows an attacker to induce users to perform actions that they do not intend to perform']}>
                <Radio.Group value={csrfToken} onChange={e => props.handleChange("csrfToken", e.target.value)}>
                    <Radio value={true}>{lang['Yes']}</Radio>
                    <Radio value={false}>{lang['No']}</Radio>
                </Radio.Group>
            </Form.Item>
        </>
    )
}

export default Privacy;