import React from "react";
import imgSrc from "../../../../default-image.jpg"

import InputDescription from "../../../../components/InputDescription";

const Image = (props) => {
    const {item} = props;
    const { imageSrc, heightValue, widthValue, cssClasses, elementPosition} = item.payload;

    return (
        <div className="form-group">
             <InputDescription position="top" {...props.item.payload} />
             <div className={"mf-image-"+elementPosition}>
                        <img className={`mf-image ${cssClasses.join(" ")}`} id={item.id} src={imageSrc==""?imgSrc:imageSrc} style={{height:heightValue, width:widthValue}} />
             </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </div>
    );
}

export default Image;