import React, {useState} from "react";
import {Icon, Alert, Popover, Button, Empty, Switch, Modal, message} from "antd";
import Types from "../../templates/actions";
import Integrations from "./Integrations";
import ActionModal from "./ActionModal";
import MailchimpIcon from "./icons/MailchimpIcon";
import SendGridIcon from "./icons/SendGridIcon";
import GetResponseIcon from "./icons/GetResponseIcon";
import StripeIcon from "./icons/StripeIcon"
import PaypalIcon from "./icons/PaypalIcon"
import GoogleAnalyticsIcon from "./icons/GoogleAnalyticsIcon";
import {Link, Switch as RouterSwitch, Route, withRouter} from "react-router-dom";
import Language from '../App/Language';

const {confirm} = Modal;

const Actions = (props) => {
    const lang = React.useContext(Language);
    const {actions} = props;
    const [popoverVisible, setPopoverVisible] = useState(false);
    const [modalVisible, setModalVisible] = useState(false);
    const [modalTitle, setModalTitle] = useState("Action");
    const [selectedAction, setSelectedAction] = useState(null);
    const [actionIndex, setActionIndex] = useState(null);

    let selectedNav = "actions";
    const segment = window.location.hash ? window.location.hash.split("/") : [];
    if (segment[2] != "") {
        selectedNav = segment[2];
    }

    const addAction = (type) => {
        const action = JSON.parse(JSON.stringify(Types[type]));
        setSelectedAction(action);
        setModalTitle(action.label);
        setActionIndex(null);
        /*actions.push(JSON.parse(JSON.stringify(Types[type])));
        props.updateActions(actions);*/
        setPopoverVisible(false);
        setModalVisible(true);
    }

    const saveAction = (action) => {
        actions.push(action);
        props.updateActions(actions);
        props.history.push("/actions");
    }


    const updateAction = (index, action) => {
        const newActions = actions.map((item, i) => i == index ? action : item);
        props.updateActions(newActions);
    }

    const showAction = (e, index) => {
        if (e.target.type == "button")
            return false;

        const action = actions.find((item, i) => i == index);
        setActionIndex(index);
        setModalVisible(true);
        setSelectedAction(action);
        setModalTitle(action.label);
    }

    const updateActionStatus = (index, status) => {
        const action = actions.find((item, i) => i == index);
        action.active = status;
        props.updateActions(actions);
    }

    const deleteAction = (actionIndex) => {
        confirm({
            title: lang['Do you want to delete this action'] + "?",
            content: lang['When clicked the OK button, system will delete this action'],
            onOk: () => {
                const newActions = actions.filter((item, i) => i != actionIndex);
                const findAction = actions.find((item,i)=> i== actionIndex)
                setAttachPdf(findAction);
                props.updateActions(newActions);
                setModalVisible(false);
                message.success(lang['Selected action deleted'] + "!");
            },
            onCancel: () => {
            },
        });
    }
    const checkActionExist = (type) => {
        return actions.find(item => item.type == type);
    }

    const setAttachPdf = (action) => {
        if(action.type == "generatePdf"){
            actions.map((item) => {
                if(item.payload.hasOwnProperty('attachPdf')){
                    item.payload.attachPdf = false;
                }
            })
        }
    }

    const renderActionIcon = action => {
        switch (action.type) {
            case "mailChimp":
                return <span style={{
                    width: 36,
                    height: 36,
                    marginRight: 10,
                    display: "inline-block",
                    verticalAlign: "middle"
                }}><MailchimpIcon/></span>
            case "sendgrid":
                return <span style={{
                    width: 36,
                    height: 36,
                    marginRight: 10,
                    display: "inline-block",
                    verticalAlign: "middle"
                }}><SendGridIcon/></span>
            case "getresponse":
                return <span style={{
                    width: 36,
                    height: 36,
                    marginRight: 10,
                    display: "inline-block",
                    verticalAlign: "middle"
                }}><GetResponseIcon/></span>
                case "googleAnalytics":
                return <span style={{
                    width: 36,
                    height: 36,
                    marginRight: 10,
                    display: "inline-block",
                    verticalAlign: "middle"
                }}><GoogleAnalyticsIcon/></span>
                case "stripe":
                    return <span style={{
                        width: 50,
                        height: 40,
                        marginRight: 10,
                        display: "inline-block",
                        verticalAlign: "middle"
                    }}><StripeIcon/></span>
                    case "paypal":
                    return <span style={{
                        width: 50,
                        height: 40,
                        marginRight: 10,
                        display: "inline-block",
                        verticalAlign: "middle"
                    }}><PaypalIcon/></span>
            default:
                return <Icon type={action.icon} className="mf-rounded-icon" style={{marginRight: 10}}></Icon>;
        }
    }

    const content = (
        <ul className="mf-actionList">
            {!checkActionExist("saveDatabase") &&
            <li><a onClick={() => addAction("saveDatabase")}><Icon type="database" className="mf-rounded-icon" style={{
                marginRight: 10,
                marginBottom: 20,
                float: "left"
            }}></Icon><h4>{lang['Save to Database']}</h4>
                <span>{lang['All submissions will be saved to database']}.</span></a></li>}
            <li><a onClick={() => addAction("sendEmail")}>
                <Icon type="mail" className="mf-rounded-icon"
                      style={{marginRight: 10, marginBottom: 20, float: "left"}}/>
                <h4>{lang['Send Email']}</h4>
                <span>{lang['All submissions will be emailed to email address']}.</span>
            </a>
            </li>
            <li>
                <a onClick={() => addAction("autoResponder")}>
                    <Icon type="rollback" className="mf-rounded-icon"
                          style={{marginRight: 10, marginBottom: 20, float: "left"}}/>
                    <h4>{lang['Auto Responder']}</h4>
                    <span>{lang['An auto responder email will sent to user']}.</span>
                </a>
            </li>
            {
            !checkActionExist("generatePdf") &&
            <li>
                <a onClick={() => addAction("generatePdf")}>
                    <Icon type="file-pdf" className="mf-rounded-icon"
                        style={{marginRight: 10, marginBottom: 20, float: "left"}}/>
                    <h4>{lang['Generate Pdf']}</h4>
                    <span>{lang['Generates pdf file with submission data']}.</span>
                </a>
            </li>
            }
             <li>
                <a onClick={() => addAction("formToPost")}>
                    <Icon type="reconciliation" className="mf-rounded-icon"
                        style={{marginRight: 10, marginBottom: 20, float: "left"}}/>
                    <h4>{lang['Form To Post']}</h4>
                    <span>{lang['Form submission auto create WP post']}.</span>
                </a>
            </li>
            <li><a onClick={() => addAction("mailChimp")}>
                <span style={{width: 34, height: 34, marginRight: 10, marginBottom: 20, float: "left"}}><MailchimpIcon/></span>
                <h4>MailChimp</h4>
                <span>{lang['You can add contacts to MailChimp lists']}</span>
            </a>
            </li>
            <li>
                <a onClick={() => addAction("sendgrid")}>
                <Icon type="rollback" className="mf-rounded-icon"
                          style={{marginRight: 10, marginBottom: 20, float: "left"}}/>
                    <h4>SendGrid</h4>
                    <span>{lang['You can add contacts to SendGrid lists']}</span>
                </a>
            </li>
            <li>
                <a onClick={() => addAction("getresponse")}>
                    <span style={{
                        width: 34,
                        height: 34,
                        marginRight: 10,
                        marginBottom: 20,
                        float: "left"
                    }}><GetResponseIcon/></span>
                    <h4>GetResponse</h4>
                    <span>{lang['You can add contacts to GetResponse lists']}</span>
                </a>
            </li>
            <li>
                <a onClick={() => addAction("stripe")}>
                <span style={{
                        width: 50,
                        height: 40,
                        marginRight: 10,
                        marginBottom: 20,
                        float: "left"
                    }}><StripeIcon/></span>
                    <h4>Stripe</h4>
                    <span>{lang['You can add payment method']} Stripe</span>
                </a>
            </li>
            <li>
                <a onClick={() => addAction("paypal")}>
                <span style={{
                        width: 50,
                        height: 40,
                        marginRight: 10,
                        marginBottom: 20,
                        float: "left"
                    }}><PaypalIcon/></span>
                    <h4>Paypal</h4>
                    <span>{lang['You can add payment method']} Paypal</span>
                </a>
            </li>
            <li><Link to="/actions/integrations">{lang['See All Integrations']}</Link></li>
        </ul>
    );

    return (

        <div className="mf-settings">
            <div className="mf-settings-left">
                <div className="mf-settings-left-inner">
                    <ul className="mf-settings-nav">
                        <li className={(selectedNav == undefined || selectedNav == "actions") ? "active" : ""}><Link
                            to="/actions"><Icon type="appstore"/>{lang['All Actions']}</Link></li>
                        <li className={(selectedNav == "integrations") ? "active" : ""}><Link
                            to="/actions/integrations"><Icon type="api"/> {lang['Integrations']}</Link></li>
                    </ul>

                    <div style={{padding: 15}}>
                        <h6>{lang['About Actions']}</h6>
                        <p>{lang['Actions triggered after form submit. You can add multiple actions']}.</p>
                        <p>{lang['Want to change action settings']}? {lang['Just click on action ']}</p>
                        <p>{lang['Click the switch on the right to activate action']}.</p>
                    </div>
                </div>
            </div>
            <div className="mf-settings-right">

                <RouterSwitch>
                    <Route path="/actions/integrations"
                           render={(props) =>
                               <Integrations checkActionExist={checkActionExist} addAction={addAction}/>
                           }/>
                    <Route path="/actions"
                           render={() =>
                               <>
                                   <div style={{display: "flex"}}>
                                       <div style={{flex: 1}}>
                                           <h1>{lang['Actions']}</h1>
                                       </div>
                                       <div style={{flex: "0 0 auto", justifyContent: "flex-end"}}>
                                           <Popover
                                               visible={popoverVisible}
                                               content={content}
                                               placement="bottomRight"
                                               trigger="hover"
                                               onVisibleChange={status => setPopoverVisible(status)}
                                           >
                                               <a className="mf-admin-btn mf-admin-btn-blue"><Icon
                                                   type="plus-circle"/>{lang['Add Action']}</a>
                                           </Popover>
                                       </div>
                                   </div>
                                   {props.actions && props.actions.length > 0 && props.actions.map((action, actionIndex) => (
                                       <a className="mf-card" onClick={(e) => showAction(e, actionIndex)}>
                                           <div className="mf-card-body"
                                                style={{display: "flex", background: action.active ? "" : "#EEE"}}>
                                               <h6 style={{marginBottom: 0, flex: 1}}>
                                                   {renderActionIcon(action)} {action.label}
                                               </h6>
                                               <div style={{flex: "0 0 auto", display: "flex", alignItems: "center"}}>
                                                   <Switch defaultChecked={action.active}
                                                           onChange={val => updateActionStatus(actionIndex, val)}>{lang['test']}</Switch>
                                               </div>
                                           </div>
                                       </a>
                                   ))}
                                   {(!props.actions || (props.actions && props.actions.length == 0)) &&
                                   <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={lang['No action']}/>}
                               </>
                           }/>
                </RouterSwitch>
            </div>
            <ActionModal
                allElements={props.allElements}
                modalTitle={modalTitle}
                modalVisible={modalVisible}
                setModalVisible={setModalVisible}
                setSelectedAction={setSelectedAction}
                actionIndex={actionIndex}
                saveAction={saveAction}
                updateAction={updateAction}
                deleteAction={deleteAction}
                action={selectedAction}
                actions={actions}
                formSettings={props.formSettings}
                emailSettings={props.emailSettings}
                payments={props.payments}
                baseUrl={props.baseUrl}
            />

        </div>


    )
}

export default withRouter(Actions);