import React from "react";
import {Select, Checkbox} from "antd";
import Language from '../Language';

const {Option} = Select;

const CheckboxLabelPos = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
    <div class="mf-part-item" style={{marginTop:10}}>
        <span className="mf-part-title">{lang['Label Position']}</span>
        <div>
                <Select value={props.value} onChange={(value) => props.handleChange("cbLabelPos", value)} style={{width:120}}>
                    <Option value="mf-thumbnail-up">{lang['Up']}</Option>
                    <Option value="mf-thumbnail-down">{lang['Down']}</Option>
                </Select>
        </div>
     </div>
        <div class="mf-part-item" style={{marginTop:10}}>
        <span className="mf-part-title">{lang["Show/Hide Label"]}</span>
        <div>
               <Checkbox checked={props.cbLabelDisplay} onChange={(e)=> props.handleChange("cbLabelDisplay",e.target.checked)} />
        </div>
     </div>
     </>
    )
}

export default CheckboxLabelPos;