import React from "react";
import MultiStepHeader from "./MultiStepHeader";
import SettingsPart from "../../../components/SettingsPart";
import ProgressBar from "./ProgressBar";
import StepSettings from "./StepSettings";
import Language from '../Language';

const MultiStep = (props) => {

    const lang = React.useContext(Language);

    const handleChange = (name, value) => {
        props.updateFormSettings(name, value);
    }

    const { multiStep,progressBar } = props.settings;

    return (
        <div style={{ padding: 20 }}>
            <h5>{lang['Multi Step Settings']}</h5>
            <SettingsPart title={lang['Multi Step Settings'].toUpperCase()}>
                <MultiStepHeader values={multiStep} pages={props.pages} handleChange={handleChange} />
            </SettingsPart>
            {
                multiStep.visible &&
                <SettingsPart title={lang['STEP SETTINGS']}>
                {
                    props.pages.map((item) => 
                        item.type != "thankyou" &&
                        <StepSettings step={item.step} index={item.index} updatePageStep={props.updatePageStep}/>
                    )
                }
                </SettingsPart>
            }
             {props.pages.length > 2 && <ProgressBar values={progressBar} handleChange={handleChange}/> }
        </div>
    )
}

export default MultiStep;