import React from "react";
import {Select, Form} from "antd";
import Language from '../Language';

const {Option} = Select;

const CheckboxPos = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
            <Form.Item label={lang['CheckBox Position']}>
                <Select 
                    value={props.value}  
                    onChange={(value) => props.handleChange("checkBoxPos", value)}  
                    style={{width:120}}>
                    <Option value="left">{lang['Left']}</Option>
                    <Option value="center">{lang['Center']}</Option>
                    <Option value="right">{lang['Right']}</Option>
                </Select>
            </Form.Item>
        </>
    )
}

export default CheckboxPos;