import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";

const ScaleRating = (props) => {
    const { item , originalItem } = props;
    const { ratings, defaultRating, bestLabel, worstLabel } = item.payload;

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
    }

    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <div className="mf-scale-container">
                <label className="mf-scale-label"> {worstLabel}</label>
                <div class="mf-scale-rating-container">
                    {
                        ratings.map((rating, index) =>
                            <div className="mf-scale-element">
                                <div>{index}</div>
                                <div className="mf-custom-control mf-custom-radio">
                                    <input
                                        onChange={(e) => handleChange(e.target.value,"defaultRating")}
                                        value={index}
                                        checked={defaultRating==index}
                                        type="radio"
                                        name={item.id}
                                        className={`mf-custom-control-input`}
                                        id={`${item.id}_${index}`}
                                    />
                                    <label className="mf-custom-control-label" htmlFor={`${item.id}_${index}`}></label>
                                </div>
                            </div>
                        )
                    }
                </div>
                <label className="mf-scale-label"> {bestLabel}</label>
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout>
    );
}

export default ScaleRating;