import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import Config from "../../../../config";
import Language from "../../Language";

const FileUpload = (props) => {
    const lang = React.useContext(Language);
    const { item , originalItem } = props;
    const { fileSize, fileSizeType, labelText, allowedFileTypes, buttonName, cssClasses, placeholder, fieldSize, fieldWidth } = item.payload;

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />

            <div className="mf-form-group">
                <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} mf-active `}  >
                    <div className={`mf-custom-file ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}>
                        <input type="file" className={`mf-custom-file-input ${cssClasses.join(" ")}`} id={item.id} />
                        <label className="mf-custom-file-label" htmlFor={item.id}>{placeholder}</label>
                    </div>
                    {item.theme.startsWith("material") && labelText != "" && <span className="mf-material-label">{labelText}</span>}
                </div>
                <small className="mf-form-text mf-text-muted"> {lang["Max File Size"]}: {fileSize}{fileSizeType}
                <b style={{ display: "inline-block", margin: "0 6px" }}>&middot;</b> {lang["Allowed File Types"]}: {allowedFileTypes.join(', ')} </small>
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
            <style dangerouslySetInnerHTML={{
                __html: `
  #${item.id} + label:after {content:"${buttonName}"}
`}}></style>
        </InputLayout >
    );
}

export default FileUpload;