import React from "react";
import { Form, Button, Icon, Input,Checkbox,Radio } from "antd";
import Config from "../../../config";
import SelectAddInput from "../../../components/SelectAddInput"
import Language from '../../App/Language';
import ImageUpload from '../../../components/ImageUpload';
class GeneratePdf extends React.Component {

    state = { logo: null, action: null, lang: {} ,payload:{}}

    componentDidMount() {
        this.setState({ lang: this.context })
        this.setValues(this.props.action);
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }


    addValue = ( type, value)=> {
      const newData = this.state.payload
      newData[type] = value
      this.setState({payload:newData})
    }

    setValues = (action) => {

        this.setState({
            payload:action.payload,
            logo: action.payload.logo,
            action:action
        });
    }

    handleChange = (title, value) => {
        const newData = this.state.payload
        newData[title] = value
        this.setState({payload:newData})
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const action = this.state.action;
                action.payload.logo = this.state.logo;
                for (let i in values) {
                    action.payload[i] = values[i];
                }
                
                action.payload = this.state.payload
                

                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    render() {
        return (
            <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>

                <Form.Item label={this.state.lang["Logo"]} >
                    {this.state.logo && <img style={{ maxWidth: 100, margin: "0 20px", maxHeight: 50 }} src={this.state.logo} />}
                    <ImageUpload isMultiple={false}  buttonName={"Upload Image"} onSelect={val => { this.setState({ logo: val }) }} />
                </Form.Item>
                <Form.Item label={this.state.lang["Title"]} extra={this.state.lang["This section is title the pdf"]}>
                    <SelectAddInput selectType="allElements" style={{width:"35px"}} type="title" value={this.state.payload.title} allElements={this.props.allElements} addValue={this.addValue} >
                        <Input.TextArea value={this.state.payload.title} onChange={(e) => this.addValue("title", e.target.value)}  />
                    </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang["Header"]} extra={this.state.lang["This section is located above the pdf"]}>
                    <SelectAddInput selectType="allElements" style={{width:"35px"}} type="header" value={this.state.payload.header} allElements={this.props.allElements} addValue={this.addValue} >
                        <Input.TextArea value={this.state.payload.header} onChange={(e) => this.addValue("header", e.target.value)}  />
                    </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang["Footer"]} extra={this.state.lang["This section is located below the pdf"]}>
                    <SelectAddInput selectType="allElements" style={{width:"35px"}} type="footer" value={this.state.payload.footer} allElements={this.props.allElements} addValue={this.addValue} >
                        <Input.TextArea value={this.state.payload.footer} onChange={(e) => this.addValue("footer", e.target.value)}  />
                    </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang['Form Information']}>
                    <Checkbox checked={this.state.payload.timeVisible} onChange={(e) =>this.handleChange("timeVisible", e.target.checked)}>{this.state.lang['Show Time']}</Checkbox>
                    <Checkbox onChange={(e) =>this.handleChange("idVisible", e.target.checked)} checked={this.state.payload.idVisible}>{this.state.lang['Show Submission Id']} </Checkbox>
                    <Checkbox onChange={(e) =>this.handleChange("formNameVisible", e.target.checked)} checked={this.state.payload.formNameVisible}>{this.state.lang['Show Form Name']} </Checkbox>
                    <Checkbox style={{marginLeft:0}} onChange={(e) =>this.handleChange("emptyField", e.target.checked)} checked={this.state.payload.emptyField}>{this.state.lang['Empty Field Remove Pdf']} </Checkbox>
                </Form.Item>
                <Form.Item {...Config.tailFormItemLayout}>
                    <Button type="primary" htmlType="submit"><Icon type="save"></Icon> {this.state.lang['Save']}</Button>
                    <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang['Delete Action']}</Button>
                </Form.Item>
               
            </Form >
        )
    }
}

GeneratePdf.contextType = Language;
const Wrapped = Form.create({ name: 'generatePdf' })(GeneratePdf);
export default Wrapped;
