import React from "react";
import {Checkbox } from "antd";
import Language from '../Language';

const SizeControl = (props) => {
    const lang = React.useContext(Language);
    return (
         <div class="mf-part-item" style={{marginTop:10}}>
            <span className="mf-part-title"></span>
            <div>
                <Checkbox checked={props.value} onChange={(e) => props.handleChange("autoSize",e.target.checked)} > {lang["Auto Size"]} </Checkbox>
            </div>
        </div>
    )
}

export default SizeControl;