import React,{ useEffect }from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import InputIcon from "../../../../components/InputIcon.js";
import Config from "../../../../config";

const Time = (props) => {
    const { item , originalItem } = props;
    const { disabled, clocks, inputIcon, cssClasses, fieldSize, fieldWidth, labelText, defaultValueCheck, defaultValue, clockFormat,timeIncrement ,minValue, maxValue} = item.payload;
      useEffect(() => {
        clocks.length = 0
        clockOptions()
    }, [clockFormat,timeIncrement,minValue,maxValue] )

    const clockOptions = () => {
        let clock = 0;
        let length = 0;
        let minute = 0;
        let tag = " a.m"
        if (clockFormat == 12) {
            length = maxValue - minValue;
            length = length * (60 /timeIncrement);

            clock = minValue;
            if (minValue > 11){
                tag = " p.m"
                clock = minValue - 11;
            }
                
            for (let i = 0; i < length; i++) {
                clocks.push(
                   clock +":"+(minute==0?"00":minute)+tag
                )
                minute+=timeIncrement;
                if(minute>=60){
                    minute = 0;
                    clock++
                    if (clock == 13) {
                        clock = 1;
                    }
                    if (clock == 12)
                        tag = " p.m"
                }

            }
        } else {
            length = maxValue - minValue;
            length = length * (60 / timeIncrement);
            clock = minValue;
            for (let i=0; i<length; i++){
               minute+=timeIncrement;
               if(minute>=60){
                   minute = 0;
                   clock++
               }
               clocks.push(
                  clock +":"+(minute==0?"00":minute)
               )
            }
        }
        
        const newItem = item 
        newItem.payload['labelDisplay'] = "inherit"
        newItem.payload['labelPosition'] = "inherit"
        props.updateElement(item.id, item)
    }

    const handleChange = (value,name) => {
     const newItem = originalItem
      newItem.payload[name] = value
      props.updateElement(item.id, newItem)
    }

  return (
    <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
      <InputDescription position="top" {...props.item.payload} />
      <div
        className={`${
          item.theme.startsWith("material") ? `mf-material-container` : ``
        } mf-active  ${inputIcon ? `mf-with-icon` : ``} `}
      >
        <InputIcon icon={inputIcon}>
          <select
           className={`mf-form-control ${cssClasses.join(" ")} ${
            Config.size[fieldSize]
          } ${Config.width[fieldWidth]}`}
            id={item.id}
            disabled={disabled}
          >
            {clocks.map((item, index) => (
              <option selected={defaultValueCheck && item==defaultValue?true:false} key={index} value={item}>
                {item}
              </option>
            ))}
          </select>
        </InputIcon>
        {item.theme.startsWith("material") && labelText != "" && (
          <span className="mf-material-label">{labelText}</span>
        )}
      </div>
      <InputDescription position="bottom" {...props.item.payload} />
    </InputLayout>
  );
};

export default Time;
