export const getNewIndexHelper = (parent, type, indexes) => {
    if (type == "page") {
        parent.map(page => {
            page.elements.map(item => {
                indexes.push(item.index);
                if (item.type == "group" || item.type == "grid") {
                    getNewIndexHelper(item, item.type, indexes);
                }
            });
        });
    }
    if (type == "group") {
        parent.payload.elements.map(item => {
            indexes.push(item.index);
            if (item.type == "group" || item.type == "grid") {
                getNewIndexHelper(item, item.type, indexes);
            }
        })
    }
    if (type == "grid") {
        parent.payload.columns.map(column => {
            column.elements.map(item => {
                indexes.push(item.index);
                if (item.type == "group" || item.type == "grid") {
                    getNewIndexHelper(item, item.type, indexes);
                }
            });
        })
    }
    return indexes;
}

export const findAndDelete = (data, type, id) => {
    if (type == "page") {
        data.elements = data.elements.filter(i => i.id != id);
        data.elements.map(item => {
            if (item.type == "group" || item.type == "grid") {
                findAndDelete(item, item.type, id);
            }
        })
    }

    if (type == "group") {
        data.payload.elements = data.payload.elements.filter(i => i.id != id);
        data.payload.elements.map(item => {
            if (item.type == "group" || item.type == "grid") {
                findAndDelete(item, item.type, id);
            }
        })
    }
    if (type == "grid") {
        data.payload.columns.map(column => {
            column.elements = column.elements.filter(i => i.id != id);
            column.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    findAndDelete(item, item.type, id);
                }
            })
        });
    }
}

export const findElement = (type, data, id,element) => {
    if (type == "page") {
        data.map((page) => {
                page.elements.map(item => {
                if(item.id==id)
                    element.item = item   
                if (item.type == "group" || item.type == "grid") {
                    findElement(item.type, item, id,element);
                }
            })
        })
    }

    if (type == "group") {
        data.payload.elements.map(item => {
            if(item.id==id)
                element.item = item

            if (item.type == "group" || item.type == "grid") {
                findElement(item.type, item, id,element);
            }
        })
    }
    
    if (type == "grid") {
        data.payload.columns.map(column => {

            column.elements.map(item => {
                if(item.id==id)
                    element.item = item
                
                if (item.type == "group" || item.type == "grid") {
                    findElement(item.type, item, id,element);
                }
            })
        });
    }
}

export const controlSubmitButton = (data, type, checkSubmit) => {
    if (type == "page") {
        data.map(pageItem => {
            if (pageItem.elements.find(i => i.type == "submitButton") != undefined)
                checkSubmit.check = true;
            pageItem.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    controlSubmitButton(item, item.type, checkSubmit);
                }
            })
        })
    }
    if (type == "group") {
        data.payload.elements.map(item => {
            if (item.type == "submitButton")
                checkSubmit.check = true;
            if (item.type == "group" || item.type == "grid") {
                controlSubmitButton(item, item.type, checkSubmit);
            }
        })
    }
    if (type == "grid") {
        data.payload.columns.map(column => {
            column.elements.map(item => {
                if (item.type == "submitButton")
                    checkSubmit.check = true;
                if (item.type == "group" || item.type == "grid") {
                    controlSubmitButton(item, item.type, checkSubmit);
                }
            })
        });
    }
}

export const updateElementHelper = (data, type, id, element) => {
    if (type == "page") {
        data.elements = data.elements.map(item => item.id === id ? element : item);
        data.elements.map(item => {
            if (item.type === "group" || item.type === "grid") {
                updateElementHelper(item, item.type, id, element);
            }
        })
    }

    if (type === "group") {
        data.payload.elements = data.payload.elements.map(item => item.id === id ? element : item);
        data.payload.elements.map(item => {
            if (item.type === "group" || item.type === "grid") {
                updateElementHelper(item, item.type, id, element);
            }
        })
    }
    if (type === "grid") {
        data.payload.columns.map(column => {
            column.elements = column.elements.map(item => item.id === id ? element : item);
            column.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    updateElementHelper(item, item.type, id, element);
                }
            })
        });
    }
}

export const changeImportElementsId = (type, data, formId) => {
    if (type == "page") {
        data.map(pageItem => {
            pageItem.elements.map(item => {
                item.id = item.id.replace(/(?<=\_)(\d*)(?=\_)/g, formId);
                if (item.type === "group" || item.type === "grid") {
                    changeImportElementsId(item.type, item, formId);
                }
            })
        })

    }

    if (type === "group") {
        data.payload.elements.map(item => {
            item.id = item.id.replace(/(?<=\_)(\d*)(?=\_)/g, formId);
            if (item.type === "group" || item.type === "grid") {
                changeImportElementsId(item.type, item, formId);
            }
        })
    }
    if (type === "grid") {
        data.payload.columns.map(column => {
            column.elements.map(item => {
                item.id = item.id.replace(/(?<=\_)(\d*)(?=\_)/g, formId);
                if (item.type == "group" || item.type == "grid") {
                    changeImportElementsId(item.type, item, formId);
                }
            })
        });
    }
}

export const updateOrderHelper = (data, type, elements, id, index) => {
    if (type == "page") {
        if (id == null) {
            data.elements = elements;
        } else {
            let find = data.elements.find(item => item.id == id);
            if (find) {
                if (find.type == "group") {
                    find.payload.elements = elements;
                } else if (find.type == "grid") {
                    find.payload.columns[index].elements = elements;
                }
            } else {
                data.elements.map(item => {
                    if (item.type == "group" || item.type == "grid") {
                        updateOrderHelper(item, item.type, elements, id, index);
                    }
                })
            }
        }
    }

    if (type === "group") {
        let find = data.payload.elements.find(item => item.id == id);
        if (find) {
            if (find.type == "group") {
                find.payload.elements = elements;
            } else if (find.type == "grid") {
                find.payload.columns[index].elements = elements;
            }
        } else {
            data.payload.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    updateOrderHelper(item, item.type, elements, id, index);
                }
            })
        }
    }
    if (type === "grid") {
        data.payload.columns.map(column => {
            let find = column.elements.find(item => item.id == id);
            if (find) {
                if (find.type == "group") {
                    find.payload.elements = elements;
                } else if (find.type == "grid") {
                    find.payload.columns[index].elements = elements;
                }
            } else {
                column.elements.map(item => {
                    if (item.type == "group" || item.type == "grid") {
                        updateOrderHelper(item, item.type, elements, id, index);
                    }
                })
            }
        });
    }
}

export const duplicatePageHelper = (data, type, lastElement, formId) => {
    if(type=="page"){
        data.elements.map((item) => {
                lastElement.id++
                item.id =  item.type + "_" + formId + "_" + lastElement.id
                item.index = lastElement.id;
                if(item.type === "group" || item.type === "grid"){
                   duplicatePageHelper(item, item.type, lastElement, formId)
                }
            })
    }

    if(type === "group") {
        data.payload.elements.map((item) => {
            lastElement.id++
            item.index = lastElement.id;
            item.id = item.type + "_" + formId + "_" + item.index
            if(item.type === "group" || item.type === "grid"){
                duplicatePageHelper(item, item.type, lastElement, formId)
            }
        })
    }

    if(type === "grid"){
            data.payload.columns.map(column => {
                column.elements.map((item) => {
                        lastElement.id++
                        item.index = lastElement.id;
                        item.id = item.type + "_" + formId + "_" + item.index 
                    if(item.type == "grid" || item.type == "group"){
                        duplicatePageHelper(item, item.type, lastElement, formId)
                    }
                })
        })
    }
}

export const duplicateElementHelper = (data, type, id, formId, getNewIndex) => {

    if (type == "page") {
        duplicateElement(data.elements, id, formId, getNewIndex);
        data.elements.map(item => {
            if (item.type === "group" || item.type === "grid") {
                duplicateElementHelper(item, item.type, id, formId, getNewIndex);
            }
        })
    }

    if (type === "group") {
        duplicateElement(data.payload.elements, id, formId, getNewIndex);
        data.payload.elements.map(item => {
            if (item.type === "group" || item.type === "grid") {
                duplicateElementHelper(item, item.type, id, formId, getNewIndex);
            }
        })
    }
    if (type === "grid") {
        data.payload.columns.map(column => {
            duplicateElement(column.elements, id, formId, getNewIndex);
            column.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    duplicateElementHelper(item, item.type, id, formId, getNewIndex);
                }
            })
        });
    }


}

const duplicateElement = (parent, id, formId, getNewIndex) => {
    const el = parent.find(item => item.id == id);
    if (el) {
        let selectedElement = JSON.parse(JSON.stringify(el));
        selectedElement.index = getNewIndex();
        selectedElement.id = selectedElement.type + "_" + formId + "_" + selectedElement.index;


        if (selectedElement.type == "group" || selectedElement.type == "grid") {
            duplicateChildElements(selectedElement, selectedElement.type, formId, selectedElement.index);
        }

        const selectedElementIndex = parent.findIndex(item => item.id === id) + 1;
        parent.splice(selectedElementIndex, 0, selectedElement);
    }
}

const duplicateChildElements = (el, type, formId, parentIndex) => {
    let lastIndex = parentIndex;
    if (type == "group") {
        el.payload.elements = el.payload.elements.map(item => {
            lastIndex++;
            item.index = lastIndex;
            item.id = item.type + "_" + formId + "_" + item.index;
            return item;
        })

        el.payload.elements.map(item => {
            if (item.type == "group" || item.type == "grid") {
                duplicateChildElements(item, item.type, formId, lastIndex);
            }
        })

    }

    if (type == "grid") {
        el.payload.columns.map(column => {
            column.elements = column.elements.map(item => {
                lastIndex++;
                item.index = lastIndex;
                item.id = item.type + "_" + formId + "_" + item.index;
                return item;
            })

            column.elements.map(item => {
                if (item.type == "group" || item.type == "grid") {
                    duplicateChildElements(item, item.type, formId, lastIndex);
                }
            })
        });
    }

}


const getUrlVars = () => {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function (m, key, value) {
        vars[key] = value;
    });
    return vars;
}

export const getUrlParam = (parameter, defaultvalue) => {
    var urlparameter = defaultvalue;
    if (window.location.href.indexOf(parameter) > -1) {
        urlparameter = getUrlVars()[parameter];
    }
    return urlparameter;
}

export const getAllElementsHelper = (data, type, result, level = -1) => {
    level++;
    if (type == "page") {
        data.map((page, pageIndex) => {
            if (page.type == "thankyou") {
                return false;
            }
            const pageObj = { id: "page-" + pageIndex, optGroup: true, label: page.name, children: [] }
            result.push(pageObj);
            page.elements.map(item => {
                if (item.type === "group" || item.type === "grid") {
                    getAllElementsHelper(item, item.type, pageObj.children, level);
                } else {
                    if (item.type != "submitButton") {
                        pageObj.children.push({ id: item.id, label: item.payload.labelText != "" && item.payload.labelText ? item.payload.labelText : item.id });
                    }
                }
            });
        })
    }
    if (type == "group") {
        const groupObj = { id: data.id, optGroup: true, label: data.payload.labelText, children: [] }
        result.push(groupObj);
        data.payload.elements.map(item => {
            if (item.type === "group" || item.type === "grid") {
                getAllElementsHelper(item, item.type, groupObj.children, level);
            } else {
                if (item.type != "submitButton") {
                    groupObj.children.push({ id: item.id, label: item.payload.labelText != "" && item.payload.labelText ? item.payload.labelText : item.id });
                }
            }
        })
    }

    if (type == "grid") {
        const gridObj = { id: data.id, optGroup: true, label:data.payload.labelText, children: [] }
        result.push(gridObj);
        data.payload.columns.map(column => {
            column.elements.map(item => {
                if (item.type === "group" || item.type === "grid") {
                    getAllElementsHelper(item, item.type, gridObj.children, level);
                } else {
                    if (item.type != "submitButton") {
                        gridObj.children.push({ id: item.id, label: item.payload.labelText != "" && item.payload.labelText ? item.payload.labelText : item.id });
                    }
                }
            })
        });
    };


    return result;
}

export const convertRgba = (color, opacity = 1, returnType = "text") => {
    if (color == "transparent")
        return color;
    if (color === null){
        return "none";
    }
    if (returnType == "text") {
        return `rgba(${color.r},${color.g},${color.b},${opacity == 1 ? color.a : opacity})`
    }
    if (returnType == "object") {
        return { r: color.r, g: color.g, b: color.b, a: opacity == 1 ? color.a : opacity }
    }
}

export const getColorLuminance = (color, percent, opacity=1)=>{
    percent = percent+1;
    const r = Math.round(Math.min(255, Math.max(0, color.r * percent)));
    const g = Math.round(Math.min(255, Math.max(0, color.g * percent)));
    const b = Math.round(Math.min(255, Math.max(0, color.b * percent)));
    return { r,g,b, a: opacity == 1 ? color.a : opacity }
}


