import React, { useState } from "react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { Radio, Input, Tag,TreeSelect } from "antd";
import SelectVariable from "../../components/SelectVariable"
import Language from './Language';
import SelectAddInput from '../../components/SelectAddInput'
const { TreeNode } = TreeSelect;
const ThankYou = (props) => {
    const lang = React.useContext(Language);
    const { message, action, redirectUrl, successMessage } = props.currentPage.settings;

    const modules = {
        toolbar: [
            ['bold', 'italic', 'underline'],        // toggled buttons
            ['blockquote'],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            [{ 'direction': 'rtl' }],                         // text direction          
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

            [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
            [{ 'font': [] }],
            [{ 'align': [] }],
            ['link', 'image'],
            ['clean']
        ],
    };

    const addValue = (type, value) => {

        props.updatePageSettings(type, value);
    }

    const addVariable = (value,label) => {
        props.updatePageSettings("message", `${message}{${label[0] + "|" + value}}`);
    }

    const replaceAllElements = () => {
        props.allElements.forEach(element => {
               element.children = element.children.filter(item => !item.id.includes('signature'))
        });
    }
    replaceAllElements()
    return (
        <div>
            <h6>{lang['Action after submit form']}</h6>
            <Radio.Group onChange={e => props.updatePageSettings("action", e.target.value)} value={action}>
                <div style={{ marginBottom: 4, fontWeight: 500 }}>
                    <Radio value="showThankYou">{lang['Show Thank You Page']}</Radio>
                </div>
                <div style={{ marginBottom: 4, fontWeight: 500 }}>
                    <Radio value="redirectUrl">{lang['Redirect Page to a spesific url']}</Radio>
                </div>
                <div style={{ marginBottom: 4, fontWeight: 500 }}>
                    <Radio value="stayOnForm">{lang['Stay on form and show only success message']}</Radio>
                </div>
            </Radio.Group>

            <hr />

            {action == "showThankYou" &&
                <>
                    <ReactQuill value={message}
                        className="thankyou-page"
                        theme="snow"
                        modules={modules}
                        onChange={(value) => props.updatePageSettings("message", value)} />
                    <div style={{ margin: "10px 0" }}><small className="mf-form-text mf-text-muted">{lang['You can use variables below']}</small></div>
                    <SelectVariable selectType="allElements" type="thankyou" allElements={props.allElements} addVariable={addVariable} />
                </>
            }

            {action == "redirectUrl" &&
                <div className="mf-form-group">
                    <label className="mf-label">{lang['Redirect Url']}</label>
                    <SelectAddInput selectType="formData" style={{width:"35px"}} type="redirectUrl" value={redirectUrl} allElements={props.allElements} addValue={addValue} >
                            <Input placeholder={lang['Redirect Url like']+"https://domain.com/page"} value={redirectUrl} onChange={e => props.updatePageSettings("redirectUrl", e.target.value)} />
                     </SelectAddInput>
                </div>
            }
            {action == "stayOnForm" &&
                <div className="mf-form-group">
                    <label className="mf-label">{lang['Success Message']}</label>
                    <Input.TextArea placeholder={lang['Success Message']} value={successMessage} onChange={e => props.updatePageSettings("successMessage", e.target.value)} />
                </div>
            }



        </div>
    )
}

export default ThankYou;