import React from "react";
import { Form, Button, Icon, Alert, TreeSelect,Select } from "antd";
import Config from "../../../config";
import Language from '../../App/Language';
const { Option } = Select;

const { TreeNode } = TreeSelect;
class Stripe extends React.Component {

    state = { action: null,lang: {}, errModal:false, payload:{} }

    componentDidMount() {
        this.setState({lang:this.context})
        this.setValues(this.props.action);
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }

    setValues = (action) => {

        this.setState({
            payload:action.payload,
            action:action
        })
        
        this.props.form.setFieldsValue({
            currency: action.payload.currency,
            totalAmount:action.payload.totalAmount
        });
    }

    createTreeTag = (elements, onlyFields) => {
        return elements.map(el => {
            return  <TreeNode  disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                        {el.children && el.children.length > 0 && this.createTreeTag(el.children)}
                    </TreeNode>
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
               
                const action = this.state.action;
               
                for (let i in values) {
                    action.payload[i] = values[i];
                }
            
                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    render() {
        const { getFieldDecorator} = this.props.form;
        const stripeSettings = this.props.payments.stripeSettings

        return (
            <>
            {
                  stripeSettings != null?
                  <Form onSubmit={this.handleSubmit} {...Config.formItemLayout} style={{margin:-24}}>
                      <div style={{ overflow: "auto", padding:20 }}>
                        <Form.Item label={this.state.lang["Total Amount"]}>
                            {getFieldDecorator('totalAmount',{ rules: [{ required: true, message: this.state.lang['Please input total amount']+'!' }]})(
                                <TreeSelect
                                showSearch
                                style={{ width: '200px' }}
                                placeholder={this.state.lang["Please select"]}
                                allowClear
                                treeDefaultExpandAll
                                dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            >
                                <TreeNode disabled={true} value="form_elements" title={this.state.lang["Form Elements"]}>
                                    {this.createTreeTag(this.props.allElements,true)}
                                </TreeNode>
                            </TreeSelect>
                            )}
                        </Form.Item> 
                        <Form.Item label={this.state.lang["Currency"]}>
                            {getFieldDecorator('currency',{ rules: [{ required: true, message: this.state.lang['Please select currency']+'!' }]})(
                                      <Select
                                      showSearch
                                      style={{ width: 200 }}
                                      placeholder="Select a currency"
                                  >
                                      {Config.currency.map((item) =>
                                            <Option value={item.value}>{item.name}</Option>
                                      )}     
                                  </Select>
                            )}
                        </Form.Item>
                      </div>
                      <div style={{padding:15, textAlign:"right", background:"#f5f7f9", display:"flex", justifyContent:"space-between", borderRadius:"0 0 4px 4px"}}>
                          <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang["Delete Action"]}</Button>
                          <Button type="primary" htmlType="submit"><Icon type="save"></Icon>{this.state.lang['Save']}</Button>
                      </div>
                  </Form>
                  : <>
                      <Alert type="warning" showIcon message={this.state.lang["Stripe Settings"]} description={this.state.lang["Please setup stripe settings in Settings"]} />
                      <div style={{ textAlign: "center", padding: "20px 0" }}><span dangerouslySetInnerHTML={{__html:this.state.lang['Setup Stripe Settings']}} /></div>
                  </>
            }
            </>
        ) 
    }
}
Stripe.contextType = Language;
const Wrapped = Form.create({ name: 'stripe' })(Stripe);
export default Wrapped;