import React from "react";
import { Tabs } from "antd";
import Id from "../../settings/Id";
import SettingsPart from "../../../../components/SettingsPart";
import Language from "../../Language";
import HeadingTags from "../../settings/HeadingTags";
import HeadingContent from "../../settings/HeadingContent"
import ColorPicker from "../../../../components/ColorPicker"
import CssClasses from "../../settings/CssClasses";
import ElementPosition from "../../settings/ElementPosition"

const TabPane = Tabs.TabPane;

const HeadingSettings = ({ color, backgroundColor, content, cssClasses, elementPosition, element, handleChange, advancedMode, id }) => {
    const lang = React.useContext(Language);
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key={1}>
                <SettingsPart title="ELEMENT">
                    <HeadingContent value={content} handleChange={handleChange} />
                    <HeadingTags element={element} handleChange={handleChange} />
                    <ElementPosition value={elementPosition} handleChange={handleChange} />
                </SettingsPart>
                {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key={2} style={{height:"500px"}}>
                <div class="mf-part-item">
                        <div class="mf-part-title" >{lang["Text Color"]}</div>
                        <div>
                            <ColorPicker value={color} onChange={(val) => handleChange("color", val)} />
                        </div>
                    </div>
                    <div class="mf-part-item">
                        <div class="mf-part-title">{lang["Background Color"]}</div>
                        <div>
                            <ColorPicker value={backgroundColor} onChange={(val) => handleChange("backgroundColor", val)} />
                        </div>
                    </div>
                {advancedMode && <CssClasses value={cssClasses} handleChange={handleChange} />}
            </TabPane>
        </Tabs>
    )
};

export default HeadingSettings;