import React from "react";
import { Form, TreeSelect, Button, Icon, Select } from "antd";
import 'react-quill/dist/quill.snow.css';
import Config from "../../../config";
import Language from '../../App/Language';
import axios from 'axios';

const { Option } = Select;
const { TreeNode } = TreeSelect;
class FormToPost extends React.Component {

    state = { message: "", action: null, lang: {},authors:[], categories:[]}

    componentDidMount() {
        this.setState({lang:this.context})
        this.setValues(this.props.action);

        axios.post(this.props.baseUrl + "admin-ajax.php", `action=magicform_post_constants`).
        then(response => {
            this.setState({
                authors: response.data.data.authors,
                categories: response.data.data.categories
            })
        })
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }


    createTree = (elements, onlyFields) => {
        return elements.map(el => {
            return <TreeNode disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                {el.children && el.children.length > 0 && this.createTree(el.children)}
            </TreeNode>
        });
    }

    setValues = (action) => {
        this.setState({
            message: action.payload.message,
            action
        })

        this.props.form.setFieldsValue({
            postTitle: action.payload.postTitle,
            postBody: action.payload.postBody,
            postCategory:action.payload.postCategory,
            postAuthor: action.payload.postAuthor,
            postStatus:action.payload.postStatus,
            postType:action.payload.postType,
            commentStatus:action.payload.commentStatus,
            tags: action.payload.tags,
        });
    }

    addVariable = (value,label) => {
        let tag = value!="all_form_data"?`{${label[0] + "|" + value}}`:`{${value}}`
        this.setState({
            "message": `${this.state.message+tag}`
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const action = this.state.action;
                action.payload.message = this.state.message;
                for (let i in values) {
                    action.payload[i] = values[i];
                }

                if(values.attachPdf ==  true)
                {
                    if(this.props.actions.find(item=>item.type == "generatePdf") == undefined){
                        this.setState({errModal:true})
                        this.props.form.setFields({
                            attachPdf:false
                        })
                        values.attachPdf =false;
                        return false;
                    }
                }

                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };


    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <>
            <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>

                <Form.Item label={this.state.lang['Title']}>
                    {getFieldDecorator('postTitle',{ rules: [{ required: true, message: this.state.lang['Please select post title element']+'!' }]})(
                         <TreeSelect
                         showSearch
                         style={{ width: '100%' }}
                         placeholder={this.state.lang['Please select']}
                         allowClear
                         treeDefaultExpandAll
                         dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                     >
                         {this.createTree(this.props.allElements, true)}
                     </TreeSelect >
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang['Body']}>
                    {getFieldDecorator('postBody', { rules: [{ required: true, message: this.state.lang['Please select post body element']+'!' }]})(
                          <TreeSelect
                          showSearch
                          style={{ width: '100%' }}
                          placeholder={this.state.lang['Please select']}
                          allowClear
                          treeDefaultExpandAll
                          dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                      >
                          {this.createTree(this.props.allElements, true)}
                      </TreeSelect >
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang['Authors']}>
                    {getFieldDecorator('postAuthor',{ rules: [{ required: true, message: this.state.lang['Please select author']+'!' }]})(
                        <Select>
                            {
                            this.state.authors.map((item) => 
                                <Option value={item.id}>{item.name}</Option>
                            )
                            }
                        </Select>
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang['Status']}>
                    {
                        getFieldDecorator('postStatus')(
                            <Select>
                                 <Option value="publish">{this.state.lang['Publish']}</Option>
                                 <Option value="pending">{this.state.lang['Pending']}</Option>
                                 <Option value="draft">{this.state.lang['Draft']}</Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label={this.state.lang['Comment Status']}>
                    {
                        getFieldDecorator('commentStatus')(
                            <Select>
                                 <Option value="open">{this.state.lang['Open']}</Option>
                                 <Option value="closed">{this.state.lang['Closed']}</Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label={this.state.lang['Post Type']}>
                    {
                        getFieldDecorator('postType')(
                            <Select>
                                 <Option value="post">{this.state.lang['Post']}</Option>
                                 <Option value="page">{this.state.lang['Page']}</Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label={this.state.lang["Category"]}>
                    {getFieldDecorator('postCategory', { rules: [{ required: true, message: this.state.lang['Please select category']+'!' }]})(
                        <Select>
                         {
                             this.state.categories.map((item)=> 
                                <Option value={item.id}>{item.name}</Option>
                             )
                         }
                        </Select>
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang['Tags']}>
                    {getFieldDecorator('tags')(
                         <TreeSelect
                         showSearch
                         style={{ width: '100%' }}
                         placeholder={this.state.lang['Please select']}
                         allowClear
                         treeDefaultExpandAll
                         dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                     >
                         {this.createTree(this.props.allElements, true)}
                     </TreeSelect >
                    )}
                </Form.Item>
                <Form.Item {...Config.tailFormItemLayout}>
                <Button type="primary" htmlType="submit"><Icon type="save"></Icon> {this.state.lang['Save']}</Button>
                <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang['Delete Action']}</Button>
                </Form.Item>
            </Form >
            </>
        )
        
    }
}
FormToPost.contextType = Language;
const Wrapped = Form.create({ name: 'formToPost' })(FormToPost);
export default Wrapped;
