import React from "react";
import { Radio, Form, Checkbox, InputNumber, Row, Col } from "antd";
import ColorPicker from "../../../components/ColorPicker";
import SettingsPart from "../../../components/SettingsPart";
import Language from '../Language';

const ProgressBar = (props) => {

    const { show, striped, animated, height, position, color, bgColor,showPercent } = props.values;
    const lang = React.useContext(Language);

    const handleChange = (name, value) => {
        const newProgressBar = props.values
        newProgressBar[name] = value
        props.handleChange("progresBar", newProgressBar)
    }

    return (
        <SettingsPart title={lang['PROGRESS BAR']} collapsed>
            <div class="mf-part-item">
    <Checkbox checked={show} onChange={(e) => handleChange("show", e.target.checked)}>{lang['Show Progress Bar']}</Checkbox>
            </div>
            {show &&
                <>
                    <div class="mf-part-item">
            <div class="mf-part-title">{lang['Appearance']}</div>
                        <div>
                            <Checkbox checked={striped} onChange={(e) => handleChange("striped", e.target.checked)}>{lang['Striped']}</Checkbox>
                            {
                                striped &&
                                <Checkbox checked={animated} onChange={(e) => handleChange("animated", e.target.checked)}>{lang['Animate']}</Checkbox>
                            }
                            
                        </div>
                    </div>
                    <div class="mf-part-item">
            <div class="mf-part-title">{lang['Position']}</div>
                        <div>
                            <Radio.Group onChange={(e) => handleChange("position", e.target.value)} value={position}>
            <Radio value="top">{lang['Top']}</Radio>
            <Radio value="bottom">{lang['Bottom']}</Radio>
                            </Radio.Group>
                        </div>
                    </div>

                            <div class="mf-part-item">
            <div class="mf-part-title">{lang['Bar Color']}</div>
                                <div>
                                    <ColorPicker value={color} onChange={val => handleChange("color", val)} />
                                </div>
                            </div>
                            <div class="mf-part-item">
            <div class="mf-part-title">{lang['Bar Background Color']}</div>
                                <div>
                                    <ColorPicker value={bgColor} onChange={val => handleChange("bgColor", val)} />
                                </div>
                            </div>
                    <Row>
                    <Col span={12}>
                        <div class="mf-part-item">
            <div class="mf-part-title">{lang['Show Percent']}</div>
                            <Radio.Group onChange={(e) => handleChange("showPercent", e.target.value)} value={showPercent}>
            <Radio value={true}>{lang['Yes']}</Radio>
            <Radio value={false}>{lang['No']}</Radio>
                            </Radio.Group>
                        </div>
                    </Col>
                    <Col span={6}>
                        <div class="mf-part-item">
            <div class="mf-part-title">{lang['Bar Height']}</div>
                            <InputNumber
                                style={{ width: "100%" }}
                                onChange={(value) => handleChange("height", value)}
                                value={height}
                            />
                        </div>
                    </Col>
                    </Row>
                </>}
        </SettingsPart>
    )
}

export default ProgressBar;