import React, { useState } from "react";
import Config from "../../../../config";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import Language from "../../Language";

const Name = (props) => {
    const lang = React.useContext(Language);

    const { item , originalItem } = props;
    const { readOnly, disabled, labelText, options, title, middle, surname, namePlaceholder, middlePlaceholder, surnamePlaceholder, cssClasses, fieldSize } = item.payload;

    const [value, setValue] = useState("");
    const [middleValue, setMiddleValue] = useState("");
    const [surnameValue, setSurnameValue] = useState("");

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <div className="form-inline">
                {title == true &&
                        <select
                            type='text'
                            placeholder={lang["Title"]}
                            className={`mf-form-control mf-nameinput-title ${cssClasses.join(" ")} ${Config.size[fieldSize]}`}
                            style={{ width: 65, marginRight: 10 }}
                            readOnly={readOnly}
                            disabled={disabled}
                            id={item.id}
                        >
                            {
                                options.map((item)=>
                                    <option>{item.name}</option>
                                )
                            }
                        </select>
                }

                <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${value != "" ? `mf-active` : ``} `}  >
                    <input
                        type='text'
                        placeholder={namePlaceholder}
                        value={value}
                        onChange={(e) => setValue(e.target.value)}
                        className={`mf-form-control mf-nameinputs ${cssClasses.join(" ")} ${Config.size[fieldSize]}`}
                        readOnly={readOnly}
                        disabled={disabled}
                        id={item.id}
                    />
                    {item.theme.startsWith("material") && namePlaceholder != "" && <span className="mf-material-label">{namePlaceholder}</span>}
                </div>

                {middle &&
                    <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${middleValue != "" ? `mf-active` : ``} `}  >
                        <input
                            type='text'
                            placeholder={middlePlaceholder}
                            value={middleValue}
                            onChange={(e) => setMiddleValue(e.target.value)}
                            className={`mf-form-control mf-nameinputs ${cssClasses.join(" ")} ${Config.size[fieldSize]}`}
                            readOnly={readOnly}
                            disabled={disabled}
                            id={item.id} />
                        {item.theme.startsWith("material") && middlePlaceholder != "" && <span className="mf-material-label">{middlePlaceholder}</span>}
                    </div>
                }
                {surname == true &&
                                    <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${surnameValue != "" ? `mf-active` : ``} `}  >

                    <input
                        type='text'
                        placeholder={surnamePlaceholder}
                        value={surnameValue}
                        onChange={(e) => setSurnameValue(e.target.value)}
                        className={`mf-form-control mf-nameinputs ${cssClasses.join(" ")} ${Config.size[fieldSize]}`}
                        readOnly={readOnly}
                        disabled={disabled}
                        id={item.id} />
                        {item.theme.startsWith("material") && surnamePlaceholder != "" && <span className="mf-material-label">{surnamePlaceholder}</span>}

                        </div>
                }
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout >
    );
}

export default Name;