import React from "react";
import { Select, Form, Checkbox } from "antd";
import Config from "../../../config"
import Language from '../Language';

const Option = Select.Option;

const Currencies = (props) => {

    const lang = React.useContext(Language);

    const handleChangeSymbol = (value) => {
        if(value === undefined)
        {
            props.handleChange("currency","");
            props.handleChange("currencySymbol","");
            return;
        }
        const currencySymbol = Config.currencySymbols.find(item => item.value == value)
        props.handleChange("currency",value);
        props.handleChange("currencySymbol",currencySymbol.symbol);
    }

    return (
         <>
        <Form.Item label={lang['Currencies']}>
            <Select  
                showSearch={true}
                style={{ width: '40%' }} 
                placeholder="Please Select"
                allowClear
                value={props.value==""?undefined:props.value} onChange={(value) => handleChangeSymbol(value)}>
                {
                    Config.currency.map((item) => 
                        <Option value={item.value}>{item.name}</Option>
                    )
                }
            </Select>
          </Form.Item>
          <Form.Item label={lang['Currency Position Left']}>
                <Checkbox checked={props.currencyPos} onChange={(e)=> props.handleChange("currencyPos",e.target.checked)}>{"  "}</Checkbox>
            <small>{lang['Default currency position is right']}</small>
          </Form.Item>
        </>
    )
}

export default Currencies;