import React, { useState } from "react";
import { Empty, Button, Icon, Tooltip, Switch, Select, Input, Row, Col,Modal } from "antd";
import { TreeSelect, Badge } from 'antd';
import {findElement} from "../App/helpers";
import Language from '../App/Language';
import PurchaseCode from "../App/PurchaseCode.js"
import PurchaseError from "../../components/PurchaseError";
const { TreeNode } = TreeSelect;

const ConditionalLogic = ({ rules, handleChange, allElements, settings, pages}) => {
    const lang = React.useContext(Language);
    const licenseCheck = React.useContext(PurchaseCode)
    const {advancedMode} = settings

    const [purchaseModalVisible,setPurchaseModalVisible] = useState(false);

    const createTree = (elements, onlyFields) => {
        return elements.map(el => {
            return <TreeNode disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                {el.children && el.children.length > 0 && createTree(el.children)}
            </TreeNode>
        });
    }


    const hadleChangeSettings = (val) => {
        const newSet = settings
        newSet['advancedMode'] = val
        handleChange('conditionalLogic', newSet)
    }

    const addRule = () => {
        if(!licenseCheck){
            setPurchaseModalVisible(true);
            return false;
        }

        const rule = { andor: "or", conditions: [{ field: '', value: '', operator: 'equals' }], actions: [{ type: 'show', field: '' }] };
        const newValues = rules;
        newValues.push(rule);
        handleChange("rules", newValues);
    }

    const updateRule = (index, attr, value) => {
        const newValues = rules;
        const rule = newValues.find((item, i) => index == i);
        rule[attr] = value;
        handleChange("rules", newValues);
    }

    const deleteRule = (index) => {
        let newValues = rules;
        newValues = newValues.filter((item, i) => i != index);
        handleChange("rules", newValues);
    }

    const dublicateRule = (index) => {
        let newRules = rules;
        const dublicateRule = newRules.find((item,i)=> i == index);
        newRules.push(dublicateRule);
        handleChange("rules",newRules);
    }

    
    
    const updateCondition = (ruleIndex, conditionIndex, attr, value) => {

        const newValues = rules;
        const rule = newValues.find((item, i) => ruleIndex == i);
        const condition = rule.conditions.find((item, i) => conditionIndex == i);
        if(attr == 'field'){
            condition['value'] = ""
        }
        
        condition[attr] = value
        handleChange("rules", newValues);
    }

    const addCondition = (ruleIndex) => {
        const newValues = rules;
        const rule = newValues.find((item, i) => ruleIndex == i);
        rule.conditions.push({ field: '', value: '', operator: 'equals' })
        handleChange("rules", newValues);
    }

    const deleteCondition = (ruleIndex, conditionIndex) => {
        const newValues = rules;
        const rule = newValues.find((item, i) => ruleIndex == i);
        rule.conditions = rule.conditions.filter((item, i) => i != conditionIndex);
        handleChange("rules", newValues);
    }

    const updateAction = (ruleIndex, actionIndex, attr, value) => {
        const newValues = rules; 
        const rule = newValues.find((item, i) => ruleIndex == i);
        const condition = rule.actions.find((item, i) => actionIndex == i);
        condition[attr] = value;
        handleChange("rules", newValues);
    }

    const addAction = (ruleIndex) => {
        const newValues = rules;
        const rule = newValues.find((item, i) => ruleIndex == i);
        rule.actions.push({ field: '', type: 'show' })
        handleChange("rules", newValues);
    }

    const deleteAction = (ruleIndex, actionIndex) => {
        const newValues = rules;
        const rule = newValues.find((item, i) => ruleIndex == i);
        rule.actions = rule.actions.filter((item, i) => i != actionIndex);
        handleChange("rules", newValues);
    }

    let selectableValue = []
    const conditionSelectable = (condition, ruleIndex, conditionIndex) => {
    
        if(condition.field)
        {
            const field = condition.field
            if( field.includes('selectBox') || field.includes('checkBox') || field.includes('radioButton') || field.includes('thumbnailSelector') || field.includes('multiSelect')){
                const element = {item:{}}
                findElement('page', pages, field, element);
                if(element.item.payload){
                    selectableValue = element.item.payload.options
                    return true
                }
                else{
                    updateCondition(ruleIndex, conditionIndex, 'field', undefined)
                    return false
                }       
            }
        }
        return false
    }   

    return ( 
        <div>
            {
                purchaseModalVisible &&
                <Modal
                    visible={purchaseModalVisible}
                    title="Conditional Logic Rules"
                    onCancel={()=> setPurchaseModalVisible(false)}
                    onOk={()=> setPurchaseModalVisible(false)}
                 >
                <PurchaseError/>
                </Modal>
        }
            <h1>{lang['Conditional Logic']}</h1>
            <div style={{ marginBottom: 10 }}>
            <a className="mf-admin-btn mf-admin-btn-blue" onClick={addRule}><Icon type="plus-circle" /> {lang['Add Rule']}</a>
            <Switch style={{ marginLeft: 30 }} checked={advancedMode} onChange={(val) => hadleChangeSettings(val)} /> {lang['Advanced Mode']}
                {advancedMode}
            </div>
            {rules && rules.length == 0 && <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={lang['No rule in this form']} />}
            {rules.map((item, ruleIndex) => {
                return (
                    <div className="mf-rule">
                        <div className="mf-rule-header">
                            <h6 style={{ margin: 0 }}><Badge style={{ backgroundColor: '#1890ff', marginBottom: 3, marginRight: 3 }} count={ruleIndex + 1} />{lang['Rule']}</h6>
                            {   
                                item.andor != "form" && 
                                <>
                                    <a style={{ position: "absolute", right: 35, top: 10 }} onClick={() => dublicateRule(ruleIndex)}>
                                        <Tooltip title={lang['Copy Rule']}><Icon type="copy"></Icon></Tooltip>
                                    </a>
                                    <a style={{ position: "absolute", right: 15, top: 10 }} onClick={() => deleteRule(ruleIndex)}>
                                        <Tooltip title={lang['Delete Rule']}><Icon type="delete"></Icon></Tooltip>
                                    </a>
                                </>
                            }
                        </div>
                        <Row style={{ borderRadius: 4, marginBottom: 10 }}>
                            {advancedMode && item.andor != "form" && <div style={{ padding: "0 15px" }}>
                                <Select size="small" onChange={(val) => updateRule(ruleIndex, "andor", val)} style={{ width: 80, margin: "10px 0" }} value={item.andor}>
                                    <Select.Option key="or">{lang['Any']}</Select.Option>
                                    <Select.Option key="and">{lang['All']}</Select.Option>
                                </Select> {lang['of the following conditions']} </div>
                            }
                            <Col span="12">
                                <div>
                        <h6 style={{ fontSize: 12, background: "#EEE", border: "1px solid #CCC", color: "#666", letterSpacing: 1, borderLeft: "none", padding: "6px 10px" }}>{lang['CONDITIONS']}</h6>
                                    <div style={{ padding: 15 }}>
                                        {item.conditions.map((condition, conditionIndex) => {
                                            if (condition.field == "form") {
                                            return <b>{lang['when form onload']}</b>
                                            } else {
                                                return (
                                                    <div style={{ display: "flex" }}>
                                                        <div style={{ flex: 1 }}>
                                                            <Row gutter={6} style={{ marginBottom: 5 }}>
                                                                <Col span={8}>
                                                                    <TreeSelect
                                                                        showSearch
                                                                        style={{ width: '100%' }}
                                                                        value={condition.field}
                                                                        style={{ width: "100%" }}
                                                                        placeholder="Please select"
                                                                        allowClear
                                                                        treeDefaultExpandAll
                                                                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                                                        onChange={(val) => updateCondition(ruleIndex, conditionIndex, "field", val)}
                                                                    >
                                                                        {createTree(allElements, true)}
                                                                    </TreeSelect >
                                                                </Col>
                                                                <Col span={8}>
                                                                    <Select value={condition.operator} onChange={(val) => updateCondition(ruleIndex, conditionIndex, "operator", val)} style={{ width: "100%" }}>
                                                                        <Select.Option key="equals">{lang['Equals']}</Select.Option>
                                                                        <Select.Option key="not_equals">{lang['Not Equals']}</Select.Option>
                                                                        <Select.Option key="greater_than">{lang['Greater Than']}</Select.Option>
                                                                        <Select.Option key="less_than">{lang['Less Than']}</Select.Option>
                                                                        <Select.Option key="contains">{lang['Contains']}</Select.Option>
                                                                        <Select.Option key="in_list">{lang['In List']}</Select.Option>
                                                                        <Select.Option key="not_in_list">{lang['Not in List']}</Select.Option>
                                                                    </Select>
                                                                </Col>
                                                                {
                                                                    conditionSelectable(condition, ruleIndex, conditionIndex) ?( 
                                                                        <Col span={8}>
                                                                            <Select value={condition.value} onChange={(val) => updateCondition(ruleIndex, conditionIndex, "value", val)} style={{ width: "100%" }}>
                                                                                {
                                                                                    selectableValue.map((item)=> 
                                                                                        <Select.Option value={item.value}>{item.name}</Select.Option>
                                                                                    )
                                                                                }
                                                                            </Select>
                                                                        </Col>
                                                                        ):( 
                                                                        <Col span={8}>
                                                                            <Input onChange={(e) => updateCondition(ruleIndex, conditionIndex, "value", e.target.value)} value={condition.value} />
                                                                        </Col>
                                                                        )
                                                                }
                                                               
                                                            </Row>
                                                        </div>
                                                        {advancedMode && <div style={{ flex: "0 0 20px" }} >
                                                            <Tooltip title={lang['Delete Condition']}><Button style={{ padding: "0 5px" }} type="link" onClick={() => deleteCondition(ruleIndex, conditionIndex)}><Icon size="large" type="close-circle" /></Button></Tooltip>
                                                        </div>}
                                                    </div>
                                                )
                                            }

                                        })}
                                        {(advancedMode && item.andor != "form") && <Button onClick={() => addCondition(ruleIndex)}>{lang['Add Condition']}</Button>}
                                    </div>
                                </div>
                            </Col>
                            <Col span="12">
                                    <h6 style={{ fontSize: 12, background: "#EEE", border: "1px solid #CCC", color: "#666", letterSpacing: 1, borderRight: "none", borderLeft: "none", padding: "6px 10px" }}>{lang['ACTIONS']}</h6>
                                <div style={{ padding: 15 }}>
                                    {item.actions.map((action, actionIndex) => (
                                        <div style={{ display: "flex" }}>
                                            <div style={{ flex: 1 }}>
                                                <Row gutter={6} style={{ marginBottom: "5px" }}>
                                                    <Col span={8}>
                                                        <Select value={action.type} onChange={(val) => updateAction(ruleIndex, actionIndex, "type", val)} style={{ width: "100%" }}>
                                                            <Select.Option key="show">{lang['Show']}</Select.Option>
                                                            <Select.Option key="hide">{lang['Hide']}</Select.Option>
                                                            <Select.Option key="enable">{lang['Enable']}</Select.Option>
                                                            <Select.Option key="disable">{lang['Disable']}</Select.Option>
                                                            <Select.Option key="setValueOf">{lang['Set Value of Field']}</Select.Option>
                                                            <Select.Option key="readonly">{lang['Set Readonly']}</Select.Option>
                                                        </Select>
                                                    </Col>
                                                    <Col span={action.type == "setValueOf"?8:16}>
                                                        <TreeSelect
                                                            showSearch
                                                            style={{ width: '100%' }}
                                                            placeholder={lang['Please select']}
                                                            allowClear
                                                            treeDefaultExpandAll
                                                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                                            value={action.field} onChange={(val) => updateAction(ruleIndex, actionIndex, "field", val)} style={{ width: "100%" }}>
                                                            {createTree(allElements, false)}
                                                        </TreeSelect>
                                                    </Col>
                                                    {
                                                        action.type == "setValueOf"&&
                                                        <Col span={8}>
                                                            <Input value={action.setValue} placeholder="Value" onChange={(e) => updateAction(ruleIndex, actionIndex, "setValue", e.target.value)} />
                                                        </Col>
                                                    }
                                                </Row>
                                            </div>
                                            {(advancedMode || item.andor == "form") && <div style={{ flex: "0 0 20px" }}>
                                                <Tooltip title={lang["Delete Action"]}><Button style={{ padding: "0 5px" }} type="link" onClick={() => deleteAction(ruleIndex, actionIndex)}><Icon size="large" type="close-circle" /></Button></Tooltip>
                                            </div>}
                                        </div>
                                    ))}
                                    {(advancedMode || item.andor == "form") && <Button onClick={() => addAction(ruleIndex)}>{lang['Add Action']}</Button>}
                                </div>
                            </Col>
                        </Row>
                    </div>
                )
            })}
        </div >
    )
}

export default ConditionalLogic;
