import React from "react";
import { Form, Button, Icon, Select ,TreeSelect, Alert,Spin} from "antd";
import Config from "../../../config";
import axios from 'axios';
import Language from '../../App/Language';
const { TreeNode } = TreeSelect;
const {Option} = Select;

class MailChimp extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = { 
            load:true,
            action: null, 
            mailLists:[],
            selectInput:"", 
            errorMessage:{
                title:"MailChimp Settings Required",
                detail:"Please setup MailChimp settings in Settings",
                status:0
            },
            lang: {}
        }
    }
    
    componentDidMount() {
        this.setState({lang:this.context})
        if(this.props.emailSettings.mailChimpSettings != null && this.props.emailSettings.mailChimpSettings.apikey != "")
        {
            axios.post(this.props.baseUrl+"admin-ajax.php?action=magicform_get_mailchimp_list")
        
            .then(function (response) {
                this.setState({
                    mailLists: response.data.data,
                    load:false
                },()=>{
                    this.setValues(this.props.action);
                });
            }.bind(this))
            .catch(function (error) {
                const errorMessage = {
                    title:this.state.lang['API Key Invalid'],
                    detail:this.state.lang["Your API key may be invalid"],
                    status:400
                }
                this.setState({
                    errorMessage:errorMessage,
                    load:false
               })
            }.bind(this))
        }
        this.setValues(this.props.action);
    }

    createTreeTag = (elements, onlyFields) => {
        return elements.map(el => {
            return  <TreeNode  disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                        {el.children && el.children.length > 0 && this.createTreeTag(el.children)}
                    </TreeNode>
        });
    }

    setValues = (action) => {
        this.setState({ action: action });
        this.props.form.setFieldsValue({
            mailListId: action.payload.mailListId,
            selectMailInput:action.payload.selectMailInput,
            selectFirstName:action.payload.selectFirstName,
            selectLastName: action.payload.selectLastName
        });
    }


    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const action = this.state.action;
                for (let i in values) {
                    action.payload[i] = values[i];
                }
                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        const { emailSettings } =this.props
        let settingsConfigured = true;

        if(emailSettings.mailChimpSettings == null || emailSettings.mailChimpSettings.apikey == "" || this.state.errorMessage.status !=0){
            settingsConfigured = false;
        }

        if( this.state.load && settingsConfigured)
            return <Spin style={{marginLeft:"35%", width:"150px"}} tip={this.state.lang["Loading"]+"MailChimp..."}></Spin>

        return (
                settingsConfigured ? (
                    <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>
                        <Form.Item label={this.state.lang["Mail List"]} extra={this.state.lang["You have to select mail list"]} >
                            {
                                getFieldDecorator('mailListId', { rules: [{ required: true, message: this.state.lang['Please select email list']+"!" }]})(
                                <Select> 
                                {
                                    this.state.mailLists.map((item,index) => (
                                        <Option key={index} value={item.id} > {item.name} </Option>
                                    ))
                                }
                                </Select>
                                )
                            }
                        </Form.Item>
                        <Form.Item label={this.state.lang["Email Field"]} extra={this.state.lang["You have to email field"]}>
                    {
                        getFieldDecorator('selectMailInput', {rules: [{ required: true, message: this.state.lang['Please select email element']+"!" }]})
                        (
                            <TreeSelect
                                showSearch
                                style={{ width: '100%' }}
                                placeholder={this.state.lang["Please select"]}
                                allowClear
                                treeDefaultExpandAll
                                dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            >
                                <TreeNode disabled={true} value="form_elements" title={this.state.lang["Form Elements"]}>
                                    {this.createTreeTag(this.props.allElements,true)}
                                </TreeNode>
                            </TreeSelect>
                        )
                    }
                </Form.Item>
                <Form.Item label={this.state.lang["First Name"]} >
                    {
                       getFieldDecorator('selectFirstName')
                       (
                           <TreeSelect
                           showSearch
                           style={{ width: '100%' }}
                           allowClear
                           treeDefaultExpandAll
                           dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                       >
                           <TreeNode disabled={true} value="form_elements" title={this.state.lang["Form Elements"]}>
                               {this.createTreeTag(this.props.allElements,true)}
                           </TreeNode>
                       </TreeSelect>
                       )
                    }
                </Form.Item>
                <Form.Item label={this.state.lang["Last Name"]} >
                {
                    getFieldDecorator('selectLastName')
                    (
                        <TreeSelect
                        showSearch
                        style={{ width: '100%' }}
                        allowClear
                        treeDefaultExpandAll
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                    >
                        <TreeNode disabled={true} value="form_elements" title={this.state.lang["Form Elements"]}>
                            {this.createTreeTag(this.props.allElements,true)}
                        </TreeNode>
                    </TreeSelect>
                    )
                }
                </Form.Item>
                        <Form.Item {...Config.tailFormItemLayout}>
                        <Button type="primary" htmlType="submit"><Icon type="save"></Icon> {this.state.lang['Save']}</Button>
                            {this.props.actionIndex != null && <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang['Delete Action']}</Button>}
                        </Form.Item>
                    </Form>
                    ):(
                        <>
                            <Alert type="warning" showIcon message={this.state.errorMessage.title} description={this.state.errorMessage.detail} />
                    <div style={{ textAlign: "center", padding: "20px 0" }}><span dangerouslySetInnerHTML={{__html:this.state.lang['Setup MailChimp Settings']}} /></div>
                        </>
                    )
                )
    }
}
MailChimp.contextType = Language;

const Wrapped = Form.create({ name: 'mailChimp' })(MailChimp);

export default Wrapped;