import React from "react";
import { Input, Radio, Tooltip, Icon } from "antd";
import SettingsPart from "../../../components/SettingsPart";
import Language from '../Language';

const Description = (props) => {
    const lang = React.useContext(Language);

    return (
        <SettingsPart title={lang['DESCRIPTION']}>

            <Input.TextArea
                value={props.descriptionValue}
                onChange={(e) => props.handleChange("description", e.target.value)} />

            {props.descriptionValue != "" &&
                <Radio.Group size="small" style={{ marginTop: 6 }} buttonStyle="solid" value={props.descriptionPosition} onChange={(e) => props.handleChange("descriptionPosition", e.target.value)}>
                    <Tooltip title="Above the field"><Radio.Button value="top"><Icon type="vertical-align-top" /></Radio.Button></Tooltip>
                    <Tooltip title="Below the field"><Radio.Button value="bottom"><Icon type="vertical-align-bottom" /></Radio.Button></Tooltip>
                </Radio.Group>
            }
        </SettingsPart>
    )
}

export default Description;