import React from "react";
import { Tabs } from "antd";
import Label from "../../settings/Label";
import Description from "../../settings/Description";
import Required from "../../settings/Required";
import FieldSize from "../../settings/FieldSize";
import CssClasses from "../../settings/CssClasses";
import NameFields from "../../settings/NameFields";
import Validations from "../../settings/Validations/index";
import NameTitle from "../../settings/NameTitle";
import Disabled from "../../settings/Disabled"
import ReadOnly from "../../settings/ReadOnly"
import Id from "../../settings/Id"
import SettingsPart from "../../../../components/SettingsPart";
import Language from "../../Language";

const TabPane = Tabs.TabPane;

const NameSettings = ({ advancedMode, id, labelText, options, labelPosition, labelDisplay, namePlaceholder, middlePlaceholder, surnamePlaceholder,  validations, labelIcon, labelWidth, readOnly, disabled, handleChange, description, descriptionPosition, required, fieldSize, middle, surname, title, cssClasses }) => {
    const lang = React.useContext(Language);
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                    <Label labelText={labelText} labelPosition={labelPosition}
                        labelDisplay={labelDisplay}
                        advancedMode={advancedMode}
                        labelIcon={labelIcon}
                        labelWidth={labelWidth} handleChange={handleChange} />
                    <SettingsPart title={lang["INPUT"]}>
                        <div className="mf-part-item">
                            <Required value={required} handleChange={handleChange} />
                            {advancedMode &&
                                <>
                                    <Disabled disabled={disabled} handleChange={handleChange} />
                                    <ReadOnly readOnly={readOnly} handleChange={handleChange} />
                                </>    
                            }
                            <NameFields title={title} namePlaceholder={namePlaceholder}
                            middlePlaceholder={middlePlaceholder} surnamePlaceholder={surnamePlaceholder}
                            middle={middle} surname={surname}
                                handleChange={handleChange} />
                            {
                                title && 
                                <NameTitle header="Titles" options={options} handleChange={handleChange} />
                            }
                        </div>
                    </SettingsPart>
                    <Description descriptionValue={description}
                        descriptionPosition={descriptionPosition}
                        handleChange={handleChange} />
                                        {advancedMode && <Id value={id} />}

            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                    <FieldSize value={fieldSize} handleChange={handleChange} />
                    <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
            <TabPane tab={lang["Validations"]} key="3">
                <Validations types={['alpha', 'alphanumeric', 'numeric','domain', 'regex']} values={validations} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default NameSettings;