import React from "react";
import SelectOption from "./SelectOption"
import { Icon, Input, Button} from "antd";
import Language from '../Language';
class NewProductOption extends React.Component { 
    
    state = {
        enableValues :false,
        calcValues: false,
        lang:{},
        option:[],
        items:[], 
        label:""
    }

    constructor(props) {
        super(props);
        this.state.option = props.options[props.editOptionIndex]
        if(this.state.option != undefined){
            this.state.items = this.state.option.items
            this.state.label = this.state.option.label
            this.state.enableValues = this.state.option.enableValues
            this.state.calcValues = this.state.option.calcValues
        }
      }


    componentDidMount = () => {
        this.setState({lang:this.context})
    }

   handleChange = (name,value) => {

        if(name == "enableValues"){
            this.setState({enableValues:value})
        }else if(name == "calcValues") {
            this.setState({calcValues:value})
        }else if(name == "options"){
            this.setState({items:value})
        }
    }

    saveOptions = () => {
        const option = {
            items:this.state.items,
            label: this.state.label,
            enableValues:this.state.enableValues,
            calcValues:this.state.calcValues
        }
        this.props.handleChange("options",option)
        this.props.saveProductOption(0)
    }

    editOption = (index) => {
        const option = {
            items:this.state.items,
            label: this.state.label,
            enableValues:this.state.enableValues,
            calcValues:this.state.calcValues
        }
        this.props.handleEditOption(index, option)
        this.props.saveProductOption(0)
    }

    render() {
        return (
            <>
                <h5><a onClick={() => this.props.saveProductOption(0)} style={{ display: "inline-block", marginRight: 10 }}><Icon type="left-circle"></Icon></a>{this.state.lang['Options']}</h5>
                <div className="mf-part-item">
                    <label>{this.state.lang['Label']}</label>
                    <Input value={this.state.label} onChange={(e) => this.setState({label:e.target.value}) } placeholder={this.state.lang['Label']} />
                </div>
                <SelectOption enableValues={this.state.enableValues} calcValues={this.state.calcValues} type="selectBox" values={this.state.items} handleChange={this.handleChange} />
                <div style={{marginTop:10}}>
                {
                this.props.editOptionIndex != -1?(
                    <div className="mf-part-item">
                        <Button type="success" icon="edit" onClick={() => this.editOption(this.props.editOptionIndex)}>{this.state.lang['Edit Option']}</Button>
                    </div>
                ):(
                    <div className="mf-part-item">
                         <Button type="success" icon="plus-circle" onClick={() => this.saveOptions()}>{this.state.lang['Save Option']}</Button>
                    </div>
                )
                }
                </div>
            </>
        )
    }
   
}
NewProductOption.contextType = Language;
export default NewProductOption