import React from "react";
import { Input, Tooltip, Icon } from "antd";
import Language from '../Language';


const DefaultValue = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Default Value']} <Tooltip title={lang['Use get variables or post variables. Ex. {name} or {description}']}><Icon type="question-circle"></Icon></Tooltip></span>
            <Input
                value={props.value}
                onChange={(e) =>  props.handleChange("defaultValue",e.target.value)} />
        </div>
    )
}

export default DefaultValue;