import React from "react";
import { Input, Radio } from "antd";
import Language from '../Language';

const OtherInput = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div class="mf-part-item">
    <span className="mf-part-title" style={{marginRight:"5px"}}>{lang['Other']}</span>
                <Radio.Group onChange={(e)=>props.handleChange("otherCheck", e.target.value)} value={props.otherCheck}>
    <Radio value={true}>{lang['Yes']}</Radio>
    <Radio value={false}>{lang['No']}</Radio>
                </Radio.Group>
            </div>
        <div class="mf-part-item">
                {
                    props.otherCheck &&
                    <>
                        <span className="mf-part-title">{lang['Placeholder']}</span>
                        <Input
                            placeholder="Placeholder"
                            value={props.otherPlaceholder}
                            onChange={(e) => props.handleChange("otherPlaceholder", e.target.value)} />
                        <span className="mf-part-title"> {lang['Default Value']}</span>
                        <Input
                            placeholder="Default Value"
                            value={props.otherDefaultValue}
                            onChange={(e) => props.handleChange("otherDefaultValue", e.target.value)} />
                        
                <span className="mf-part-title">{lang['Label']}</span>
                        <Input
                            placeholder="Label"
                            value={props.otherLabel}
                            onChange={(e) => props.handleChange("otherLabel", e.target.value)} />
                    </>
                }
            </div>
        </>
    )
}

export default OtherInput;