import React from "react";
import FieldWidth from "./settings/FieldWidth";
import FieldSize from "./settings/FieldSize";
import Label from "./settings/Label";
import PageTitle from "./settings/PageTitle";
import PageSubTitle from "./settings/PageSubTitle";
import SettingsPart from "../../components/SettingsPart";
import ButtonSize from "./settings/ButtonSize";
import ButtonWidth from "./settings/ButtonWidth";
import PageTitleShow from "./settings/PageTitleShow"

import Language from './Language';

const PageSettings = (props) => {

    const lang = React.useContext(Language);
    const handleChange = (name, value) => {
        props.updatePageSettings(name, value);
    }

    const { fieldWidth, fieldSize, buttonSize,buttonWidth, subTitle, titleVisible, labelPosition, labelWidth, labelDisplay } = props.settings;

    return (
        <div style={{ padding: 20 }}>
            <h4>Page Settings</h4>
            <SettingsPart title={lang["PAGE"]}>
                <PageTitleShow value={titleVisible} handleChange={handleChange} />
                { 
                    titleVisible &&
                    <>  
                      <PageTitle value={props.name} handleChange={props.pageNameChange}/>
                      <PageSubTitle value={subTitle} handleChange={handleChange}/>
                    </>
                }
            </SettingsPart>
            <Label labelPosition={labelPosition} labelDisplay={labelDisplay} hideLabelText={true}
                labelWidth={labelWidth} handleChange={handleChange} />
            <SettingsPart title={lang["INPUT"]}>
                <FieldWidth value={fieldWidth} handleChange={handleChange} />
                <FieldSize value={fieldSize} handleChange={handleChange} />
            </SettingsPart>
            <SettingsPart title={lang["BUTTON"]}>
                <ButtonSize value={buttonSize} handleChange={handleChange} />
                <ButtonWidth value={buttonWidth} handleChange={handleChange} />
            </SettingsPart>
        </div>
    )
}

export default PageSettings;