import React from "react";
import './rating.css';
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
const StarRating = (props) => {
    const { item , originalItem } = props;
    const { ratings, defaultRating} = item.payload;
    const id = item.id

    const handleChange = (value,name) => {
        const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
    }

    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
                <div className="mf-rate" id={id}>
                    {
                        ratings.map((item, index) =>
                            <>
                                <input type="radio" onChange={(e) => handleChange(ratings.length-(e.target.value),"defaultRating")} value={index} checked={defaultRating==(ratings.length-(index))?true:false} id={`${id}_${index}`} name={id} />
                                <label htmlFor={`${id}_${index}`} title={item.name}>{item.name + index}</label>
                            </>
                        )
                    }
                </div>
                <div className="clearfix"></div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout>
    );
}

export default StarRating;