import React from "react";
import { Select, Button, Modal } from "antd";
import Themes from "../../../templates/themes.json";
import Language from '../Language';

const { confirm } = Modal;

const { Option } = Select;

const SelectTheme = (props) => {
    const lang = React.useContext(Language);

    const handleChange = (val) => {
        if (props.value.customized) {
            confirm({
                title: lang['This theme was customized'],
                content: lang['You will lose changes if change theme']+ lang['Are you sure']+"?",
                onOk() {
                    const value = JSON.parse(JSON.stringify(Themes[val]));
                    props.handleChange("theme", value);
                },
                onCancel() {
                },
            });
        } else {
            const value = JSON.parse(JSON.stringify(Themes[val]));
            props.handleChange("theme", value);
        }

    }

    return (
        <div className="mf-part-item">
            <Select value={props.value.name} style={{ width: 160 }}
                onChange={(value) => handleChange(value)}>
                {
                    Object.entries(Themes).map(item => (
                        <Option value={item[1].name}>{item[1].themeLabel}</Option>
                    ))
                }
            </Select>
            <Button type="link" style={{ marginLeft: 5 }} onClick={() => props.setCustomizeShow(1)}>{lang['Customize']}</Button>
        </div>
    )
}

export default SelectTheme;