import React from "react";
import { Form, Checkbox, Input } from "antd";
import Language from '../Language';

const ConfirmInput = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <div className="mf-part-item">
                <Checkbox checked={props.value}
                    onChange={(e) => props.handleChange("confirmInput", e.target.checked)}>
                   {lang['Show Confirm Input']}</Checkbox>
            </div>
            {props.value &&
                <>
                    <div className="mf-part-item">
                    <span className="mf-part-title">{lang['Confirm Label Text']}</span>
                        <Input
                            name="label"
                            value={props.confirmLabelText}
                            onChange={(e) => props.handleChange("confirmLabelText", e.target.value)} />
                    </div>
                    <div className="mf-part-item">
                    <span className="mf-part-title">{lang['Confirm Placeholder']}</span>
                        <Input
                            name="label"
                            value={props.confirmPlaceholder}
                            onChange={(e) => props.handleChange("confirmPlaceholder", e.target.value)} />
                    </div>
                </>
            }
        </div>
    )
}

export default ConfirmInput;