import React from "react";
import { Input, Tag, Tooltip, Icon } from "antd";
import Language from '../../Language';

const ErrorMessage = ({ value, onChange }) => {
    const lang = React.useContext(Language);


    const addVariable = (variable) => {
        onChange(`${value} {${variable}}`);
    }

    return (
        <div class="mf-part-item">
            <span class="mf-part-title">{lang['Error Message']}</span> <Tooltip title={lang['You can use variable like {value}']}><Icon type="question-circle"></Icon></Tooltip>
            <Input value={value}
                onChange={(e) => onChange(e.target.value)} />
            <div style={{ marginTop: 10 }}>
    <Tooltip placement="bottom" title={lang['Value that user entered']}><Tag onClick={() => addVariable("value")}>{lang['Input Value']}</Tag></Tooltip>
            </div>
        </div>
    );
}
export default ErrorMessage;