import React from "react";
import { Select } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const Date = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
            <div class="mf-part-item">
    <span class="mf-part-title">{lang['Date Format']}</span>
                
                <Select style={{width:"100%"}} value={item.payload.format} onChange={(val) => updateValidation(index, "format", val)}>
                    <Select.Option key="dd/mm/yyyy">{lang['Date']} d/m/y (01/12/2020)</Select.Option>
                    <Select.Option key="mm/dd/yyyy">{lang['Date']} m/d/y (12/31/2020)</Select.Option>
                    <Select.Option key="dd.mm.yyyy">{lang['Date']} d.m.y (31.12.2020)</Select.Option>
                    <Select.Option key="yyyy-mm-dd">Mysql {lang['Date Format']} (2020-12-31)</Select.Option>
                </Select>
            </div>
        </>
    )
}

export default Date;