import React from 'react'
import Language from './Language';

import { withRouter } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import EditableInput from "../../components/EditableInput";

import {Alert} from "antd";

const MultiStepHeader = (props) => {
    const lang = React.useContext(Language);

    const { type, theme, visible } = props.formSettings.multiStep;
    const {show, striped, animated, position, height, showPercent, bgColor, color} = props.formSettings.progressBar
    const hashes = window.location.hash.split("/");
    let selectedMenu = null;
    if (hashes && hashes[1] != "") {
        selectedMenu = hashes[1];
    }

    if (props.pages.length <= 2) {
        return null;
    }
    const percent =( props.currentPage.index-1) * 100 / (props.pages.length-1);

    const EditableSpan = EditableInput("span");
    return (
      
        <div onClick={() => props.history.push("/multi-step")} className={`mf-multistep-header ${selectedMenu=="multi-step" ? "mf-active" : ""} mf-multistep-${theme}`}>
        {
             visible?(
            <ul className="mf-multistep-nav">
            { props.pages.map((page, index) => {
                if (page.type == "page") {
                    return (
                        <li className={`mf-multistep-nav-item ${page.index == props.currentPage.index ? "mf-multistep-nav-item-active" : ""} `}>
                            <span className="mf-multistep-page-icon">{type == "icon" ? <FontAwesomeIcon icon={['fas', page.step.icon]} /> : index + 1}</span>
                            <div className="mf-multistep-page-title"><EditableSpan placeholder={lang['Step Title']} value={page.step.title} onChange={(val)=> props.updatePageStep("title",val, page.index)}/></div>
                            <div className="mf-multistep-page-subtitle"><EditableSpan placeholder={lang['Step Description']} value={page.step.description} onChange={(val)=> props.updatePageStep("description",val, page.index)}/></div>
                        </li>
                    )
                }
            })}
        </ul>):( <Alert type="warning" message={lang['Hidden Multi-Step Header']}/>)
        }
        {
            show &&
            <div className="mf-progress" style={{height:`${height}px`, backgroundColor:`rgba(${bgColor.r}, ${bgColor.g}, ${bgColor.b}, ${bgColor.a})`}}>
                <div className={`mf-progress-bar ${striped?"mf-progress-bar-striped":""} ${animated?"mf-progress-bar-animated":""}` } style={{ backgroundColor:`rgba(${color.r}, ${color.g}, ${color.b}, ${color.a})`, width:percent.toFixed(2)+"%"}}>
                    {showPercent? (percent.toFixed(0))+"%":""}
                </div>
            </div>
        }
    </div>
        
    )
}

export default withRouter(MultiStepHeader);