import React, { useState } from "react";
import { Icon, Tooltip, Popconfirm } from "antd";
import Text from "./Text/index";
import Password from "./Password/index";
import ParagraphText from "./ParagraphText/index";
import Number from "./Number/index";
import SelectBox from "./SelectBox/index";
import Group from "./Group/index";
import Grid from "./Grid/index";
import RadioButton from "./RadioButton/index";
import Index from "./CheckBox/index";
import ThumbnailSelector from "./ThumbnailSelector/index";
import GoogleMaps from "./GoogleMaps/index";
import Name from "./Name/index";
import EMail from "./Email/index";
import Phone from "./Phone/index";
import AddressForm from "./AddressForm/index";
import CountryList from "./CountryList/index";
import FileUpload from "./FileUpload/index";
import SubmitButton from "./SubmitButton/index";
import Button from "./Button/index"
import DatePicker from "./DatePicker/index"
import RangeSlider from "./RangeSlider/index";
import StarRating from "./StarRating";
import ScaleRating from "./ScaleRating";
import ReCaptchaElement from "./ReCaptcha";
import TermsOfUse from "./TermsOfUse";
import Time from "./Time"
import Hidden from "./Hidden"
import Html from "./Html"
import Signature from "./Signature"
import Image from "./Image"
import Heading from "./Heading"
import Language from "../Language";
import MultiSelect from "./MultiSelect";
import CalculateField from "./CalculateField";
import ProductList from "./ProductList";
import HoneyPot from './HoneyPot';

const getInheritedValues = (attrs, obj, parent, parent2) => {
    attrs.map(attr => {
        if (obj.payload[attr]) {
            let val = obj.payload[attr] == "inherit" ? (parent[attr] == "inherit" ? parent2[attr] : parent[attr]) : obj.payload[attr];
            obj.payload[attr] = val;
        }
    });
}

const Element = (props) => {
    const lang = React.useContext(Language);
    const [collapsible, setCollapsible] = useState(false);

    /**
     * Elemanlarn inherit olarak seilen parametrelerini parent objelerinden alyoruz.
     * Eer inherit seildi ise nce page settings e bakyoruz.
     * Eer o da inherit ise form settings'den alyoruz.
     */
    let { item, pageSettings, formSettings } = props;
    item = JSON.parse(JSON.stringify(item));
    const originalItem = JSON.parse(JSON.stringify(item));

    item.theme = formSettings.theme.name;
    getInheritedValues(["labelPosition", "labelDisplay", "labelWidth", "fieldSize", "fieldWidth","buttonSize","buttonWidth"], item, pageSettings, formSettings);

    const renderElementType = type => {
        switch (type) {
            case "text":
                return <Text item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "password":
                return <Password item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "paragraphText":
                return <ParagraphText item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "number":
                return <Number item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "selectBox":
                return <SelectBox item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "group":
                return <Group
                    item={item}
                    updateElement={props.updateElement}
                    id={item.id}
                    selectElement={props.selectElement}
                    deleteElement={props.deleteElement}
                    activeElement={props.activeElement}
                    duplicateElement={props.duplicateElement}
                    pageSettings={props.pageSettings}
                    formSettings={props.formSettings}
                    pluginSettings={props.pluginSettings}
                    updateOrder={props.updateOrder}
                    collapsible={collapsible}
                />

            case "grid":
                return <Grid
                    item={item}
                    updateElement={props.updateElement}
                    id={item.id}
                    selectElement={props.selectElement}
                    deleteElement={props.deleteElement}
                    activeElement={props.activeElement}
                    duplicateElement={props.duplicateElement}
                    pageSettings={props.pageSettings}
                    formSettings={props.formSettings}
                    pluginSettings={props.pluginSettings}
                    updateOrder={props.updateOrder}
                    collapsible={collapsible}
                />
            case "radioButton":
                return <RadioButton item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "checkBox":
                return <Index item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "thumbnailSelector":
                return <ThumbnailSelector item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "name":
                return <Name item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "email":
                return <EMail item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "phone":
                return <Phone item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "address":
                return <AddressForm item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "googleMap":
                return <GoogleMaps item={item} originalItem ={originalItem} baseUrl={props.baseUrl} language={formSettings.selectLanguage} googleSettings={props.pluginSettings.googleSettings} updateElement={props.updateElement} id={item.id} />
            case "countryList":
                return <CountryList item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "fileUpload":
                return <FileUpload item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "submitButton":
                return <SubmitButton item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "button":
                return <Button item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "datePicker":
                return <DatePicker language={formSettings.selectLanguage} originalItem ={originalItem} item={item} updateElement={props.updateElement} id={item.id} />
            case "rangeSlider":
                return <RangeSlider item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "starRating":
                return <StarRating item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "scaleRating":
                return <ScaleRating item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "reCaptcha":
                return <ReCaptchaElement item={item} originalItem ={originalItem} baseUrl={props.baseUrl} language={formSettings.selectLanguage} recaptchaSettings={props.pluginSettings.recaptchaSettings} updateElement={props.updateElement} id={item.id} />
            case "termsOfUse":
                return <TermsOfUse item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "time":
                return <Time item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "hidden":
                return <Hidden item={item} updateElement={props.updateElement} id={item.id} />
            case "html":
                return <Html item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "signature":
                return <Signature item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "image":
                return <Image item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "multiSelect":
                return <MultiSelect item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "heading":
                return <Heading item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "calculateField":
                return <CalculateField item={item} originalItem ={originalItem} updateElement={props.updateElement} id={item.id} />
            case "productList":
                return <ProductList item={item} originalItem={originalItem} updateElement={props.updateElement} id={item.id}/>
            case "honeyPot":
                return <HoneyPot item={item} originalItem={originalItem} updateElement={props.updateElement} id={item.id}/>
        }
    }

    return (
        <div data-id={item.id}
            className={props.activeElement === item.id ? "mf-formElementContainer active" : "mf-formElementContainer"}
            onClick={(e) => props.selectElement(item.id, e)}>
            <div className="mf-formElementOptions">
                <ul>
                    {(item.type === "group" || item.type === "grid") &&
                        <li><a onClick={() => setCollapsible(!collapsible)}>
                            <Tooltip title={lang["Collapse"]}>
                                {collapsible ? <Icon type="plus-circle" /> : <Icon type="minus-circle" />}
                            </Tooltip>
                        </a></li>
                    }
                    <li><a className="handle"><Icon type="drag" /></a></li>
                    <li><a onClick={() => props.duplicateElement(item.id)}>
                        <Tooltip title={lang["Duplicate"]}>
                            <Icon type="copy" />
                        </Tooltip>
                    </a></li>
                    <li><Popconfirm
                        title={lang["Are you sure delete this element"]+`?`}
                        onConfirm={() => props.deleteElement(item.id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <a><Tooltip title={lang["Delete"]}><Icon type="delete" /></Tooltip></a>
                    </Popconfirm></li>
                </ul>
            </div>
            {renderElementType(item.type)}
        </div>
    )
}

export default Element;