import React from "react";
import {Form, Input, Switch, Button, Icon} from "antd";
import Config from "../../../config";
import axios from 'axios';
import Language from '../../App/Language';

class SaveDatabase extends React.Component {

    state = {action: null, tableName: "", lang: {}}

    componentDidMount() {
        this.setState({lang: this.context})
        axios.post(this.props.baseUrl + "admin-ajax.php", `action=magicform_get_table_name`, {}).then(response => {
            this.setState({
                tableName: response.data.data.table_name
            })
        })
            .catch(error => {
            });
        this.setValues(this.props.action);
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }

    setValues = (action) => {
        this.setState({action: action});
        this.props.form.setFieldsValue({
            logIp: action.payload.logIp,
            logUserAgent: action.payload.logUserAgent,
            logRegisteredUser: action.payload.logRegisteredUser
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const action = this.state.action;
                for (let i in values) {
                    action.payload[i] = values[i];
                }
                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;

        return (
            <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>
                <Form.Item label={this.state.lang["Table Name"]} extra={this.state.lang["Table name can not change"]}>
                    <Input readOnly value={this.state.tableName}></Input>
                </Form.Item>
                <Form.Item label={this.state.lang["Save IP"]}
                           extra={this.state.lang["Visitor IP will be saved to database, and shown in submission detail"] + "."}>
                    {getFieldDecorator('logIp', {valuePropName: 'checked'})(
                        <Switch/>
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Save User-Agent Info"]}
                           extra={this.state.lang["Visitor User-Agent info like Browser, Operating System... will be saved to database, and shown in submission detail"] + "."}>
                    {getFieldDecorator('logUserAgent', {valuePropName: 'checked'})(
                        <Switch/>
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Save Registered User Info"]}
                           extra={this.state.lang["Save WordPress logged in user name and email address to database"]}>
                    {getFieldDecorator('logRegisteredUser', {valuePropName: 'checked'})(
                        <Switch/>
                    )}
                </Form.Item>
                <Form.Item {...Config.tailFormItemLayout}>
                    <Button type="primary" htmlType="submit"><Icon type="save"></Icon>{this.state.lang["Save"]}</Button>
                    {this.props.actionIndex != null &&
                    <Button type="link" size="small" style={{color: "#CC3333", marginLeft: 20}}
                            onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon
                        type="delete"></Icon>{this.state.lang["Delete Action"]}</Button>}
                </Form.Item>
            </Form>
        )
    }
}

SaveDatabase.contextType = Language;
const Wrapped = Form.create({name: 'saveDatabase'})(SaveDatabase);

export default Wrapped;