import React from "react";
import { Form, Input, TreeSelect, Button, Icon, Alert, Radio, InputNumber } from "antd";
import Config from "../../../config";
import Language from '../../App/Language';

class GoogleAnalytics extends React.Component {

    state = { action: null, lang: {} }

    componentDidMount() {
        this.setState({ lang: this.context })
        this.setValues(this.props.action);
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }

    setValues = (action) => {
        this.setState({
            action
        });
        this.props.form.setFieldsValue({
            type: action.payload.type,
            event: action.payload.event,
            eventAction: action.payload.eventAction,
            eventLabel: action.payload.eventLabel,
            conversionValue: action.payload.conversionValue,
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {

                const action = this.state.action;
                for (let i in values) {
                    action.payload[i] = values[i];
                }
                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>
                <Form.Item label={this.state.lang["Type"]}>
                    {getFieldDecorator('type', {rules:[{ required: true}] })(
                        <Radio.Group>
                            <Radio value="tagmanager">Google Tag Manager</Radio>
                            <Radio value="analyticsjs">analytics.js</Radio>
                            <Radio value="gtagjs">gtag.js</Radio>
                        </Radio.Group>
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Event"]}>
                    {getFieldDecorator('event', { rules: [{ required: true }] })(
                        <Input />
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Event Action"]}>
                    {getFieldDecorator('eventAction', { rules: [{ required: true }] })(
                        <Input />
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Event Label"]}>
                    {getFieldDecorator('eventLabel')(
                        <Input />
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang["Event Value"]}>
                    {getFieldDecorator('eventValue')(
                        <InputNumber />
                    )}
                </Form.Item>
                <Form.Item {...Config.tailFormItemLayout}>
                    <Button type="primary" htmlType="submit"><Icon type="save"></Icon> {this.state.lang['Save']}</Button>
                    <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang['Delete Action']}</Button>
                </Form.Item>
            </Form >
        )
    }
}

GoogleAnalytics.contextType = Language;
const Wrapped = Form.create({ name: 'googleAnalytics' })(GoogleAnalytics);
export default Wrapped;
