import React, { useState } from "react";
import Element from "./elements";
import { Empty, Icon, Popover,Alert } from "antd";
import {withRouter}  from "react-router-dom";
import EditableInput from "../../components/EditableInput";
import { ReactSortable } from "react-sortablejs";
import Language from './Language';


const Page = (props) => {
    const { elements, addElement, updateElement, updatePageSettings, selectElement, deleteElement, duplicateElement, activeElement, pluginSettings, currentPage, pageNameChange, formSettings, updateOrder, addPage,baseUrl } = props
    const [popoverVisible, setPopoverVisible] = useState(false);
    const lang = React.useContext(Language);

    /**
     * Bu fonksiyonu popover' kapatabilmek iin yazdm. State'i de yukar tamak istemedim.
     */
    const addElementMiddleware = (type, control = false) => {
        addElement(type, control);
        setPopoverVisible(false);
    }

    const content = (
        <div className="mf-addElementList">
            <ul>
                <li><a onClick={() => addElementMiddleware('text')}><Icon type="file-text" />{lang['Single Line Text']}</a></li>
                <li><a onClick={() => addElementMiddleware('paragraphText')}><Icon type="align-left" />{lang['Paragraph Text']}</a></li>
                <li><a onClick={() => addElementMiddleware('email')}><Icon type="mail" /> {lang['E-Mail']}</a></li>
                <li><a onClick={() => addElementMiddleware('selectBox')}><Icon type="select" /> {lang['Select list']}</a></li>
                <li><a onClick={() => addElementMiddleware('multiSelect')}><Icon type="select" /> {lang['Multi Select']}</a></li>
                <li><a onClick={() => addElementMiddleware('radioButton')}><Icon type="check-circle" /> {lang['Radio']}</a></li>
                <li><a onClick={() => addElementMiddleware('checkBox')}><Icon type="check-square" /> {lang['Check Box']}</a></li>
                <li><a onClick={() => addElementMiddleware('phone')}><Icon type="phone" /> {lang['Phone']}</a></li>
                <li><a onClick={() => addElementMiddleware('datePicker')}><Icon type="calendar" /> {lang['Date Picker']}</a></li>
                <li><a onClick={() => addElementMiddleware('time')}><Icon type="clock-circle" /> {lang['Time Picker']}</a></li>
                <li><a onClick={() => addElementMiddleware('fileUpload')}><Icon type="file-add" /> {lang['File Upload']}</a></li>
            </ul>
            <div className="mf-divider-vertical" />
            <ul>
                <li><a onClick={() => addElementMiddleware('calculateField',true)}><Icon type="calculator" /> {lang['Calculate Field']}</a></li>
                <li><a onClick={() => addElementMiddleware('productList',true)}><Icon type="shopping" /> {lang['Product List']}</a></li>
                <li><a onClick={() => addElementMiddleware('thumbnailSelector')}><Icon type="check-square" /> {lang['Thumbnail Selector']}</a></li>
                <li><a onClick={() => addElementMiddleware('name')}><Icon type="user" /> {lang['Name']}</a></li>
                <li><a onClick={() => addElementMiddleware('number')}><Icon type="number" /> {lang['Number Input']}</a></li>
                <li><a onClick={() => addElementMiddleware('address')}><Icon type="idcard" /> {lang['Address']} </a></li>
                <li><a onClick={() => addElementMiddleware('password')}><Icon type="lock" /> {lang['Password']}</a></li>
                <li><a onClick={() => addElementMiddleware('countryList')}><Icon type="flag" /> {lang['Country List']}</a></li>
                <li><a onClick={() => addElementMiddleware('rangeSlider',true)}><Icon type="sliders" /> {lang['Range Slider']}</a></li>
                <li><a onClick={() => addElementMiddleware('termsOfUse',true)}><Icon type="file-text" /> {lang['Terms Of Use']}</a></li>
                <li><a onClick={() => addElementMiddleware('button')}><Icon type="border" /> {lang['Button']}</a></li>
                <li><a onClick={() => addElementMiddleware('submitButton')}><Icon type="file-protect" /> {lang['Submit Button']}</a></li>
            </ul>
            <div className="mf-divider-vertical" />
            <ul>
                <li><a onClick={() => addElementMiddleware('html',true)}><Icon type="edit" /> {lang['Custom Html']}</a></li>
                <li><a onClick={() => addElementMiddleware('heading', true)}><Icon type="font-size" /> {lang['Heading']}</a></li>
                <li><a onClick={() => addElementMiddleware('group', true)}><Icon type="block" /> {lang['Group']}</a></li>
                <li><a onClick={() => addElementMiddleware('grid',true)}><Icon type="table" /> {lang['Grid']}</a></li>
                <li><a onClick={() => addElementMiddleware('reCaptcha',true)}><Icon type="unordered-list" />reCaptcha</a></li>
                <li><a onClick={() => addElementMiddleware('honeyPot',true)}><Icon type="coffee" />Honey Pot</a></li>
                
                <li><a onClick={() => addElementMiddleware('googleMap',true)}><Icon type="environment" /> Google Maps</a></li>
                <li><a onClick={() => addElementMiddleware('starRating',true)}><Icon type="star" /> {lang['Star Rating']}</a></li>
                <li><a onClick={() => addElementMiddleware('scaleRating',true)}><Icon type="rise" /> {lang['Scale Rating']}</a></li>
                <li><a onClick={() => addElementMiddleware('hidden',true)}><Icon type="eye-invisible" />{lang['Hidden Input']}</a></li>
                <li><a onClick={() => addElementMiddleware('signature',true)}><Icon type="edit" />{lang['Signature']}</a></li>
                <li><a onClick={() => addElementMiddleware('image',true)}><Icon type="file-image" />{lang['Image']}</a></li>
            </ul>
        </div>
    );

    const EditableSpan = EditableInput("span");

    const hashes = window.location.hash.split("/");
    let selectedMenu = null;
    if (hashes && hashes[1] != "") {
        selectedMenu = hashes[1];
    }

    return (
        <form autoComplete="off">        
            <div onClick={()=>props.history.push("/page-settings")} className={`mf-multistep-header ${selectedMenu=="page-settings" ? "mf-active" : ""}`}>
                    {
                        currentPage.settings.titleVisible?( 
                        <>
                            <h2 className="mf-page-title"><EditableSpan placeholder={lang['Page Name']} value={currentPage.name} onChange={(val)=> pageNameChange(val,{keyCode:10})}/></h2>
                            <p className="mf-page-description"><EditableSpan placeholder={lang['Page Sub Title']} value={currentPage.settings.subTitle} onChange={(val)=> updatePageSettings("subTitle",val)}/></p>
                        </>
                        ):(
                            <>
                                <Alert type="warning" message={lang['Hidden Page Title and Sub Title']}/>
                            </>
                        )
                    
                    }
            </div>
            {!elements || elements.length === 0 &&
                <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={lang['No form element in this page']} />
            }

            <ReactSortable
                list={elements}
                animation={200}
                delayOnTouchStart={true}
                delay={2}
                group="page-elements"
                handle=".handle"
                setList={newState => updateOrder(newState, 'page')}
            >
                {elements && elements.length > 0 && elements.map(item => <Element
                    key={item.id}
                    selectElement={selectElement}
                    deleteElement={deleteElement}
                    activeElement={activeElement}
                    duplicateElement={duplicateElement}
                    updateElement={updateElement}
                    item={item}
                    pageSettings={currentPage.settings}
                    formSettings={formSettings}
                    updateOrder={updateOrder}
                    pluginSettings={pluginSettings}
                    baseUrl={baseUrl}
                />
                )}
            </ReactSortable>
            <div style={{ marginTop: 30, border: "1px dashed #CCC", borderRadius:4, padding: 30, textAlign: "center" }}>
                <Popover visible={popoverVisible}
                    onVisibleChange={status => setPopoverVisible(status)}
                    placement="bottom" content={content}>
                    <button type="button" className="mf-admin-btn mf-admin-btn-green"><Icon type="plus-circle" />{lang['Add Element']} </button>
                </Popover>
                <div style={{marginTop:15}}>
                <a onClick={()=>addPage()} className="mf-admin-btn"><Icon type="file-add"></Icon>{lang['Add Page']}</a>
                </div>
            </div>
        </form>
    )
}

export default withRouter(Page);