import React from "react";
import { Radio } from "antd";
import Language from '../Language';

const SelectInputType = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <div className="mf-part-title">{lang['Select Type']} </div>
            <div>
                <Radio.Group onChange={(e) => props.handleChange("inputType", e.target.value)} value={props.value}>
                    <Radio value="radio">{lang['Single Choice']}</Radio>
                    <Radio value="checkbox">{lang['Multiple Selection']}</Radio>
                </Radio.Group>
            </div>
        </div>
    )
}
export default SelectInputType;