import React from "react";
import { Empty } from "antd";
import TextSettings from "./elements/Text/settings";
import PasswordSettings from "./elements/Password/settings";
import ParagraphTextSettings from "./elements/ParagraphText/settings";
import NumberSettings from "./elements/Number/settings";
import SelectBoxSettings from "./elements/SelectBox/settings";
import RadioButtonSettings from "./elements/RadioButton/settings";
import CheckboxSettings from "./elements/CheckBox/settings";
import ThumbnailSelectorSettings from "./elements/ThumbnailSelector/settings";
import NameSettings from "./elements/Name/settings";
import EmailSettings from "./elements/Email/settings";
import PhoneSettings from "./elements/Phone/settings";
import AddressSettings from "./elements/AddressForm/settings";
import GoogleMapSettings from "./elements/GoogleMaps/settings";
import CountryListSettings from "./elements/CountryList/settings";
import FileUploadSettings from "./elements/FileUpload/settings";
import ButtonSettings from "./elements/Button/settings";
import SubmitButtonSettings from "./elements/SubmitButton/settings";
import RangeSliderSettings from "./elements/RangeSlider/settings";
import StarRatingSettings from "./elements/StarRating/settings";
import ScaleRatingSettings from "./elements/ScaleRating/settings";
import ReCaptchaSettings from "./elements/ReCaptcha/settings";
import DatePickerSettings from "./elements/DatePicker/settings"
import TermsOfUseSettings from "./elements/TermsOfUse/settings";
import TimeSettings from "./elements/Time/settings";
import HiddenSettings from "./elements/Hidden/settings";
import HtmlSettings from "./elements/Html/settings";
import SignatureSettings from "./elements/Signature/settings";
import ImageSettings from "./elements/Image/settings";
import MultiSelectSettings from "./elements/MultiSelect/settings";
import Heading from "./elements/Heading/settings"
import CalculateFieldSettings from "./elements/CalculateField/settings"
import ProductListSettings from "./elements/ProductList/settings"
import GridSettings from "./elements/Grid/settings"
import GroupSettings from "./elements/Group/settings"
import HoneyPotSettings from './elements/HoneyPot/settings';

const ElementSettings = (props) => {

    let element;
    let find;
    const getElementHelper = (data, type, id) => {
        if (type === "page") {
            find = data.elements.find(item => item.id === id);
            if (find) {
                element = find;
            } else {
                data.elements.map(item => {
                    if (item.type === "group" || item.type === "grid") {
                        getElementHelper(item, item.type, id);
                    }
                })
            }
        }
        if (type === "group") {
            find = data.payload.elements.find(item => item.id === id);
            if (find) {
                element = find;
            } else {
                data.payload.elements.map(item => {
                    if (item.type === "group" || item.type === "grid") {
                        getElementHelper(item, item.type, id);
                    }
                })
            }
        }
        if (type === "grid") {
            data.payload.columns.map(column => {
                find = column.elements.find(item => item.id === id);
                if (find) {
                    element = find;
                } else {
                    column.elements.map(item => {
                        if (item.type === "group" || item.type === "grid") {
                            getElementHelper(item, item.type, id);
                        }
                    })
                }
            });
        }
    }

    const newData = props.pages;
    const currentPage = newData.find((item, index) => item.index === props.activePage);
    getElementHelper(currentPage, "page", props.activeElement);

    const handleChange = (name, value) => {
        if (element.payload) {
            element.payload[name] = value;

            if (name === "labelPosition" && value !== "inline") {
                element.payload["labelWidth"] = "inherit";
            }
            props.updateElement(props.id, element);
        }
    }

    const renderTypes = type => {

        const elProps =  {id:element.id, ...element.payload, handleChange, advancedMode:props.advancedMode };

        switch (type) {
            case "text":
                return <TextSettings {...elProps} />
            case "password":
                return <PasswordSettings {...elProps} />
            case "paragraphText":
                return <ParagraphTextSettings {...elProps} />
            case "number":
                return <NumberSettings {...elProps} />
            case "selectBox":
                return <SelectBoxSettings {...elProps} />
            case "radioButton":
                return <RadioButtonSettings {...elProps} />
            case "checkBox":
                return <CheckboxSettings {...elProps} />
            case "thumbnailSelector":
                return <ThumbnailSelectorSettings {...elProps} />
            case "name":
                return <NameSettings {...elProps} />
            case "email":
                return <EmailSettings {...elProps} />
            case "phone":
                return <PhoneSettings {...elProps} />
            case "address":
                return <AddressSettings  {...elProps} />
            case "googleMap":
                return <GoogleMapSettings {...elProps} />
            case "countryList":
                return <CountryListSettings {...elProps} />
            case "fileUpload":
                return <FileUploadSettings {...elProps} />
            case "submitButton":
                return <SubmitButtonSettings  {...elProps} />
            case "button":
                return <ButtonSettings {...elProps} />
            case "rangeSlider":
                return <RangeSliderSettings  {...elProps} />
            case "starRating":
                return <StarRatingSettings {...elProps} />
            case "scaleRating":
                return <ScaleRatingSettings {...elProps} />
            case "reCaptcha":
                return <ReCaptchaSettings {...elProps} />
            case "datePicker":
                return <DatePickerSettings {...elProps} />
            case "termsOfUse":
                return <TermsOfUseSettings {...elProps} allElements={props.allElements} />
            case "time":
                return <TimeSettings {...elProps} />
            case "hidden":
                return <HiddenSettings {...elProps} />
            case "html":
                return <HtmlSettings {...elProps} />
            case "signature":
                return <SignatureSettings {...elProps} />
            case "image":
                return <ImageSettings {...elProps} />
            case "multiSelect":
                return <MultiSelectSettings {...elProps} />
            case "heading":
                return <Heading {...elProps} />
            case "calculateField":
                return <CalculateFieldSettings {...elProps} allElements={props.allElements}/>
            case "productList":
                return <ProductListSettings {...elProps} allElements={props.allElements} baseUrl={props.baseUrl}/>
            case "group":
                return <GroupSettings {...elProps} />
            case "grid": 
                return <GridSettings {...elProps} />
            case "honeyPot": 
                return <HoneyPotSettings {...elProps} />
        }
    }

    return (
        <>
            {props.activeElement && element ? renderTypes(element.type) : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description="Please select an element to edit" />
            }
        </>
    )
}

export default ElementSettings;