import React from "react";
import Language from '../Language';

import { InputNumber } from "antd";

const Height = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Height']}</span>
            <InputNumber
                style={{ width: "100%" }}
                min={props.min}
                value={props.value}
                onChange={(value) => props.handleChange("heightValue", value)} />
                <div className="ant-form-extra">{lang['Enter pixel. want to 100px, enter 100']}</div>
        </div>
    )
}

export default Height;