import React from "react";
import { Input } from "antd";
import Language from '../Language';
const HiddenName = (props) => {
    const lang = React.useContext(Language);
    return (
        <div class="mf-part-item">
            <div class="mf-part-title">{lang['Hidden Name']}</div>
            <Input
                value={props.value}
                onChange={(e) => props.handleChange("labelText", e.target.value)} />
                <small> {lang['Hidden name is displayed only on pdf, submission ,email and print pages. It is invisible from within the form.']} </small>
        </div>
    )
}

export default HiddenName;