import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Config from "../../../../config";

const Button = (props) => {
    const { item , originalItem } = props;
    const { buttonName, cssClasses, buttonPosition, buttonType, buttonSize, buttonIcon, buttonWidth } = item.payload;
    return (
        <>
            <div className={'button-' + buttonPosition}>
                <button
                    id={item.id}
                    className={`mf-btn ${Config.buttonSize[buttonSize]} ${buttonWidth != "default" && 'mf-btn-width-' + buttonWidth} ${buttonType != "default" && 'mf-btn-' + buttonType} ${cssClasses.join(" ")}`}
                    type="button">
                    {buttonIcon && <FontAwesomeIcon icon={['fas', buttonIcon]} style={{ marginRight: "5px" }} />} {buttonName}
                </button>
            </div>
        </>
    );
}

export default Button;