import React from "react";
import { Tabs } from "antd";
import Label from "../../settings/Label";
import Description from "../../settings/Description";
import Required from "../../settings/Required";
import FieldWidth from "../../settings/FieldWidth";
import FieldSize from "../../settings/FieldSize";
import CssClasses from "../../settings/CssClasses";
import CountryConfigure from "../../settings/CountryConfigure";
import IconSelect from "../../settings/IconSelect"
import Disabled from "../../settings/Disabled"
import Id from "../../settings/Id"
import SettingsPart from "../../../../components/SettingsPart";
import Language from "../../Language";
import PleaseSelect from "../../settings/PleaseSelect";

const TabPane = Tabs.TabPane;

const CountryListSettings = ({ advancedMode, id, labelText, labelPosition, pleaseSelect, inputIcon, labelIcon, allCountry, labelDisplay, disabled, labelWidth, handleChange, description, descriptionPosition, required, fieldWidth, fieldSize, countryList, cssClasses }) => {

    const lang = React.useContext(Language);

    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                <Label labelText={labelText}
                    labelPosition={labelPosition}
                    labelDisplay={labelDisplay}
                    labelIcon={labelIcon}
                    advancedMode={advancedMode}
                    labelWidth={labelWidth} handleChange={handleChange} />
                <SettingsPart title={lang["INPUT"]}>
                    <div className="mf-part-item">
                        <Required value={required} handleChange={handleChange} />
                        {advancedMode && <Disabled disabled={disabled} handleChange={handleChange} />}
                    </div>
                    {advancedMode && <IconSelect labelText={lang["Input Icon"]} property="inputIcon" icon={inputIcon} handleChange={handleChange} />}
                    <PleaseSelect value={pleaseSelect} handleChange={handleChange} />
                </SettingsPart>
                <Description descriptionValue={description}
                    descriptionPosition={descriptionPosition}
                    handleChange={handleChange} />
                <div className="mf-settings-part">
                    <h6>{lang["COUNTRY LIST"]}</h6>
                    <div className="mf-part-item">
                        <CountryConfigure allCountry={allCountry} handleChange={handleChange} />
                    </div>
                </div>
                {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                <FieldWidth value={fieldWidth} handleChange={handleChange} />
                <FieldSize value={fieldSize} handleChange={handleChange} />
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default CountryListSettings;