import React from "react";
import { Radio } from "antd";
import Language from '../Language';

const ViewHideIcon = (props) => {
    const lang = React.useContext(Language);

    return (
            <div class="mf-part-item">
                <span className="mf-part-title" style={{marginRight:"5px"}}>{lang['View Hide Icon']}</span>
                <Radio.Group onChange={(e)=>props.handleChange("viewIcon", e.target.value)} value={props.value}>
                    <Radio value={true}>{lang['Yes']}</Radio>
                    <Radio value={false}>{lang['No']}</Radio>
                </Radio.Group>
            </div>
        )

}
export default ViewHideIcon;