import React from "react";
import { Select,  Radio } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const Contains = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div class="mf-part-item">
            <span class="mf-part-title">{lang['Contain Items']}</span>
                <Select
                    defaultValue={item.payload.containList}
                    mode="tags"
                    style={{ width: '100%' }}
                    onChange={(value) => updateValidation(index, "containList", value)}
                />
            </div>
            <div class="mf-part-item">
                <Radio.Group onChange={(e) => updateValidation(index, "type", e.target.value)} value={item.payload.type}>
                 <Radio value='contains'>{lang['Contains']}</Radio>
                <Radio value='notContains'>{lang['Not Contains']}</Radio>
                </Radio.Group>
            </div>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
        </>
    )
}

export default Contains;