import React from "react";
import { Tooltip } from "antd";
import ColorSchemes from "../../../templates/colorScheme.json";
import styled from "styled-components";
import { convertRgba } from "../helpers";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Language from '../Language';

const Scheme = styled.div`
    width:30px;
    height:30px;
    display:inline-block;
    margin-right:5px;
    text-align:center;
    overflow:hidden;
    border-radius:4px;
    cursor:pointer;
    font-size:15px;
    font-weight:bold;
    border: ${props => props.active ? "2px solid #1890ff" : props.default ? "2px solid #ccc" : "2px solid transparent"};
    background: ${props => props.value && convertRgba(props.value.primaryColor)};
    text-align:center;
    padding:2px 0;
    &>svg {
        color:#FFF;
        font-size:16px;
        font-weight:bold;
    }
`;


const ColorScheme = (props) => {

    const lang = React.useContext(Language);

    const handleChange = (val) => {
        const value = JSON.parse(JSON.stringify(ColorSchemes[val]));
        props.handleChange("colorScheme", value);
    }

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Color Scheme']}</span>
            <div>
                {
                    Object.entries(ColorSchemes).map(item => {
                        if (item[0] == "default") {
                            return <Tooltip title={lang['Theme Default']}><Scheme default active={item[0] == props.value.name} onClick={() => handleChange(item[0])}>
                            D</Scheme>
                            </Tooltip>
                        } else {
                            return <Scheme value={item[1]} onClick={() => handleChange(item[0])}>
                                {item[0] == props.value.name && <FontAwesomeIcon icon={['fas', 'check']} />}
                            </Scheme>
                        }
                    }
                    )
                }
            </div>
        </div>
    )
}
export default ColorScheme;