import React, {useEffect} from "react";
import { Radio, Select, Input, Checkbox } from "antd";
import MinMaxRange from "./MinMaxRange";
import Language from '../Language';

const { Option } = Select

const TimeFormat = (props) => {
    const lang = React.useContext(Language);
    
    const clockOptions = () => {
        const clocks = [];
        let clock = 0;
        let length = 0;
        let minute = 0;
        let tag = " a.m"
        if (props.clockFormat == 12) {
            length = props.maxValue - props.minValue;
            length = length * (60 /props.timeIncrement);

            clock = props.minValue;
            if (props.minValue > 11){
                tag = " p.m"
                clock = props.minValue - 11;
            }
                
            for (let i = 0; i < length; i++) {
                clocks.push(
                clock +":"+(minute==0?"00":minute)+tag
                )
                minute+=props.timeIncrement;
                if(minute>=60){
                    minute = 0;
                    clock++
                    if (clock == 13) {
                        clock = 1;
                    }
                    if (clock == 12)
                        tag = " p.m"
                }

            }
        } else {
            length = props.maxValue - props.minValue;
            length = length * (60 / props.timeIncrement);
            clock = props.minValue;
            for (let i=0; i<length; i++){
            minute+=props.timeIncrement;
            if(minute>=60){
                minute = 0;
                clock++
            }
            clocks.push(
                clock +":"+(minute==0?"00":minute)
            )
            }
        }

        return clocks;
    }


    return (
        <div className="mf-settings-part">
            <h6>{lang['CLOCK SETTINGS']}</h6>
            <div className="mf-part-item">
                <Radio.Group onChange={(e) =>  props.handleChange("clockFormat", e.target.value)} value={props.clockFormat}>
                    <Radio value={24}>24 {lang['hour']}</Radio>
                    <Radio value={12}>12 {lang['hour']}</Radio>
                </Radio.Group>
            </div>
            <div className="mf-part-item">
                <span className="mf-part-title">{lang['Time Increment']}</span>
                <Select style={{width:"100%"}} onChange={(value) =>  props.handleChange("timeIncrement", value)} value={props.timeIncrement}>
                    <Option value={15}>{lang['Quater']}</Option>
                    <Option value={20}>{lang['Thirty Percent']}</Option>
                    <Option value={30}>{lang['Half']}</Option>
                </Select>
            </div>
            <div className="mf-part-item">
                <span style={{marginRight:"5px"}} className="mf-part-title">{lang['Default Value']}:</span>
                <Checkbox onChange={(e) => props.handleChange("defaultValueCheck",e.target.checked)}  checked={props.defaultValueCheck}/> 
                <br/>
                {
                    props.defaultValueCheck &&
                    <Select defaultValue={props.defaultValue} style={{ width: 120 }} onChange={(value)=> props.handleChange("defaultValue",value)}>
                         {clockOptions().map((item, index) => (
                            <Option value={item} key={index}>{item}</Option>
                        ))}
                    </Select> 
                }
            </div>
            <MinMaxRange minValue={0} maxValue={24} min={props.minValue} max={props.maxValue} handleChange={props.handleChange} />
        </div >
    )
}

export default TimeFormat;