import React from "react";
import { Input } from "antd";
import Language from '../Language';
import SelectAddInput from "../../../components/SelectAddInput"
const TextArea = (props) => {

    const lang = React.useContext(Language);
    const { TextArea } = Input;

    return (
        <>
        <div style={{float:"left", marginBottom:"10px"}}>
           <SelectAddInput selectType="formData" style={{width:"35px"}} value={props.value} type="textAreaValue" allElements={props.allElements} addValue={props.handleChange}/>
        </div>
        <div class="mf-part-item" style={{marginTop:10}}>
            <span className="mf-part-title"></span>
            <div>
                <TextArea
                    value={props.value}
                    placeholder={lang['Text Area']}
                    autoSize={{ minRows: 5, maxRows:20}}
                    onChange={(e) => props.handleChange("textAreaValue", e.target.value)} />
            </div>
        </div>
        </>
    )
}

export default TextArea;