import React from "react";
import {InputNumber, Form,DatePicker} from "antd";
import moment from 'moment';
import Language from '../Language';
const FormSubmission = (props) => {
    const lang = React.useContext(Language);
    const onChange = (date, dateString) => {
       props.handleChange('endSubDate',dateString);
    }
    return (
        <>
            <Form.Item label={lang['Submission Limit']}>
                <InputNumber
                    value={props.subLimit}
                    onChange={(value) => props.handleChange("subLimit", value)}
                />
                <div className="ant-form-extra">{lang['The form is displayed until the reach submission limit']+"."+lang['Zero means you can get unlimited submissions']+"."}</div>
            </Form.Item>
            <Form.Item label={lang['Submission end of date']}>
                <DatePicker
                        format="YYYY-MM-DD HH:mm:ss"
                        onChange={(date,dateString) => onChange(date,dateString)}
                        defaultValue={props.endSubDate?moment(props.endSubDate,'YYYY-MM-DD HH:mm:ss' ):null}
                        showTime={{ defaultValue:moment('00:00:00', 'HH:mm:ss') }}
                />
                <div className="ant-form-extra">{lang['The form is displayed until the selected date']}</div>
            </Form.Item>
        </>
    )
}

export default FormSubmission;