import React from "react";
import { Input} from "antd";
import Language from '../Language';


const PleaseSelect = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Please Select']}</span>
            <Input
                value={props.value}
                onChange={(e) =>  props.handleChange("pleaseSelect",e.target.value)} />
        </div>
    )
}

export default PleaseSelect;