import React from "react";
import { Select } from "antd";
import Language from '../Language';

const Option = Select.Option;

const ButtonSize = (props) => {

    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Button Size']}</span>
            <Select value={props.value}
                style={{ width: "100%" }}
                onChange={(value) => props.handleChange("buttonSize", value)}>
                {!props.hideInherit && <Option value="inherit">{lang['Inherit']}</Option>}
                <Option value="default">{lang['Default']}</Option>
                <Option value="small">{lang['Small']}</Option>
                <Option value="large">{lang['Large']}</Option>
            </Select>
        </div>
    )
}

export default ButtonSize;