import React from "react";
import InputLayout from "../../../../components/InputLayout";

const CalculateField = (props) => {
    const { item } = props;
    const { total,handleChange } = item.payload;
    return (
        <>
            <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
                <label>{total}</label>
            </InputLayout>
        </>
    );
}

export default CalculateField;