import React from "react";
import {TreeSelect} from "antd";
import Language from '../pages/App/Language';

const {TreeNode} = TreeSelect;
const SelectVariable = (props) => {

    const lang = React.useContext(Language);
    const {style} = props
    const createTreeTag = (elements, onlyFields) => {
        return elements.map(el => {
            return <TreeNode disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                {el.children && el.children.length > 0 && createTreeTag(el.children)}
            </TreeNode>
        });
    }

    return (
        <TreeSelect
            showSearch
            style={style==null?{width: '200px'}:style}
            placeholder={lang["Please select"]}
            treeDefaultExpandAll
            dropdownStyle={{maxHeight: 400, overflow: 'auto'}}
            onChange={(value, label) => props.addVariable(value, label)}
            dropdownClassName="selectVariableDropdown"
        >
            <TreeNode disabled={true} value="form_elements" title={lang["Form Elements"]}>
                {
                    props.type !="formData"?( <>
                        <TreeNode value="all_form_data" title={lang["All Form Data"]}/>
                        <TreeNode value="form_name" title={lang["Form Name"]}/>
                    </>):("")
                }
                {createTreeTag(props.allElements, true)}
            </TreeNode>
            {
                props.type !="formData"?( 
                <>
                    <TreeNode disabled={true} value="page" title={lang["Page"]}>
                        <TreeNode value="page_title" title={lang["Page Title"]}/>
                        <TreeNode value="page_url" title={lang["Page Url"]}/>
                        <TreeNode value="time" title={lang["Time"]}/>
                    </TreeNode>
                    <TreeNode disabled={true} value="user" title={lang["User"]}>
                        <TreeNode value="user_name" title={lang["User Name"]}/>
                        <TreeNode value="user_id" title={lang["User Id"]}/>
                        <TreeNode value="user_email" title={lang["User Email"]}/>
                        <TreeNode value="user_agent" title={lang["User Agent"]}/>
                    </TreeNode>
                    <TreeNode disabled={true} value="user" title={lang["Submission"]}>
                        <TreeNode value="submission_id" title={lang["Submission Id"]}/>
                    </TreeNode>
                </>):("")
            }
            
        </TreeSelect>
    )
}

export default SelectVariable;