import React from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import axios from 'axios';
import { Empty, message, Spin, Modal } from 'antd';
import Types from "../../templates/types.json";
import PageJson from "../../templates/page.json";
import ElementSettings from "./ElementSettings";
import Actions from "../Actions/Actions";
import Page from "./Page";
import PageSettings from "./PageSettings";
import FormSettings from "../Settings/index";
import Publish from "../Publish/index";
import PageList from "./PageList";
import AppHeader from "../../components/AppHeader";
import { getNewIndexHelper, findAndDelete, controlSubmitButton, updateElementHelper, duplicatePageHelper, changeImportElementsId, updateOrderHelper, duplicateElementHelper, getUrlParam, getAllElementsHelper, getAllElementsListHelper, convertRgba, getColorLuminance } from "./helpers";
import ThankYou from "./ThankYou";
import FormStyles from "./FormStyles";
import PerfectScrollbar from 'react-perfect-scrollbar'
import Preview from "../Preview";
import ThemeStyles from "./ThemeStyles";
import Themes from "../../templates/themes.json";
import WebFont from 'webfontloader';
import MultiStepHeader from "./MultiStepHeader";
import MultiStep from "./settings/MultiStep";
import Language from "./Language.js";
import PurchaseCode from "./PurchaseCode.js"
import PurchaseError from "../../components/PurchaseError";
class App extends React.Component {

    state = {
        formId: null,
        activePage: 1,
        activeElement: null,
        formSettings: {},
        preview: false,
        pluginSettings: {},
        actions: [],
        pages: [],
        loading: true,
        formSaving: false,
        baseUrl: "",
        loadPreview: false,
        lang:{},
        licenseCheck:true,
        purcahaseErrorModal:false,
        purchaseModalTitle:""
        
    };

    componentDidMount() {
        
        let formId = getUrlParam("id");
        formId = formId.substring(0, formId.indexOf("#"));
        this.setState({
            formId
        });

        axios.post(this.props.baseUrl + "admin-ajax.php", `action=magicform_get_form&id=${formId}`, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }).then((response) => {
            if (response.data.success) {
                this.setState({
                    pages: response.data.data.pages,
                    activePage:response.data.data.pages[0].index,
                    actions: response.data.data.actions,
                    formSettings: response.data.data.settings,
                    pluginSettings: response.data.data.pluginSettings,
                    lang: response.data.data.lang,
                    loading:false
                })
                
                this.checkLicense()
                this.changeFontFamily(this.state.formSettings.fontFamily)
            }
           
        });
    }

    checkLicense = () => {
        axios.post(this.props.baseUrl + "admin-ajax.php", `action=magicform_check_license`, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        }).then((response) => {
            if(!response.data.success){                
                this.setState({licenseCheck:false})
            }
        })
    }

    handlePusrchaseOk = () => {
        window.location.href = this.props.baseUrl+"?page=magicform_admin";
    }

    handleMessage = (e) => {
    }

    changePage = (index) => {
        this.setState({
            activePage: index,
            activeElement: null
        })
    };

    addPage = () => {
        const newPage = JSON.parse(JSON.stringify(PageJson));
        const lastPage = this.state.pages.length - 2;
        const newIndex = this.state.pages[lastPage].index + 1;
        const newData = this.state.pages;
        newPage.name = 'Page ' + newIndex;
        newPage.index = newIndex;
        newPage.step.title = "Step " + newIndex
        newData.splice(this.state.pages.length - 1, 0, newPage);
        this.setState({
            pages: newData,
            activePage: newIndex
        });
    };

    deletePage = (index) => {
        let newData = this.state.pages;
        const activePage = newData.find((item, i) => item.index == index);

        if (activePage.type == "thankyou") {
            message.error("Thank you page can not be deleted!");
            return false;
        }

        if (newData.length > 2) {
            newData = newData.filter((item, i) => item.index != index);
            const lastPageIndex = newData.length - 1
           
            this.setState({
                pages: newData,
                activePage: lastPageIndex
            })
            this.updatePageOrder(newData, lastPageIndex)
        } else {
            message.error(this.state.lang["Last page can not be removed"]);
        }
    };

    getNewIndex = () => {
        const indexes = getNewIndexHelper(this.state.pages, "page", []);
        return indexes.length > 0 ? indexes.reduce((a, b) => Math.max(a, b)) + 1 : 1;
    };

    addElement = (type, control) => {
        if(this.state.licenseCheck == false){
            if(control){
                this.setState({purcahaseErrorModal:true})
                this.setState({purchaseModalTitle:type})
                return false;
            }
        }
          
        const newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);
        const newIndex = this.getNewIndex();
        const data = JSON.parse(JSON.stringify(Types[type]));
        data.id = type + "_" + this.state.formId + "_" + newIndex;
        data.index = newIndex;
        currentPage.elements.push(data);
        this.setState({
            pages: newData,
        });
    };

    deleteElement = (id) => {
        let newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);
        findAndDelete(currentPage, "page", id);
        this.setState({
            pages: newData,
            activeElement: null
        })
    };

    selectElement = (id, e) => {
        this.props.history.push("/");
        const el = e.target.closest(".mf-formElementContainer");
        if (el) {
            const clickedId = el.getAttribute("data-id");
            this.setState({
                activeElement: clickedId
            });

        }
    };

    updateElement = (id, element) => {
        const newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);
        updateElementHelper(currentPage, "page", id, element);
        this.setState({
            pages: newData
        })
    };

    duplicateElement = (id) => {
        const newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);

        duplicateElementHelper(currentPage, "page", id, this.state.formId, this.getNewIndex);

        this.setState({
            pages: newData
        })
    };

    duplicatePage = (index) => {
        const newData = this.state.pages
        const duplicatePage = JSON.parse(JSON.stringify(newData.find((item) => item.index === index)));
        const lastElement = {
            id: this.getNewIndex() - 1
        }
        duplicatePageHelper(duplicatePage, 'page', lastElement, this.state.formId)
        const newIndex = this.state.pages[this.state.pages.length - 2].index + 1;
        const newPage = duplicatePage;
        newPage.name += " Copy";
        newPage.index = newIndex;
        newData.splice(this.state.pages.length - 1, 0, newPage);
        this.setState({
            pages: newData,
            activePage: newIndex
        });
    }

    pageNameChange = (name, e) => {
        if (e.keyCode == 13)
            e.preventDefault()

        const newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);
        currentPage.name = name;
        this.setState({
            pages: newData
        })
    }

    updatePageSettings = (name, value) => {
        const newData = this.state.pages;
        const currentPage = newData.find((item, index) => item.index === this.state.activePage);
        currentPage.settings[name] = value;
        this.setState({
            pages: newData
        });
    }

    updatePageStep = (name, value, index) => {
        const newData = this.state.pages;
        const currentPage = newData.find((item, i) => item.index == index)
        currentPage.step[name] = value
        this.setState({ pages: newData })
    }

    changeFontFamily = (font) => {
        WebFont.load({
            google: {
                families: font.split(",")
            }
        })
    }

    updateFormSettings = (name, value) => {
        const newData = this.state.formSettings;

        // Tema deiikliklerinde baz deerler temann ayarlarna dnyor.
        if (name == "fontFamily") {
            if (value != 'Arial')
                this.changeFontFamily(value)
        }

        if (name == "theme") {
            if (newData.theme.name != value.name) {
                if (value.fontFamily != 'Arial')
                    this.changeFontFamily(value.fontFamily);
                newData.fontFamily = value.fontFamily;
                newData.bgColor = value.bgColor;
                newData.labelDisplay = value.labelDisplay;
                // Tema deiince color schemasn da default olarak ayarlyoruz.
                newData.colorScheme = { name: 'default', label: 'Default' }
            }
        }

        // Color Schemas deitiinde tema zerindeki etki ettii yerleri de deitirmemiz gerekiyor.
        if (name == "colorScheme") {
            const scheme = value.name;
            const primaryColor = value.primaryColor;
            if (newData.theme.name == "dribbble") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.dribbble.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.dribbble.submitButton.borderColor : primaryColor;
            }
            if (newData.theme.name == "material" || newData.theme.name == "material-outlined") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.material.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.material.submitButton.borderColor : primaryColor;
                newData.theme.submitButton.backgroundColorHover = scheme == "default" ? Themes.material.submitButton.backgroundColorHover : getColorLuminance(primaryColor, -0.15);
                newData.theme.input.borderColorFocus = scheme == "default" ? Themes.material.input.borderColorFocus : primaryColor;
                newData.theme.button.backgroundColorHover = scheme == "default" ? Themes.material.button.backgroundColorHover : convertRgba(primaryColor, 0.08, "object");
                newData.theme.button.color = scheme == "default" ? Themes.material.button.color : primaryColor;
            }
            if (newData.theme.name == "atlassian") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.atlassian.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.atlassian.submitButton.borderColor : primaryColor;
                newData.theme.submitButton.backgroundColorHover = scheme == "default" ? Themes.atlassian.submitButton.backgroundColorHover : getColorLuminance(primaryColor, -0.15);
                newData.theme.input.borderColorFocus = scheme == "default" ? Themes.atlassian.input.borderColorFocus : primaryColor;
            }
            if (newData.theme.name == "default") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.default.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.default.submitButton.borderColor : primaryColor;
                newData.theme.input.boxShadowFocus = scheme == "default" ? Themes.default.input.boxShadowFocus : `0 0 0 0.2rem ${convertRgba(primaryColor, 0.25)} `;
                newData.theme.input.borderColorFocus = scheme == "default" ? Themes.default.input.borderColorFocus : convertRgba(primaryColor, 0.6, "object");
            }
            if (newData.theme.name == "hubspot") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.hubspot.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.hubspot.submitButton.borderColor : primaryColor;
                newData.theme.input.boxShadowFocus = scheme == "default" ? Themes.hubspot.input.boxShadowFocus : `0 0 4px 1px ${convertRgba(primaryColor, 0.5)}, 0 0 0 1px ${convertRgba(primaryColor)} `;
                newData.theme.input.borderColorFocus = scheme == "default" ? Themes.hubspot.input.borderColorFocus : convertRgba(primaryColor, 0.5, "object");
            }
            if (newData.theme.name == "gitbook") {
                newData.theme.submitButton.backgroundColor = scheme == "default" ? Themes.gitbook.submitButton.backgroundColor : primaryColor;
                newData.theme.submitButton.borderColor = scheme == "default" ? Themes.gitbook.submitButton.borderColor : primaryColor;
                newData.theme.submitButton.backgroundColorHover = scheme == "default" ? Themes.gitbook.submitButton.backgroundColorHover : getColorLuminance(primaryColor, -0.15);
                newData.theme.submitButton.borderColorHover = scheme == "default" ? Themes.gitbook.submitButton.borderColorHover : primaryColor;
                newData.theme.input.borderColorFocus = scheme == "default" ? Themes.gitbook.input.borderColorFocus : primaryColor;
                newData.theme.button.backgroundColorHover = scheme == "default" ? Themes.gitbook.button.backgroundColorHover : convertRgba(primaryColor, 0.08, "object");
                newData.theme.button.color = scheme == "default" ? Themes.gitbook.button.color : primaryColor;
            }
        }

        newData[name] = value;
        this.setState({
            formSettings: newData
        });
    }
    updateActions = (actions) => {
        this.setState({
            actions
        }, () => {
            this.saveForm();
        });
    }

    updateOrder = (elements, type, id = null, index = null) => {
        const newData = this.state.pages;
        const currentPage = newData.find((item, i) => item.index === this.state.activePage);
        updateOrderHelper(currentPage, 'page', elements, id, index);

        this.setState({
            pages: newData
        });
    }

    updatePageOrder = (orderPages, activePage) => {
        const thankyouIndex = orderPages.findIndex(item => item.type == "thankyou")
       
        
        if(thankyouIndex != orderPages.length-1)
            return;
        
        orderPages.map((item,index) => {
            if(item.type !== "thankyou")
                item.index = index+1;
        })
       
       const newActivePage = orderPages.find(item => item.index == activePage)
       this.setState({pages:orderPages, activePage:newActivePage.index})
    }

    themeaLicenseControl = (type) => {
        
        if(this.state.licenseCheck || type == 0)
            return true;
        
        const theme = this.state.formSettings.theme

        if(theme.name != "default" && theme.name != "material" && theme.name != "material-outlined" )
        {
            this.setState({purcahaseErrorModal:true})
            this.setState({purchaseModalTitle:theme.themeLabel+" " +this.state.lang['theme is pro feature']})
            return false
        }

        if(theme.customized)
        {
            this.setState({purcahaseErrorModal:true})
            this.setState({purchaseModalTitle:this.state.lang['Theme customize is pro feature']})
            return false
        }
        
        return true;
    }

    saveForm = (type) => {
        if (this.state.actions.length == 0) {
            message.warning('You have to add at least one Action!');
            return;
        }
        const checkSubmit = {
            check: false
        }
        controlSubmitButton(this.state.pages, 'page', checkSubmit)

        if (!checkSubmit.check) {
            message.warning("You have to add a submit button to the form!")
            this.setState({ preview: false })
            return;
        }
        this.setState({ preview: true })

        if(!this.themeaLicenseControl(type))
            return false;

        this.setState({
            formSaving: true,
            loadPreview: false
        })
       
        const data = {
            settings: this.state.formSettings,
            pages: this.state.pages,
            actions: this.state.actions
        }
        const formData = encodeURIComponent(JSON.stringify(data));
        axios.post(this.props.baseUrl + "admin-ajax.php", `action=magicform_update_form&type=${type}&id=${this.state.formId}&data=${formData}`, {

        })
            .then(response => {
                if (type == 1)
                    message.success(response.data.data.formName + " has been successfully saved.");

                this.setState({
                    formSaving: false,
                    loadPreview: true
                })
            })
            .catch(error => {
                this.setState({
                    formSaving: false
                });
                message.error("An error occurd. Please try again.");
            });
    }

    /**
     * Boa tklarsa active eleman boaltyoruz.
     */
    containerClick = (e) => {
        const el = e.target.closest(".mf-formElementContainer");
        const pageTitle = e.target.closest(".mf-page-title");
        const multiStepHeader = e.target.closest(".mf-multistep-header");
        if (!el) {
            this.setState({
                activeElement: null
            });
        }
        if (!el && !pageTitle && !multiStepHeader) {
            this.props.history.push("/");
        }
    }

    /**
       * Btn elemanlar hiyerarik bir ekilde getirir.
       */
    getAllElements = () => {
        return getAllElementsHelper(this.state.pages, "page", []);
    }


    handleKeyDown = (event) => {
        let charCode = String.fromCharCode(event.which).toLowerCase();
        if (event.ctrlKey && charCode === 's') {
            event.preventDefault();
            this.saveForm();
        }
    }

    render() {
        
        const { formSettings, loading,  pages, activePage } = this.state;
       
        if (loading) {
            return <div className="mf-spinContainer"><Spin tip="Form Loading..." /></div>;
        }

        const currentPage = pages.find(item => item.index == activePage);
        const elements = currentPage ? currentPage.elements : [];
        const allElements = this.getAllElements();
       
        return (
                <Language.Provider  value={this.state.lang} >
                <PurchaseCode.Provider value={this.state.licenseCheck}>
                {
                    this.state.purcahaseErrorModal &&
                    <Modal
                        visible={this.state.purcahaseErrorModal}
                        title={this.state.purchaseModalTitle}
                        onCancel={()=> this.setState({purcahaseErrorModal:false})}
                        onOk={()=> this.setState({purcahaseErrorModal:false})}
                    >
                        <PurchaseError/>
                    </Modal>
                }
            <div className="mf-mainContainer" tabIndex="0" onKeyDown={this.handleKeyDown} >
                <div className="mf-contentContainer">
                    <AppHeader formName={formSettings.name} preview={this.preview} formSaving={this.state.formSaving} saveForm={this.saveForm} />
                    <ThemeStyles settings={formSettings} />
                    <Switch>
                        <Route path="/settings"
                            render={(props) =>
                                <FormSettings
                                    pages={this.state.pages}
                                    allElements={allElements}
                                    settings={formSettings}
                                    emailSettings={formSettings.emailSettings}
                                    updateFormSettings={this.updateFormSettings} />
                            } />

                        <Route path="/actions"
                            render={(props) =>
                                <Actions
                                    allElements={allElements}
                                    actions={this.state.actions}
                                    updateActions={this.updateActions}
                                    formSettings={formSettings}
                                    baseUrl={this.props.baseUrl}
                                    emailSettings={this.state.pluginSettings.emailSettings}
                                    payments={this.state.pluginSettings.payments}
                                />
                            } />

                        <Route path="/publish"
                            render={(props) =>
                                <Publish formId={this.state.formId} />
                            } />
                        {
                            this.state.preview &&
                            <Route path="/preview"
                                render={
                                    (props) =>
                                        <Preview formId={this.state.formId} loadPreview={this.state.loadPreview} />
                                }
                            />
                        }


                        <Route path="/"
                            render={(props) =>
                                <>
                                    <PageList advancedMode={formSettings.advancedMode} updateFormSettings={this.updateFormSettings} pages={this.state.pages} addPage={this.addPage} deletePage={this.deletePage}
                                       updatePageOrder={this.updatePageOrder} activePage={this.state.activePage} duplicatePage={this.duplicatePage} changePage={this.changePage} />

                                    <div className="mf-formContentContainer" style={{background:  currentPage.type === "page" ? convertRgba(formSettings.bgColor) : null}}>
                                        <div onClick={this.containerClick} className="mf-formElementsContainer">
                                            <PerfectScrollbar>
                                                <div className={`magicform magicform-${formSettings.theme.name} mf-formElementsContainerInner`}>
                                                    {(this.state.pages.length > 0) ?
                                                        currentPage.type == "page" ?
                                                            <>
                                                                <MultiStepHeader formSettings={formSettings} updatePageStep={this.updatePageStep} updatePageSettings={this.updatePageSettings} currentPage={currentPage} pages={this.state.pages} />
                                                                <Page
                                                                    elements={elements}
                                                                    currentPage={currentPage}
                                                                    updatePageSettings={this.updatePageSettings}
                                                                    pageNameChange={this.pageNameChange}
                                                                    deleteElement={this.deleteElement}
                                                                    selectElement={this.selectElement}
                                                                    updateElement={this.updateElement}
                                                                    duplicateElement={this.duplicateElement}
                                                                    activeElement={this.state.activeElement}
                                                                    addElement={this.addElement}
                                                                    formSettings={formSettings}
                                                                    pluginSettings={this.state.pluginSettings}
                                                                    updateOrder={this.updateOrder}
                                                                    addPage={this.addPage}
                                                                    baseUrl={this.props.baseUrl}
                                                                />
                                                            </> : <ThankYou allElements={allElements} currentPage={currentPage} updatePageSettings={this.updatePageSettings} />
                                                        : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description="No page in this form" />
                                                    }
                                                </div>
                                            </PerfectScrollbar>
                                        </div>

                                        {currentPage.type == "page" &&
                                            <div className="mf-settingsSection" style={{ flex: "0 0 400px" }}>
                                                <PerfectScrollbar>
                                                    <Switch>
                                                        <Route path="/" exact render={() =>
                                                            this.state.activeElement ?
                                                                <>
                                                                    <a className="mf-settingSectionClose" onClick={this.containerClick}>&times;</a>
                                                                    <ElementSettings
                                                                        baseUrl ={this.props.baseUrl}
                                                                        advancedMode={formSettings.advancedMode}
                                                                        updateElement={this.updateElement}
                                                                        pluginSettings={this.state.pluginSettings}
                                                                        pages={this.state.pages}
                                                                        activePage={this.state.activePage}
                                                                        activeElement={this.state.activeElement}
                                                                        allElements={allElements}
                                                                    />
                                                                </>
                                                                :
                                                                <FormStyles pages={this.state.pages} id={this.state.formId} settings={formSettings} updateFormSettings={this.updateFormSettings} />
                                                        }
                                                        />

                                                        <Route path="/page-settings"
                                                            render={() =>
                                                                <PageSettings
                                                                    pageNameChange={this.pageNameChange}
                                                                    name={currentPage.name}
                                                                    settings={currentPage.settings}
                                                                    updatePageSettings={this.updatePageSettings} />
                                                            } />
                                                        <Route path="/multi-step"
                                                            render={() =>
                                                                <MultiStep settings={formSettings} pages={this.state.pages} updatePageStep={this.updatePageStep} updateFormSettings={this.updateFormSettings} />
                                                            } />
                                                    </Switch>
                                                </PerfectScrollbar>
                                            </div>
                                        }
                                    </div>
                                </>
                            } />

                    </Switch>
                </div>
            </div >
            </PurchaseCode.Provider> 
            </Language.Provider>
        );
    }

}

export default withRouter(App);
