import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import SignatureCanvas from 'react-signature-canvas'
import { convertRgba } from "../../helpers";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Text = (props) => {
  const { item , originalItem } = props;
  const {
    heightValue,
    widthValue,
    backgroundColor,
    borderColor,
    penColor,
  } = item.payload;

  let mySignature = {}

  const handleChange = (value,name) => {
   const newItem = originalItem
    newItem.payload[name] = value
    props.updateElement(item.id, newItem)
  }

  const clear = () => {
    mySignature.clear()
  }

  return (
    <InputLayout payload={props.item.payload} handleChange={handleChange}>
      <InputDescription position="top" {...props.item.payload} />
      <div style={{ display: "inline-block", border: `1px solid ${convertRgba(borderColor)}`, background: convertRgba(backgroundColor) }}>
        <SignatureCanvas penColor={convertRgba(penColor)} ref={(ref) => { mySignature = ref }}
          canvasProps={{ width: widthValue, height: heightValue }} />
      </div>
      <FontAwesomeIcon onClick={() => clear()} icon={['fas', 'eraser']} />
      <InputDescription position="bottom" {...props.item.payload} />
    </InputLayout>
  );
};

export default Text;
