import React from "react";
import Countries from "../sub-elements/Countries"
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import InputIcon from "../../../../components/InputIcon.js";
import Config from "../../../../config";
import Language from "../../Language";

const CountyList = (props) => {
    const lang = React.useContext(Language);

    const { item , originalItem } = props;
    const { countryGetJson, disabled, countryList, inputIcon, pleaseSelect, labelText, cssClasses, fieldSize, fieldWidth } = item.payload;

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} mf-active `}  >
                <InputIcon icon={inputIcon}>
                    {
                        countryGetJson ?
                            (
                                <select
                                    className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
                                    disabled={disabled}
                                    id={item.id}
                                >
                                    {
                                        pleaseSelect != "" && 
                                        <option key={0} >{pleaseSelect}</option>
                                    }
                                    {
                                        countryList.map((item, index) =>
                                            <option value={item.value} key={index}> {item.name} </option>
                                        )
                                    }
                                </select>
                            ) : (
                                <Countries pleaseSelect={pleaseSelect} fieldSize={Config.size[fieldSize]} fieldWidth={Config.width[fieldWidth]} disabled={disabled} />
                            )
                    }
                </InputIcon>
                {item.theme.startsWith("material") && labelText != "" && <span className="mf-material-label">{labelText}</span>}
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout >
    );
}

export default CountyList;