import React, { useState } from "react";
import DatePicker, {registerLocale} from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import InputIcon from "../../../../components/InputIcon";
import Config from "../../../../config";
import  * as Local from 'date-fns/esm/locale'

const CustomDatePicker = (props) => {
  const { item , originalItem, language } = props;
  const {
    isClearable,
    showTime,
    timeInterval,
    inputIcon,
    datePlaceholder,
    labelText,
    minDate,
    maxDate,
    defaultValue,
    dateFormat,
    cssClasses,
    fieldSize,
    fieldWidth
  } = item.payload;
  const lang = language.toLowerCase()
  registerLocale(lang, Local[lang])
  const [date, setDate] = useState(new Date());

  const handleChange = (value,name) => {
   const newItem = originalItem
    newItem.payload[name] = value
    props.updateElement(item.id, newItem)
  }
  return (
   <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
      <InputDescription position="top" {...props.item.payload} />
      <div
        className={`${
          item.theme.startsWith("material") ? `mf-material-container` : ``
        } mf-active  ${inputIcon ? `mf-with-icon` : ``} `}
      >
        <InputIcon icon={inputIcon}>
          <DatePicker
            id={item.id}
            placeholderText={datePlaceholder}
            minDate={new Date(minDate)}
            maxDate={new Date(maxDate)}
            className={`mf-form-control ${cssClasses.join(" ")} ${
                Config.size[fieldSize]
              } ${Config.width[fieldWidth]}`}
            selected={date}
            locale={lang}
            isClearable={isClearable}
            defaultValue={defaultValue}
            onChange={(date) => setDate(date)}
            dateFormat={showTime ? "yyyy/MM/dd HH:mm" : dateFormat}
            showTimeSelect={showTime}
            timeFormat="HH:mm"
            timeIntervals={timeInterval}
            timeCaption="time"
          />
          {item.theme.startsWith("material") && labelText != "" && (
            <span className="mf-material-label">{labelText}</span>
          )}
        </InputIcon>
      </div>
      <InputDescription position="bottom" {...props.item.payload} />
    </InputLayout>
  );
};

export default CustomDatePicker;
