import React from "react";
import { Input } from "antd";
import Language from '../Language';

const ScaleConfigure = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['First Label']}</span>
            <Input value={props.worstLabel}
                onChange={(e) => props.handleChange("worstLabel", e.target.value)}
            />
        </div>
        <div className="mf-part-item">
        <span className="mf-part-title">{lang['Last Label']}</span>
        <Input value={props.bestLabel}
                onChange={(e) => props.handleChange("bestLabel", e.target.value)}
            />
        </div>
        </>
    )
}
export default ScaleConfigure;