import React, { useState } from "react";
import Countries from "../sub-elements/Countries";
import Config from "../../../../config";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import EditableInput from "../../../../components/EditableInput";
import Language from "../../Language";

const EditableSpan = EditableInput("span");


const AddressForm = (props) => {

    const lang = React.useContext(Language);

    const { item, originalItem } = props;
    const { address1Check, address2Check, cityCheck, disabled, pleaseSelect, readonly, labelDisplay, zipPlaceholder, zipLabel, stateCheck, stateLabel, statePlaceholder, cityPlaceholder, countryCheck, zipCheck, address1Label, address2Label, cityLabel, countryLabel, address1Placeholder, address2Placeholder, labelPosition, cssClasses, fieldSize } = item.payload;

    const [address1Value, setAddress1Value] = useState("");
    const [address2Value, setAddress2Value] = useState("");
    const [cityValue, setCityValue] = useState("");
    const [stateValue, setStateValue] = useState("");
    const [zipValue, setZipValue] = useState("");

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            {
                address1Check &&
                <div className="mf-form-group">
                    <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${address1Value != "" ? `mf-active` : ``} `}  >
                        {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["Address Label"]} value={address1Label} onChange={(val)=> handleChange(val,'address1Label')}/>
                        </label>}
                        <input type="text" disabled={disabled} readOnly={readonly} value={address1Value} onChange={(e) => setAddress1Value(e.target.value)} className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}`} id="inputAddress1" placeholder={address1Placeholder} />
                        {item.theme.startsWith("material") && address1Label != "" && <span className="mf-material-label">{address1Label}</span>}
                    </div>
                </div>
            }
            {
                address2Check &&
                <div className="mf-form-group">
                    <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${address2Value != "" ? `mf-active` : ``} `}  >
                        {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["Address Label"]} value={address2Label} onChange={(val)=> handleChange(val,'address2Label')}/>
                        </label>}
                        <input type="text" disabled={disabled} readOnly={readonly} value={address2Value} onChange={(e) => setAddress2Value(e.target.value)} className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}`} id="inputAddress2" placeholder={address2Placeholder} />
                        {item.theme.startsWith("material") && address2Label != "" && <span className="mf-material-label">{address2Label}</span>}
                    </div>
                </div>
            }
            <div className="mf-row">
                {
                    cityCheck &&
                    <div className="mf-form-group mf-col">
                        <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${cityValue != "" ? `mf-active` : ``} `}  >
                            {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["City Label"]} value={cityLabel} onChange={(val)=> handleChange(val,'cityLabel')}/>
                        </label>}
                            <input type="text" disabled={disabled} readOnly={readonly} value={cityValue} onChange={(e) => setCityValue(e.target.value)} className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}`} id="inputAddress2" placeholder={cityPlaceholder} />
                            {item.theme.startsWith("material") && cityLabel != "" && <span className="mf-material-label">{cityLabel}</span>}
                        </div>
                    </div>
                }
               
                {
                    stateCheck &&
                    <div className="mf-form-group mf-col">
                        <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${stateValue != "" ? `mf-active` : ``} `}  >
                            {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["c"]} value={stateLabel} onChange={(val)=> handleChange(val,'stateLabel')}/>
                        </label>}
                            <input type="text" disabled={disabled} readOnly={readonly} value={stateValue} onChange={(e) => setStateValue(e.target.value)} className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}`} id="inputAddress2" placeholder={statePlaceholder} />
                            {item.theme.startsWith("material") && stateLabel != "" && <span className="mf-material-label">{stateLabel}</span>}
                        </div>
                    </div>
                }
                {
                    zipCheck &&
                    <div className="mf-form-group mf-col">
                        <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${zipValue != "" ? `mf-active` : ``} `}  >
                            {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["Zip Label"]} value={zipLabel} onChange={(val)=> handleChange(val,'zipLabel')}/>
                        </label>}
                            <input type="text" value={zipValue} readOnly={readonly} disabled={disabled} onChange={(e) => setZipValue(e.target.value)} className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]}`} id="inputAddress2" placeholder={zipPlaceholder} />
                            {item.theme.startsWith("material") && zipLabel != "" && <span className="mf-material-label">{zipLabel}</span>}
                        </div>
                    </div>
                }
                 {
                    countryCheck &&
                    <div className="mf-form-group mf-col">
                        <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} mf-active `}  >
                            {!item.theme.startsWith("material") && labelDisplay == "show" && <label className={`mf-label mf-label-${labelPosition}`}>
                            <EditableSpan placeholder={lang["Country Label"]} value={countryLabel} onChange={(val)=> handleChange(val,'countryLabel')}/>
                        </label>}
                            <Countries pleaseSelect={pleaseSelect} fieldSize={Config.size[fieldSize]} disabled={disabled}  />
                            {item.theme.startsWith("material") && countryLabel != "" && <span className="mf-material-label">{countryLabel}</span>}
                        </div>
                    </div>
                }
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout>
    );
}

export default AddressForm;