import React from "react";
import { TreeSelect} from "antd";
import Language from '../Language';

const { TreeNode } = TreeSelect;

const ButtonTypes = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Button Type']}</span>
            <TreeSelect
                style={{ width: '100%' }}
                value={props.value}
                dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                placeholder={lang['Please select']}
                treeDefaultExpandAll
                onChange={(value) => props.handleChange("buttonType", value)}
            >
                <TreeNode value="default" title={lang['Theme Default']} key="0" />
                <TreeNode value="normal" title={lang['Normal']} key="0-1">
                <TreeNode value="primary" title={<><span style={{ display:'block', width:5, height:5, color: '#08c' }}></span>{lang['Primary']}</>} key="0-1-1" />
                    <TreeNode value="secondary" title={lang['Secondary']} key="0-1-2" />
                    <TreeNode value="success" title={lang['Success']} key="0-1-3" />
                    <TreeNode value="danger" title={lang['Danger']} key="0-1-4" />
                    <TreeNode value="warning" title={lang['Warning']} key="0-1-5" />
                    <TreeNode value="info" title={lang['Info']} key="0-1-6" />
                    <TreeNode value="light" title={lang['Light']} key="0-1-7" />
                    <TreeNode value="dark" title={lang['Dark']} key="0-1-8" />
                    <TreeNode value="link" title={lang['Link']} key="0-1-9" />
                </TreeNode>
                <TreeNode value="outline" title="Outline" key="0-2">
                    <TreeNode value="outline-primary" title={lang['Primary']} key="0-2-1" />
                    <TreeNode value="outline-secondary" title={lang['Secondary']} key="0-2-2" />
                    <TreeNode value="outline-success" title={lang['Success']} key="0-2-3" />
                    <TreeNode value="outline-danger" title={lang['Danger']} key="0-2-4" />
                    <TreeNode value="outline-warning" title={lang['Warning']} key="0-2-5" />
                    <TreeNode value="outline-info" title={lang['Info']} key="0-2-6" />
                    <TreeNode value="outline-light" title={lang['Light']} key="0-2-7" />
                    <TreeNode value="outline-dark" title={lang['Dark']} key="0-2-8" />
                </TreeNode>
            </TreeSelect>
        </div>
    )
}

export default ButtonTypes;