import React, {useState} from "react";
import {ChromePicker} from 'react-color';
import styled, {css} from "styled-components";
import {Menu, Dropdown, Icon} from 'antd';
import Language from '../pages/App/Language';

const ColorHolder = styled.div`
    padding: 5px;
    background: #fff;
    border-radius: 1px;
    box-shadow: 0 0 0 1px rgba(0,0,0,.1);
    display: inline-flex;
    cursor: pointer;
    align-items:center;
    &>i{
        color:#999;
    }
`;

const ColorInner = styled.div`
    width: 22px;
    height: 14px;
    border-radius: 2px;
    text-align:center;
    margin-right:6px;
    background: ${props => props.color ? 'rgba(' + props.color.r + ',' + props.color.g + ',' + props.color.b + ',' + props.color.a + ')' : ''}
`;


const ColorPickerContainer = styled.div`
    position: absolute;
    z-index: 2;
    ${props => props.position == "right" && css`
        right: 20px;
    `}
`;

const Cover = styled.div`
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
`;

const NotSet = styled.div`
    cursor:pointer;
    color: #1890ff;
`;

const ColorPicker = (props) => {

    const lang = React.useContext(Language);
    const [color, setColor] = useState(props.value);
    const [visible, setVisible] = useState(false);

    const handleChange = (color) => {
        setColor(color);
        props.onChange(color);
    }

    const menu = (
        <Menu>
            <Menu.Item>
                <a onClick={() => handleChange({r: 0, g: 0, b: 0, a: 0})}>
                    {lang["None"]}
                </a>
            </Menu.Item>
            <Menu.Item>
                <a onClick={() => setVisible(true)}>
                    {lang["Select Color"]}
                </a>
            </Menu.Item>
        </Menu>
    );


    return (
        <>
            {props.value == null ?
                <NotSet onClick={() => setVisible(true)}>None</NotSet> :
                <ColorHolder>
                    <ColorInner onClick={() => setVisible(true)}
                                color={props.value}>{!props.value && "none"}</ColorInner>
                    <Dropdown trigger={["click"]} overlay={menu}>
                        <Icon type="down"/>
                    </Dropdown>
                </ColorHolder>
            }
            {visible &&
            <>
                <Cover onClick={() => setVisible(false)}/>
                <ColorPickerContainer position={props.position || "left"} >
                    <ChromePicker
                        style={{zIndex:100}}
                        color={color == null ? undefined : color}
                        onChangeComplete={(color) => handleChange(color.rgb)}
                    />
                </ColorPickerContainer>
            </>
            }
        </>
    );
}

export default ColorPicker;