import React from "react";
import { Icon, Input, InputNumber, Select, Tabs } from "antd";
import ColorPicker from "../../../components/ColorPicker";
import SettingsPart from "../../../components/SettingsPart";
import ButtonStyles from "./ButtonStyles";
import Themes from "../../../templates/themes.json";
import Language from '../Language';
import PurchaseCode from "../PurchaseCode.js"

const Option = Select.Option;
const TabPane = Tabs.TabPane;

const CustomizeTheme = (props) => {
    const lang = React.useContext(Language);
    const licenseCheck = React.useContext(PurchaseCode)
    const theme = props.theme;
    const { input, label, button, submitButton, nextButton, backButton } = theme;

    const handleChange = (type, name, value) => {
        if(!licenseCheck){
            return false;
        }

        if (type == "theme") {
            theme[name] = value;
        } else {
            theme[type][name] = value;
        }
        theme.customized = true;
        props.handleChange("theme", theme);
    }
    
    const resetDefaults = ()=>{
        props.handleChange("theme", JSON.parse(JSON.stringify(Themes[theme.name])));
    }

    return (
        <div className={"customize-theme-container"+(licenseCheck == false?" blur":"")}>
            <h5><a onClick={() => props.setCustomizeShow(0)} style={{ display: "inline-block", marginRight: 10 }}><Icon type="left-circle"></Icon></a>{lang['Customize Theme']}</h5>
            <a href="javascript:void(0)" onClick={resetDefaults}>Reset to Defaults</a>
            <SettingsPart title={lang['COLORS']}>
                <table cellPadding={3} cellSpacing={0} style={{ width: "100%", fontSize: 12, }}>
                    <tr>
                        <td style={{ width: 120 }}>
                            <b>{lang['Primary Color']}:</b>
                        </td>
                        <td>
                            <ColorPicker hideNotSet value={theme.primaryColor} onChange={(val) => { handleChange("theme", "primaryColor", val) }} />
                        </td>
                    </tr>
                </table>
            </SettingsPart>
            <SettingsPart title={lang['INPUT STYLES']} collapsed>

                <table cellPadding={3} cellSpacing={0} style={{ width: "100%", fontSize: 12, }}>
                    <thead>
                        <tr>
                            <td style={{ width: 120 }}></td>
                            <td>{lang['Default']}</td>
                            <td>{lang['Hover']}</td>
                            <td>{lang['Focus']}</td>
                        </tr>
                    </thead>
                    <tr>
                        <td>
                            <b>{lang['Background Color']}:</b>
                        </td>
                        <td>
                            <ColorPicker value={input.backgroundColor} onChange={(val) => { handleChange("input", "backgroundColor", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.backgroundColorHover} onChange={(val) => { handleChange("input", "backgroundColorHover", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.backgroundColorFocus} onChange={(val) => { handleChange("input", "backgroundColorFocus", val) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Text Color']}:</b>
                        </td>
                        <td>
                            <ColorPicker value={input.color} onChange={(val) => { handleChange("input", "color", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.colorHover} onChange={(val) => { handleChange("input", "colorHover", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.colorFocus} onChange={(val) => { handleChange("input", "colorFocus", val) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Border Color']}:</b>
                        </td>
                        <td>
                            <ColorPicker value={input.borderColor} onChange={(val) => { handleChange("input", "borderColor", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.borderColorHover} onChange={(val) => { handleChange("input", "borderColorHover", val) }} />
                        </td>
                        <td>
                            <ColorPicker position="right" value={input.borderColorFocus} onChange={(val) => { handleChange("input", "borderColorFocus", val) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Border Width']}:</b>
                        </td>
                        <td>
                            <InputNumber style={{ width: "100%" }} value={input.borderWidth} onChange={(value) => { handleChange("input", "borderWidth", value) }} />
                        </td>
                        <td>
                            <InputNumber style={{ width: "100%" }} value={input.borderWidthHover} onChange={(value) => { handleChange("input", "borderWidthHover", value) }} />
                        </td>
                        <td>
                            <InputNumber style={{ width: "100%" }} value={input.borderWidthFocus} onChange={(value) => { handleChange("input", "borderWidthFocus", value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Box Shadow']}:</b>
                        </td>
                        <td>
                            <Input style={{ width: "100%" }} value={input.boxShadow} onChange={(e) => { handleChange("input", "boxShadow", e.target.value) }} />
                        </td>
                        <td>
                            <Input style={{ width: "100%" }} value={input.boxShadowHover} onChange={(e) => { handleChange("input", "boxShadowHover", e.target.value) }} />
                        </td>
                        <td>
                            <Input style={{ width: "100%" }} value={input.boxShadowFocus} onChange={(e) => { handleChange("input", "boxShadowFocus", e.target.value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Placeholder Color']}:</b>
                        </td>
                        <td colspan="3">
                            <ColorPicker value={input.placeholderColor} onChange={(val) => { handleChange("input", "placeholderColor", val) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Border Style']}:</b>
                        </td>
                        <td colspan="3">
                            <Select style={{ width: "120px" }} value={input.borderStyle} onChange={(val) => handleChange("input", "borderStyle", val)}>
                                <Option value="none">{lang['None']}</Option>
                                <Option value="solid">{lang['Solid']}</Option>
                                <Option value="dotted">{lang['Dotted']}</Option>
                                <Option value="dashed">{lang['Dashed']}</Option>
                            </Select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Font Size']}:</b>
                        </td>
                        <td colspan="3">
                            <InputNumber style={{ width: "120px" }} value={input.fontSize} onChange={(value) => { handleChange("input", "fontSize", value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Font Weight']}:</b>
                        </td>
                        <td colspan="3">
                            <Select style={{ width: "120px" }} value={input.fontWeight} onChange={(val) => handleChange("input", "fontWeight", val)}>
                                <Option value={300}>{lang['Light']}</Option>
                                <Option value={400}>{lang['Regular']}</Option>
                                <Option value={500}>{lang['Medium']}</Option>
                                <Option value={700}>{lang['Bold']}</Option>
                            </Select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Border Radius']}:</b>
                        </td>
                        <td colspan="3">
                            <InputNumber style={{ width: "120px" }} value={input.borderRadius} onChange={(value) => { handleChange("input", "borderRadius", value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Padding']}:</b>
                        </td>
                        <td colspan="3">
                            <Input style={{ width: "100%" }} value={input.padding} onChange={(e) => { handleChange("input", "padding", e.target.value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Margin']}:</b>
                        </td>
                        <td colspan="3">
                            <Input style={{ width: "100%" }} value={input.margin} onChange={(e) => { handleChange("input", "margin", e.target.value) }} />
                        </td>
                    </tr>
                </table>
            </SettingsPart>
            <SettingsPart title={lang['LABEL STYLES']} collapsed>
                <table cellPadding={3} cellSpacing={0} style={{ width: "100%", fontSize: 12, }}>
                    <tr>
                        <td style={{ width: 120 }}>
                            <b>{lang['Text Color']}:</b>
                        </td>
                        <td>
                            <ColorPicker value={label.color} onChange={(val) => { handleChange("label", "color", val) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Font Size']}:</b>
                        </td>
                        <td>
                            <InputNumber style={{ width: "120px" }} value={label.fontSize} onChange={(value) => { handleChange("label", "fontSize", value) }} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <b>{lang['Font Weight']}:</b>
                        </td>
                        <td>
                            <Select style={{ width: "120px" }} value={label.fontWeight} onChange={(val) => handleChange("label", "fontWeight", val)}>
                                <Option value={300}>{lang['Light']}</Option>
                                <Option value={400}>{lang['Regular']}</Option>
                                <Option value={500}>{lang['Medium']}</Option>
                                <Option value={700}>{lang['Bold']}</Option>
                            </Select>
                        </td>
                    </tr>
                </table>

            </SettingsPart>
            <SettingsPart title={lang['BUTTON STYLES']} collapsed>
                <Tabs style={{margin:"15px -20px"}} type="card" defaultActiveKey="1">
                    <TabPane tab={lang['Default']} key="1">
                      <ButtonStyles property="button" button={button} handleChange={handleChange}/>
                    </TabPane>
                    <TabPane tab={lang['Submit']} key="2">
                        <ButtonStyles property="submitButton" button={submitButton} handleChange={handleChange}/>
                    </TabPane>
                    <TabPane tab={lang['Next']} key="3">
                        <ButtonStyles property="nextButton" button={nextButton} handleChange={handleChange}/>
                    </TabPane>
                    <TabPane tab={lang['Back']} key="4">
                        <ButtonStyles property="backButton" button={backButton} handleChange={handleChange}/>
                    </TabPane>
                </Tabs>
            </SettingsPart>
        </div>
    )
}

export default CustomizeTheme;