import React from "react";
import { convertRgba } from "../../helpers";

const Heading = (props) => {
    const {item} = props;
    const {element, content, elementPosition,  color, backgroundColor} = item.payload
    return (
            <div className="mf-form-text">
                <div className={"mf-position-"+elementPosition}>
                    <div dangerouslySetInnerHTML={{__html:`<${element} id='${item.id}' style="background:${convertRgba(backgroundColor)}; color:${convertRgba(color)};">${content}</${element} >`}} />
                </div>
            </div>
    );
}

export default Heading;