import React from "react";
import { Tabs } from "antd";
import FieldWidth from "../../settings/FieldWidth";
import CssClasses from "../../settings/CssClasses";
import ButtonTypes from "../../settings/ButtonTypes";
import ButtonName from "../../settings/ButtonName";
import ButtonPosition from "../../settings/ButtonPosition";
import IconSelect from "../../settings/IconSelect"
import ButtonOnClick from "../../settings/ButtonOnClick"
import ButtonSize from "../../settings/ButtonSize"
import ButtonWidth from "../../settings/ButtonWidth";
import Language from '../../Language';

const TabPane = Tabs.TabPane;

const ButtonSettings = ({ advancedMode, handleChange, buttonOnclick, eventType, buttonSize,buttonWidth, buttonType, buttonHref, forwardType, buttonName, buttonPosition, fieldWidth, cssClasses, buttonIcon }) => {
    const lang = React.useContext(Language);

    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                <ButtonTypes value={buttonType} handleChange={handleChange} />
                <ButtonName value={buttonName} handleChange={handleChange} />
                <ButtonPosition value={buttonPosition} handleChange={handleChange} />
                <ButtonSize value={buttonSize} handleChange={handleChange} />
                <ButtonWidth value={buttonWidth} handleChange={handleChange} />
                <ButtonOnClick buttonHref={buttonHref} buttonOnclick={buttonOnclick} eventType={eventType} forwardType={forwardType} handleChange={handleChange} />
                {advancedMode && <IconSelect labelText="Button Icon" property="buttonIcon" icon={buttonIcon} handleChange={handleChange} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default ButtonSettings;