import React from "react";
import ReactHtmlParser from 'react-html-parser';

const Html = (props) => {
    const {item} = props;
    const { htmlContent} = item.payload;
    
    return (
        <div className="form-group">
            <div id={item.id}>
                    <div className="form-group">
                        {ReactHtmlParser(htmlContent)}
                    </div>
            </div>
        </div>
    );
}

export default Html;