import React, { useState } from "react";
import Language from '../Language';

import { Tag, Tooltip, Icon, Input, Form } from "antd";
const CssClasses = (props) => {
    const lang = React.useContext(Language);

    const styleReplace = (value) => {
        const regex = /^[A-Za-z_-]+([A-Za-z0-9-_]*)/g
        if (regex.test(value))
            return true;
        return false;
    }

    const [inputVisible, setinputVisible] = useState(false)
    const [inputValue, setinputValue] = useState("")

    const handleClose = (removeTag) => {
        const tags = props.value.filter(item => item !== removeTag)
        props.handleChange("cssClasses", tags);
    }

    const handleInputConfirm = () => {
        if (inputValue && props.value.indexOf(inputValue) === -1) {
            const tags = props.value
            tags.push(inputValue)
            props.handleChange("cssClasses",tags)
          }
          setinputVisible(false)
          setinputValue("")
    } 

    return (
        <div class="mf-part-item">
        <span className="mf-part-title">{lang['Additional Css Classes']}</span>
                <div>
                    {
                    props.value.map((tag, index) => {
                    const isLongTag = tag.length > 20;
                    const tagElem = (
                        <Tag 
                            key={tag} closable={true} 
                            color={styleReplace(tag)?('#ccc'):('red')} 
                            onClose={() => handleClose(tag)}
                        >
                            {isLongTag ? `${tag.slice(0, 20)}...` : tag}
                        </Tag>
                    );
                    return isLongTag ? (
                        <Tooltip title={tag} key={tag}>
                        {tagElem}
                        </Tooltip>
                    ) : (
                            tagElem
                        );
                })}
                {inputVisible && (
                    <Input
                        autoFocus
                        type="text"
                        size="small"
                        style={{ width: 78, height:22, minHeight:22 }}
                        value={inputValue}
                        onChange={(e) => setinputValue(e.target.value)}
                        onBlur={handleInputConfirm}
                        onPressEnter={handleInputConfirm}
                    />
                )}
                {!inputVisible && (
                    <Tag style={{ background: '#fff', borderStyle: 'dashed' }} onClick={() => setinputVisible(true)}>
                        <Icon type="plus" />  {lang['Add Class']}
                    </Tag>
                )}
            </div>
        </div>
    )
}

export default CssClasses;