import React, { useState } from "react";
import NewProduct from "../../settings/NewProduct";
import Label from "../../settings/Label";
import CssClasses from "../../settings/CssClasses";
import Required from "../../settings/Required";
import Disabled from "../../settings/Disabled";
import Description from "../../settings/Description";
import Id from "../../settings/Id";
import FetchProducts from "../../settings/FetchProducts";
import SettingsPart from "../../../../components/SettingsPart";
import { Tabs, Button, Card, Icon, Input } from "antd";
import Language from "../../Language";
import Currencies from "../../settings/Currencies"
const { Meta } = Card;
const TabPane = Tabs.TabPane;

const ProductSettings = ({ advancedMode, id, currencyPos, currencySymbol, labelText, currency, products, labelPosition, baseUrl, labelDisplay, labelIcon, readOnly, disabled, labelWidth, handleChange, description, fieldWidth, descriptionPosition, required, cssClasses, fieldSize }) => {
    const lang = React.useContext(Language);

    const [newProductShow, setNewProductShow] = useState(false);
    const [productIndex, setProductIndex] = useState(0);
    const [editable, setEditable] = useState(false);

    const handleDeleteProduct = (index) => {
       const newProducts = products.filter((item, i) => i!= index)
       handleChange("products",newProducts)
    }

    const handleEditProduct = (index) => {
        setProductIndex(index)
        setEditable(true);
        setNewProductShow(1);
    }

    const handleNewProduct = () => {
        setProductIndex(-1)
        setEditable(false);
        setNewProductShow(1);
    }

    return (
        <div style={{padding:20}}>   
       {
           !newProductShow?  
           <>
           <h5>{lang['Product List']}</h5>
            <Tabs type="card">
            <TabPane tab={lang["Products"]} key="0">
                <FetchProducts baseUrl={baseUrl} products={products} handleChange={handleChange}/>
                <Button onClick={()=>handleNewProduct()}>{lang['New Product']}</Button>
                <Currencies currencyPos={currencyPos} value={currency} handleChange={handleChange} />
                {
                    products.map( (item,index) => 
                    <Card
                        style={{ width: 300, marginTop:5}}
                    actions={[
                      <Icon type="edit" key="edit" onClick={()=> handleEditProduct(index)}/>,
                      <Icon type="close-circle" onClick={()=>handleDeleteProduct(index)}/>
                    ]}
                  >
                      <div style={{ display: "flex", flexDirection: "row"}}>
                        <img style={{margin:5,height:100, width:100, objectFit: "cover"}} src={item.views[0]}/>
                        <div style={{marginLeft:"5px"}}>
                            <label>{lang['Name']}:{item.name}</label>
                            <label>{lang['Description']}:{item.description}</label>
                            <label>{lang['Price']}:{currencySymbol+parseFloat(item.price).toFixed(2)}</label>
                        </div>
                      </div>
                  </Card>
                    )
                }
            </TabPane>
                <TabPane tab={lang["Settings"]} key="1">
                    <Label labelText={labelText}
                        advancedMode={advancedMode}
                        labelIcon={labelIcon}
                        labelPosition={labelPosition}
                        labelDisplay={labelDisplay}
                        labelWidth={labelWidth} handleChange={handleChange} />
                         <SettingsPart title={lang["INPUT"]}>
                            <div className="mf-part-item">
                                <Required value={required} handleChange={handleChange} />
                                {advancedMode && <Disabled disabled={disabled} handleChange={handleChange} />}
                            </div>
                        </SettingsPart>
                        <Description descriptionValue={description}
                            descriptionPosition={descriptionPosition}
                            handleChange={handleChange} />
                        {advancedMode && <Id value={id} />}
                    </TabPane>
                <TabPane tab={lang["Style"]} key="2">
                    <CssClasses value={cssClasses} handleChange={handleChange} />
                </TabPane>
            </Tabs>
            </>
        :   <NewProduct baseUrl={baseUrl} editable={editable} productIndex={productIndex} setNewProductShow={setNewProductShow} products={products} handleChange={handleChange}/>   
       } 
        </div>   
    )
}
export default ProductSettings;