import React from "react";
import {Row, Col} from "antd";
import InputLayout from "../../../../components/InputLayout"
import InputDescription from "../../../../components/InputDescription"
import imgSrc from "../../../../default-image.jpg"

const ThumbnailSelector = (props) => {

    const {item, originalItem} = props;
    const {options, optionsDisplay, disabled, cbLabelPos,autoSize, inputType, cbLabelDisplay, heightValue, widthValue} = item.payload;
    const size = autoSize?{height:"auto", widht:"auto"}:{height:heightValue, width:widthValue}
    const createOption = (option, index) => (
       
        <>
        <div  key={index} className="mf-thumbnail-container">
            <div className={"mf-thumbnail-inner " + cbLabelPos} id={item.id}>
                <input
                    type={inputType=="radio"?"radio":"checkbox"}
                    className="mf-css-checkbox"
                    name={item.id}
                    disabled={disabled}
                    checked={option.default}
                    value={option.value}
                    id={`${item.id}_${index}`}/>

                <label htmlFor={`${item.id}_${index}`} className="mf-thumbnail">
                    <img style={size} className="mf-thumbnail-img" src={option.image==""?imgSrc:option.image}  alt="image"/>
                </label>
               {
                   cbLabelDisplay &&
                   <div className="mf-thumbnail-label form-check form-check-inline">
                     <label className="form-check-label"
                            placeholder="Image Label"
                              htmlFor={`${item.id}_${option.value}`}>{option.name}
                    </label>
                </div>
               }
            </div>
        </div>
      </>
    );

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
             <InputDescription position="top" {...props.item.payload} />
            <Row>
                {
                    options.map((option, index) => optionsDisplay == "inline" ? createOption(option, index) :
                        <Col span={24 / optionsDisplay} key={index}>{createOption(option, index)}</Col>)
                }
            </Row>
            <InputDescription position="bottom" {...props.item.payload} />
         </InputLayout>
    );
}

export default ThumbnailSelector;

