import React ,{useState}from "react";
import {Form, Checkbox, Input} from "antd";
import Config from "../../../config"
import Language from '../Language';

const { Search } = Input;

const PhoneCode = (props) => {
    const lang = React.useContext(Language);

    const [phoneCodes, setPhoneCodes] = useState(Config.phoneCodes)
   const handleAllChecked = (event) => {
        props.handleChange("countryCodeGetJson",true);
        const phoneCodes = Config.phoneCodes
        phoneCodes.forEach(country => country.isChecked = event.target.checked) 
        if(event.target.checked) 
            props.handleChange("phoneCodes",phoneCodes);
        else
            props.handleChange("phoneCodes" ,[])
    }
    
    const handleCheckChieldElement = (event) => {
        props.handleChange("countryCodeGetJson",true);
        const phoneCodes = Config.phoneCodes
        phoneCodes.forEach(country => {
            if (country.alpha2code === event.target.value)
                country.isChecked = event.target.checked
        })
        let listOfCountry = phoneCodes.filter(item => item.isChecked === true)
        props.handleChange("phoneCodes",listOfCountry);   
    } 

    const handleSearch = (value) => {
        setPhoneCodes(Config.phoneCodes.filter(item => item.name.substr(0, value.length).toLowerCase() == value.toLowerCase()))
    }

      const CheckBox = props => {
        return (
            <li>
                <Checkbox key={props.id} onChange={(e)=>props.handleCheckChieldElement(e)}  checked={props.isChecked} value={props.alpha2code} /> 
                {props.name+" ("+ props.phoneCode     +")"}
            </li>
        )
    }

    return (
        <>
            <Form.Item> 
                <Checkbox type="checkbox" onChange={(e) => handleAllChecked(e)} /> {lang['Check']} / {lang['Uncheck All']}
            </Form.Item>
            <Search
                    placeholder={lang['Search Country']}
                    onChange={event => handleSearch(event.target.value)}
                    style={{ width: "100%",marginBottom:"5px" }}
            />
            <div style={{overflow:"auto", border:"1px solid #CCC", padding:10, height:"200px"}}> 
            { 
                phoneCodes.map((item,i) => {
                    return (
                        <CheckBox key={i} handleCheckChieldElement={handleCheckChieldElement}  {...item} />
                    )
                })
            }
            </div>
        </>
    )
}

export default PhoneCode;