import React ,{useState} from "react";
import {Form, Checkbox,Input} from "antd";
import Config from "../../../config"
import Language from '../Language';

const { Search } = Input;
const CountryConfigure = (props) => {
    const lang = React.useContext(Language);

   const [countries, setCountries] = useState(Config.countries)
   const handleAllChecked = (event) => {
        props.handleChange("countryGetJson",true);
        props.handleChange("allCountry", event.target.checked);
        const countries = Config.countries
        countries.forEach(country => country.isChecked = event.target.checked) 
        if(event.target.checked)
            props.handleChange("countryList",countries);
        else
            props.handleChange("countryList" ,[])
    }
    
    const handleCheckChieldElement = (event) => {
        props.handleChange("countryGetJson",true);
        const countries = Config.countries
        countries.forEach(country => {
            if (country.value === event.target.value)
                country.isChecked = event.target.checked
        })
        let listOfCountry = countries.filter(item => item.isChecked === true)
        props.handleChange("countryList",listOfCountry);   
    } 

    const handleSearch = (value) => {
        setCountries(Config.countries.filter(item => item.name.substr(0, value.length).toLowerCase() == value.toLowerCase()))
    }


      const CheckBox = props => {
        return (
            <li>
                <Checkbox key={props.id} onChange={props.handleCheckChieldElement}  checked={props.isChecked} value={props.value} /> {props.name}
            </li>
        )
    }

    return (
        <>
            <Form.Item> 
                <Checkbox type="checkbox" checked={props.allCountry} onChange={(e) => handleAllChecked(e)} /> {lang['Check']} / {lang['Uncheck All']}
            </Form.Item>
            <Search
                    placeholder={lang['Search Country']}
                    onChange={event => handleSearch(event.target.value)}
                    style={{ width: "100%",marginBottom:"5px" }}
            />
            <div style={{overflow:"auto", border:"1px solid #CCC", padding:10, height:"200px"}}> 
            { 
                countries.map((item,i) => {
                    return (
                        <CheckBox key={i} handleCheckChieldElement={handleCheckChieldElement}  {...item} />
                    )
                })
            }
            </div>
        </>
    )
}

export default CountryConfigure;