import React from "react";
import { Icon, Popconfirm, Tooltip, Dropdown, Menu } from "antd";
import { Link } from "react-router-dom";
import Language from './Language';
import { ReactSortable } from "react-sortablejs";
const PageList = ({ pages, activePage, deletePage, updatePageOrder, addPage, changePage, duplicatePage }) => {
    const lang = React.useContext(Language);

    let menu;
    return (
        <div className="mf-pageListContainer">
          
            <ul>
            <ReactSortable
                list={pages}
                swapThreshold={1}
                animation={150}
                setList={(newState) => updatePageOrder(newState, activePage)}
            >
                {
                    pages.length > 0 &&
                    pages.map((item, index) => {

                        menu = (
                            <Menu>
                                <Menu.Item key="0">
                        <Link to="/page-settings"><Icon type="setting" /> {lang['Page Settings']}</Link>
                                </Menu.Item>
                                <Menu.Divider />
                              
                                <Menu.Item>
                                    <Popconfirm
                                        title={lang['Are you sure duplicate this page']+"?"}
                                        onConfirm={() => duplicatePage(item.index)}
                                        okText={lang['Yes']}
                                        cancelText={lang['No']}
                                    >
                                        <a>
                                            <Icon type="copy" /> {lang['Duplicate Page']}
                                        </a>
                                    </Popconfirm>
                                </Menu.Item>
                                <Menu.Divider/>
                                <Menu.Item key="3">
                                    <Popconfirm
                                         title={lang['Are you sure duplicate this page']+"?"}
                                        onConfirm={() => deletePage(item.index)}
                                        okText={lang['Yes']}
                                        cancelText={lang['No']}
                                    >
                                        <a>
                                            <Icon type="close-circle" /> {lang['Delete Page']}
                                        </a>
                                    </Popconfirm>
                                </Menu.Item>
                            </Menu>
                        );
                        return (
                            <li key={item.index } className={item.index === activePage ? "active" : ""}>
                                <a className={item.type == "thankyou" ? "thankyou" : ""} onClick={(e) => changePage(item.index)}>
                                    <span className="mf-pageTitle">{item.name!="" ? item.name : "Page "+item.index}</span>

                                </a>
                                {item.index === activePage && item.type == "page" &&

                                    <Dropdown className="mf-pageSettingsIcon" overlay={menu}>
                                        <span className="ant-dropdown-link" href="#">
                                            <Icon type="ellipsis" />
                                        </span>
                                    </Dropdown>
                                }
                            </li>
                        )
                    }
                    )}   
                    </ReactSortable>
                <li><a onClick={addPage}><Tooltip title={lang['Add New Page']}><Icon
                    type="plus-circle" /></Tooltip></a></li>
                  
            </ul>
           
        </div>
    )
}

export default PageList;