import React, {useState}from "react";
import Language from '../Language';

import ReactQuill, {Quill} from 'react-quill';
import { Radio} from "antd";
import 'react-quill/dist/quill.snow.css';
const Block = Quill.import('blots/block');
Block.tagName = 'DIV';
Quill.register(Block, true);
const HtmlEditor = (props) => {

    const lang = React.useContext(Language);

    const modules = {
        toolbar: [
            ['blockquote'],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],                       // text direction          
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
            [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
            [{ 'align': [] }]
        ],
    };

    const [html, setHtml] = useState(false)

    const handleChange = (newValue, delta, source) => {
        if (source === 'user') {
            props.handleChange('htmlContent',newValue)
        }
    }
    return (
        <div className="mf-part-item">
            <Radio.Group onChange={(e)=>setHtml(e.target.value)} value={html}>
                <Radio value={true}>Html</Radio>
                <Radio value={false}>{lang['Content']}</Radio>
            </Radio.Group>
            {
                html?(
                    <textarea style={{marginTop:"5px", height:"220px",width:"100%", borderRadius:"0px"}} onChange={(e)=>props.handleChange("htmlContent", e.target.value)}>{props.content}</textarea>
                    ):(
                    <>
                    <ReactQuill 
                      value={props.content}
                      modules={modules}
                      theme="snow"
                      onChange={handleChange} />
                      </>
                )
            }
           
        </div>
    )
}

export default HtmlEditor;