import React from "react";
import {Select} from "antd";
import Language from '../Language';

const FontFamily = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Font Family']}</span>
            <div>
                <Select style={{width:"100%"}} value={props.value} onChange={val => props.handleChange("fontFamily", val)} >
                    <Select.Option value="inherit">{lang['Inherit']}</Select.Option>
                    <Select.Option value="-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Fira Sans,Droid Sans,Helvetica Neue,sans-serif">{lang['System Font']}</Select.Option>
                    <Select.Option value="Arial">Arial</Select.Option>
                    <Select.Option value="Century Gothic">Century Gothic</Select.Option>
                    <Select.Option value="Times New Roman">Times New Roman</Select.Option>
                    <Select.Option value="Patrick Hand">Patrick Hand</Select.Option>
                    <Select.Option value="Roboto">Roboto</Select.Option>
                    <Select.Option value="Roboto Condensed">Roboto Condensed</Select.Option>
                    <Select.Option value="Open Sans">Open Sans</Select.Option>
                    <Select.Option value="Raleway">Raleway</Select.Option>
                    <Select.Option value="Nunito">Nunito</Select.Option>
                </Select>
            </div>
        </div>
    )
}
export default FontFamily;

