import React from "react";
import { Tabs } from "antd";
import Currencies from "../../settings/Currencies"
import CalculateVariable from "../../settings/CalculateVariable"
import CssClasses from "../../settings/CssClasses"
import Language from "../../Language";
import Label from "../../settings/Label";

const TabPane = Tabs.TabPane;

const CalculateFiledSettings = ({advancedMode, handleChange, currency, currencyPos, customSymbol, calculateVariables, labelText, labelIcon, labelDisplay, labelWidth, labelPosition, decimalCount, symbolStart, symbolEnd, symbolGroup, symbolDecimal, cssClasses, allElements }) => {
    const lang = React.useContext(Language);

    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
            <Label labelText={labelText}
                        labelPosition={labelPosition}
                        labelIcon={labelIcon}
                        labelDisplay={labelDisplay}
                        advancedMode={advancedMode}
                        labelWidth={labelWidth} handleChange={handleChange} />
                <Currencies currencyPos={currencyPos} value={currency} handleChange={handleChange} />
                <CalculateVariable symbolStart={symbolStart} decimalCount={decimalCount} symbolEnd={symbolEnd} symbolGroup={symbolGroup}
                  symbolDecimal={symbolDecimal} customSymbol={customSymbol} value={calculateVariables} handleChange={handleChange} allElements={allElements} />
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default CalculateFiledSettings;