import React from "react";
import {Form, Input} from "antd";
import Language from '../App/Language';

const Translations = (props) => {

    const translate = props.settings.translate
    const {gdpr, gdprText} = props.settings
    const {requiredErr, confirmErr, nextButton, previousButton,extensionErr, formLimitMessage, fileSizeErr, maxFileSize, allowedFileTypes,gdprValidationMessage } = translate;
    const lang = React.useContext(Language);

    const handleChange = (name, value) => {
        const newData = translate
        newData[name] = value
        props.handleChange("translate", newData)
    }
    return (
        <>
            <h1>{lang['Translations']}  </h1>
            <Form.Item label={lang['Required Error']}> 
                <Input
                    value={requiredErr}
                    onChange={(e) => handleChange("requiredErr", e.target.value)}
                />  
            </Form.Item >
            <Form.Item label={lang['Confirm Error']}> 
            <Input
                    value={confirmErr}
                    onChange={(e) => handleChange("confirmErr", e.target.value)}
                />  
            </Form.Item >
            <Form.Item label={lang['Next Button']}> 
                <Input
                    value={nextButton}
                    onChange={(e) => handleChange("nextButton", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['Previous Button']}> 
                <Input
                    value={previousButton}
                    onChange={(e) => handleChange("previousButton", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['Max File Size']}> 
                <Input
                    value={maxFileSize}
                    onChange={(e) => handleChange("maxFileSize", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['Allowed File Types']}> 
                <Input
                    value={allowedFileTypes}
                    onChange={(e) => handleChange("allowedFileTypes", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['File Extension Error']}> 
                <Input
                    value={extensionErr}
                    onChange={(e) => handleChange("extensionErr", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['File size Error']}> 
                <Input
                    value={fileSizeErr}
                    onChange={(e) => handleChange("fileSizeErr", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['Form Limit Message']}> 
                <Input
                    value={formLimitMessage}
                    onChange={(e) => handleChange("formLimitMessage", e.target.value)}
                />  
            </Form.Item>
            <Form.Item label={lang['Gdpr validation message']}> 
                <Input
                    value={gdprValidationMessage}
                    onChange={(e) => handleChange("gdprValidationMessage", e.target.value)}
                />  
             </Form.Item>
            {
                gdpr &&
                <Form.Item colon={false} label={lang['GDPR compliance message']} extra={lang['This text will be used for GDPR compliance checkbox']}>
                    <Input value={gdprText} onChange={e => props.handleChange("gdprText", e.target.value)}></Input>
                    <div style={{ marginTop: 10 }}></div>
                    <label> {lang['Privacy policy is provided by WordPress']}. {lang['You can change it']}.</label><a href="options-privacy.php" > {lang['Privacy Policy']} </a> 
                </Form.Item>
            }
           
        </>
    )
}

export default Translations;