import React from "react";
import { Modal, Alert } from "antd";
import SaveDatabase from "./types/SaveDatabase";
import Autoresponder from "./types/Autoresponder";
import SendEmail from "./types/SendEmail";
import MailChimp from "./types/MailChimp";
import SendGrid from "./types/SendGrid";
import GetResponse from "./types/GetResponse";
import GeneratePdf from "./types/GeneratePdf";
import GoogleAnalytics from "./types/GoogleAnalytics";
import PurchaseCode from "../App/PurchaseCode";
import PurchaseError from "../../components/PurchaseError";
import FormToPost from "./types/FormToPost";
import Stripe from "./types/Stripe";
import Paypal from "./types/Paypal";
const ActionModal = (props) => {

    const { modalTitle, modalVisible, setModalVisible, action, setSelectedAction } = props;
    const licenseCheck = React.useContext(PurchaseCode);
   
    const renderActionType = (type) => {
        switch (type) {
            case "saveDatabase":
                return <SaveDatabase {...props} />
            case "sendEmail":
                return <SendEmail {...props} />
            case "autoResponder":
                return (    !licenseCheck ? (<PurchaseError />): ( <Autoresponder {...props} />  ) )
            case "mailChimp":
                return  (   !licenseCheck?<PurchaseError/>:  <MailChimp {...props} /> )
            case "sendgrid":
                return  (  !licenseCheck?<PurchaseError />:  <SendGrid {...props} /> )
            case "getresponse":
                return  (  !licenseCheck?<PurchaseError />:  <GetResponse {...props} /> )
            case "generatePdf":
                return  (  !licenseCheck?<PurchaseError />:  <GeneratePdf {...props} /> )
            case "formToPost":
                return  (  !licenseCheck?<PurchaseError />:  <FormToPost {...props} /> )
            case "googleAnalytics":
                return  (  !licenseCheck?<PurchaseError />:  <GoogleAnalytics {...props} /> )
            case "stripe":
                return  (  !licenseCheck?<PurchaseError />:  <Stripe {...props} /> )
            case "paypal":
                return  (  !licenseCheck?<PurchaseError />:  <Paypal {...props} /> )
        }
    }

    return (

        <Modal
            width={700}
            title={modalTitle}
            visible={modalVisible}
            footer={null}
            maskClosable={false}
            onCancel={() => { setModalVisible(false); setTimeout(() => setSelectedAction(null), 200) }}
        >
            {action && renderActionType(action.type)}
        </Modal>
    );

}

export default ActionModal;
