import React from "react";
import { ReactSortable } from "react-sortablejs";
import Element from "../index";
import Language from "../../Language";

const Group = (props) => {
    const lang = React.useContext(Language);
    const { item , originalItem } = props;
    const elements = item.payload.elements;
    const labelText = item.payload.labelText

    return (
        <div className="mf-formElement-group">
        <h6 className={props.collapsible ? "mf-group-collapsible" : "mf-group-collapsible active"}
                placeholder={props.item.payload.name}
                style={{fontSize: 14, background: "aliceblue" }}
            >{labelText}</h6>
            <div className={props.collapsible ? "magicForm-collapsible" : "magicForm-collapsible active"}>

                <ReactSortable
                    list={elements}
                    style={{ minHeight: 200 }}
                    animation={200}
                    delayOnTouchStart={true}
                    delay={2}
                    group="page-elements"
                    handle=".handle"
                    setList={newState => props.updateOrder(newState, "group", props.item.id)}
                >
                    {elements.map(item => <Element
                        key={item.id}
                        selectElement={props.selectElement}
                        deleteElement={props.deleteElement}
                        activeElement={props.activeElement}
                        duplicateElement={props.duplicateElement}
                        updateElement={props.updateElement}
                        item={item}
                        pageSettings={props.pageSettings}
                        formSettings={props.formSettings}
                        pluginSettings={props.pluginSettings}
                        updateOrder={props.updateOrder}
                    />
                    )}

                </ReactSortable>
                {elements && elements.length == 0 && <small className="text-muted">{lang["Please add some elements"]}</small>}
            </div>
        </div>
    );
}

export default Group;