import React from "react";
import { Select, Radio } from "antd";
import Language from '../Language';

const { Option } = Select;

const ButtonPosition = (props) => {

    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Button Position']}</span>
            <div>
                <Radio.Group
                    value={props.value}
                    buttonStyle="solid"
                    onChange={(e) => props.handleChange("buttonPosition", e.target.value)}>
                    <Radio.Button value="left">{lang['Left']}</Radio.Button>
                    <Radio.Button value="center">{lang['Center']}</Radio.Button>
                    <Radio.Button value="right">{lang['Right']}</Radio.Button>
                </Radio.Group>
            </div>
        </div>
    )
}

export default ButtonPosition;