import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import EditableInput from "./EditableInput";

const InputLayout = (props) => {
    const {labelDisplay, labelPosition, labelWidth, labelIcon, required} = props.payload
    const labelText= props.labelText?props.labelText:props.payload.labelText
    const labelName = props.labelName?props.labelName:"labelText"

    const EditableSpan = EditableInput("span");
    return (
        <div className="mf-form-group">
                {labelDisplay == "show" && !props.isMaterial && <label className={"mf-label mf-label-" + labelPosition}
                style={labelPosition === "inline" ? { width: `${labelWidth}px` } : {}}>
                {
                    labelIcon &&
                    <FontAwesomeIcon style={{ height: "20px", width: "20px", marginRight: "5px" }} icon={['fas', labelIcon]} />
                }
                <EditableSpan placeholder="Label" value={labelText} onChange={(val)=> props.handleChange(val,labelName)}/>
                {required ? <span style={{ color: "red" }}> *</span> : ""}
            </label>
            }
            <div style={labelPosition === "inline" ? { paddingLeft: `${labelWidth}px` } : {}}>
                {props.children}
            </div>
        </div>
    );
}

export default InputLayout;