import React from "react";
import {Row, Col} from "antd";

import SelectVariable from "./SelectVariable"

const SelectAddInput = (props) => { 

    const addVariable = (value, label) => {
        let newValue = props.value
        newValue += value!="all_form_data"?`{${label[0] + "|" + value}}`:`{${value}}`
        props.addValue(props.type, newValue)
    }

    return (
        <>
        <Row gutter={[8, 8]}>
            <Col span={21}>
                {props.children}
            </Col>
            <Col span={3}>
                <SelectVariable type={props.selectType} style={props.style} allElements={props.allElements} addVariable={addVariable} />
            </Col>
        </Row>
        </>
    )
}

export default SelectAddInput;