import React from "react";
import { Form, Checkbox } from "antd";
import Language from '../Language';


const PhoneCodeControl = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <Checkbox checked={props.value}
                onChange={(e) => props.handleChange("phoneCodeControl", e.target.checked)}>
                {lang['Control']}</Checkbox>
        </div>
    )
}

export default PhoneCodeControl;