import React from "react";
import { Radio, Form, Checkbox, Row, Col, Select } from "antd";
import ColorPicker from "../../../components/ColorPicker"
import IconSelect from "./IconSelect";
import Language from '../Language';

const Option = Select.Option;

const MultiStepHeader = (props) => {

    const lang = React.useContext(Language);

    const { type, buttonPlacement, nextButtonIcon, backButtonIcon, theme, visible } = props.values;
    const handleChange = (name, value) => {
        const newData = props.values
        newData[name] = value;
        props.handleChange("multiStep", newData)
    }

    return (
        <>
            <div class="mf-part-item">
                <div class="mf-part-title">{lang['Header Theme']}</div>
                <div>
                    <Select value={theme} style={{ width: "100%" }} onChange={(val) => handleChange("theme", val)}>
                        <Option value="1">{lang['Style']} 1</Option>
                        <Option value="2">{lang['Style']} 2</Option>
                        <Option value="3">{lang['Style']} 3</Option>
                        <Option value="4">{lang['Style']} 4</Option>
                        <Option value="5">{lang['Style']} 5</Option>
                    </Select>
                </div>
            </div>
            <div class="mf-part-item">
                <div class="mf-part-title">{lang['Show Multi-Step Header']}</div>
                <div>
                    <Checkbox
                        onChange={(e) => handleChange("visible", e.target.checked)}
                        checked={visible}
                    >
                        {lang['Show / Hide']}
                    </Checkbox>
                </div>
            </div>
            {
                visible &&
                <div class="mf-part-item">
                    <div class="mf-part-title">{lang['Header Type']}</div>
                    <div>
                        <Radio.Group onChange={(e) => handleChange("type", e.target.value)} value={type}>
                            <Radio value="number">{lang['Numbers']}</Radio>
                            <Radio value="icon">{lang['Icons']}</Radio>
                        </Radio.Group>
                    </div>
                </div>
            }
            <div class="mf-part-item">
                <div class="mf-part-title">{lang['Next / Preview Buttons Placement']}</div>
                <div>
                    <Radio.Group onChange={(e) => handleChange("buttonPlacement", e.target.value)} value={buttonPlacement}>
                        <Radio value="right">{lang['Both Right']}</Radio>
                        <Radio value="left">{lang['Both Left']}</Radio>
                        <Radio value="leftright">{lang['Left and Right']}</Radio>
                    </Radio.Group>
                </div>
            </div>
            <Row>
                <Col span="12">
                    <div class="mf-part-item">
                        <IconSelect property="backButtonIcon" labelText={lang['Back Button Icon']} icon={backButtonIcon} handleChange={handleChange} />
                    </div>
                </Col>
                <Col span="12">
                    <div class="mf-part-item">
                        <IconSelect property="nextButtonIcon" labelText={lang['Next Button Icon']} icon={nextButtonIcon} handleChange={handleChange} />
                    </div>
                </Col>
            </Row>
        </>
    )
}

export default MultiStepHeader;