import React from "react";
import Language from '../pages/App/Language';
import { Button } from "antd";

const ImageUpload = (props) => {
	const lang = React.useContext(Language);

	var file_frame;

	const handleClick = () => {
		if (file_frame) {
			file_frame.open();
			return;
		}
		file_frame = window.wp.media.frames.file_frame = window.wp.media({
			title: lang['Browsing Media'],
			button: {
				text: lang['Select'],
			},
			multiple: props.isMultiple // Set to true to allow multiple files to be selected
		});
		file_frame.on('select', function () {
			const selection = file_frame.state().get('selection');
			if (  props.isMultiple ) {
				// multiple images selected
				let selected = [];
				selection.map(function(file) {
					selected.push({
						fileName:file.toJSON().filename,
						url:file.toJSON().url,
						mime:file.toJSON().mime
					})
				});
				props.onSelect(selected)
			}else{
				props.onSelect(selection.first().toJSON().url);
			}
		})
		file_frame.open();
	}

	return (
		<Button onClick={() => handleClick()}>{lang[props.buttonName]}</Button>
	);
}

export default ImageUpload;