import React from "react";
import { Checkbox } from "antd";
import Language from '../Language';

const PageTitleShow = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <Checkbox
                checked={props.value}
    onChange={(e) => props.handleChange("titleVisible", e.target.checked)} >{lang['Show Page Title']} &amp; {lang['Description']}</Checkbox>
        </div>
    )
}

export default PageTitleShow;