import React from "react";
import { Select } from "antd";
import Config from "../../../config"
import Language from '../Language';

const { Option } = Select;

const FileType = (props) => {
    const lang = React.useContext(Language);

    const handleChange = (value) => {
        if(props.value.indexOf(value) === -1){
            const fileTypes = props.value
            fileTypes.push(value)
            props.handleChange("allowedFileTypes",fileTypes)
        }
    }

    const handleDeselect = (value) => {
        const fileTypes = props.value.filter(item=>item !== value)
        props.handleChange("allowedFileTypes",fileTypes)
    }

    return (
            <div className="mf-part-item">
                <span className="mf-part-title">{lang['Allowed File Types']}</span>
                <Select
                    mode="multiple"
                    style={{ width: '100%' }}
                    placeholder={lang['Please select']}
                    defaultValue={props.value}
                    onDeselect={(value)=>handleDeselect(value)}
                    onSelect={(value) => handleChange(value)}
                >
                    {Config.fileTypes.map((item,index)=> 
                        <Option key={item}>{item}</Option>
                    )}
                </Select>
            </div>
        )
}
export default FileType;