import React, {useState, useEffect } from "react";
import { Select, Button, Icon } from "antd";
import axios from 'axios';
import Language from '../Language';

const {Option} = Select

const FetchProducts = (props) => {
    const lang = React.useContext(Language);

    const [dbProducts, setDbProducts] = useState([]);
    const [productId, setProductId] = useState(null);

    async function fetchData () {
        const result = await axios.post(props.baseUrl+"admin-ajax.php?action=magicform_list_product")
        setDbProducts(result.data.data);
    };

    useEffect( () => {
          fetchData();
    },[]);

    const handleAddProduct = () => {
        const findProduct = dbProducts.find(item => item.id == productId);
        const newProduct = JSON.parse(findProduct.product);
        const newProducts = props.products

        newProduct.id = findProduct.id
        newProducts.push(newProduct)
        props.handleChange("products",newProducts)
    }

    return (
        <div style={{marginBottom:10}}>
          <Select  showSearch placeholder={lang['Please Select']} style={{width:150}} onChange={(value)=> setProductId(value)}
          optionFilterProp="children"
          filterOption={(input, option) =>
            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          >
                {
                    dbProducts.map((item,i)=> 
                        <Option value={item.id}>{item.product_name}</Option>
                    )
                }
            </Select>
            <Button style={{marginLeft:10}} onClick={() => handleAddProduct()}>{lang['Add Product']} </Button> 
        </div>
    )
}
export default FetchProducts;