import React from "react";
import { Link, Switch, Route } from "react-router-dom";
import { Icon} from "antd";
import ConditionalLogic from "./ConditionalLogic";
import General from "./General";
import Privacy from "./Privacy";
import Translations from "./Translations"
import Submissions from "./Submissions"
import Language from '../App/Language';
import PurchaseCode from "../App/PurchaseCode";
import PurchaseError from "../../components/PurchaseError";
const FormSettings = (props) => {
    const lang = React.useContext(Language);
    const licenseCheck = React.useContext(PurchaseCode);

    const handleChange = (name, value) => {
        props.updateFormSettings(name, value);
    }

    const { rules} = props.settings;
    let selectedNav = "general";
    const segment = window.location.hash ? window.location.hash.split("/") : [];
    if (segment[2] != "") {
        selectedNav = segment[2];
    }
    return (
        <div className="mf-settings">
            <div className="mf-settings-left">
                <div className="mf-settings-left-inner">
                    <ul className="mf-settings-nav">
                        <li className={(selectedNav == undefined || selectedNav == "general") ? "active" : ""}><Link to="/settings"><Icon type="appstore" /> General</Link></li>
                        <li className={selectedNav == "conditional-logic" ? "active" : ""}><Link to="/settings/conditional-logic"><Icon type="retweet" /> Conditional Logic</Link></li>
                        <li className={selectedNav == "submissions" ? "active" : ""}><Link to="/settings/submissions"><Icon type="book" /> Submissions</Link></li>
                        <li className={selectedNav == "privacy" ? "active" : ""}><Link to="/settings/privacy"><Icon type="safety" /> Privacy</Link></li>
                        <li className={selectedNav == "translations" ? "active" : ""}><Link to="/settings/translations"><Icon type="highlight" /> Translations</Link></li>
                    </ul>
                    {
                        (!licenseCheck) && 
                        <div style={{padding: 15, border:"1px"}}>
                            
                            <h6>{lang['You have to purchase license to use pro features']}:</h6>
                            <li>{lang['Strong support that solves your problem']}.</li>
                            <li>{lang['Great 10+ elements']}</li>
                            <li>{lang['Unlimited form']}.</li>
                            <li>{lang['Customizable designs']}.</li>
                            <li>{lang['Ready themes']}.</li>
                            <li>{lang['We offer our Pro version, which includes our best features for only $25']}.</li>
                            <p>{lang['Upgrade today and increase your customer interactions']}.</p>
                            
                            <div style={{ textAlign: "center", padding: "20px 0" }}><span dangerouslySetInnerHTML={{__html:lang['Purchase Plugin']}} /></div>
                        </div> 
                    }
                </div>
            </div>
            <div className="mf-settings-right">

                <Switch>
                    <Route path="/settings" exact render={() =>
                        <General settings={props.settings} handleChange={handleChange}/>
                    } />
                     <Route path="/settings/submissions" exact render={() =>
                            <Submissions settings={props.settings} handleChange={handleChange} />
                    } />
                    <Route path="/settings/conditional-logic" exact render={() =>
                            <ConditionalLogic pages={props.pages} allElements={props.allElements} settings={props.settings.conditionalLogic} rules={rules} handleChange={handleChange} />
                    } />
                     <Route path="/settings/privacy" exact render={() =>
                        <Privacy settings = {props.settings}   handleChange={handleChange}/>
                    } />
                     <Route path="/settings/translations" exact render={() =>
                        <Translations settings = {props.settings}   handleChange={handleChange}/>
                    } />
                </Switch>
            </div>
        </div>
    )
}

export default FormSettings;