import React from "react";
import FormName from "../App/settings/FormName";
import FormTitle from "../App/settings/FormTitle";
import FormDescription from "../App/settings/FormDescription";
import FormActive from "../App/settings/FormActive";
import SelectLanguage from "../App/settings/SelectLanguage";
import DateFormat from "../App/settings/DateFormat"
import Language from '../App/Language';

const General = (props) => {
    const lang = React.useContext(Language);
    const { name, title, description, active, endSubDate, subLimit,selectLanguage,titleVisible, dateFormat, dateFormatType, formPadding, verticalElementSpacing} = props.settings;
    const {handleChange} = props
   
    return (
        <>
            <h1>{lang['Form Settings']}</h1>
            <FormName value={name} handleChange={handleChange} />
            <FormTitle value={title} titleVisible={titleVisible} handleChange={handleChange} />
            <FormDescription value={description} handleChange={handleChange} />
            <SelectLanguage value={selectLanguage} handleChange={handleChange}/>
            <FormActive value={active} handleChange={handleChange} />
            <DateFormat dateFormat={dateFormat} formatType={dateFormatType} handleChange={handleChange}/>
        </>
    )
}

export default General;