import React from "react";
import Language from '../Language';
import { Select, Input, InputNumber, Radio, Tooltip, Icon } from "antd";
import IconSelect from "./IconSelect";
import SettingsPart from "../../../components/SettingsPart";

const { Option } = Select;

const Label = (props) => {

    const lang = React.useContext(Language);

    return (
        <SettingsPart title={lang['Label'].toUpperCase()} collapsed={props.collapsed}>


            {!props.hideLabelText &&
                <Input
                    name="label"
                    value={props.labelText}
                    style={{ marginBottom: 6 }}
                    onChange={(e) => props.handleChange("labelText", e.target.value)} />
            }
            <div className="mf-part-item">
                <Radio.Group size="small" buttonStyle="solid" value={props.labelDisplay} onChange={(e) => props.handleChange("labelDisplay", e.target.value)}>
                    {!props.hideInherit && <Tooltip title={lang['Inherit']}><Radio.Button value="inherit"><Icon type="up" /></Radio.Button></Tooltip>}
                    <Tooltip title={lang['Show']}><Radio.Button value="show"><Icon type="eye" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Hide']}><Radio.Button value="hide"><Icon type="eye-invisible" /></Radio.Button></Tooltip>
                </Radio.Group>

                <Radio.Group size="small" buttonStyle="solid" style={{ marginLeft: 10 }} value={props.labelPosition} onChange={(e) => {
                    const value = e.target.value;
                    if (value == "inline") {
                        props.handleChange("labelWidth", 150);
                    }
                    props.handleChange("labelPosition", value)
                }
                }>
                    {!props.hideInherit && <Tooltip title={lang['Inherit']}><Radio.Button value="inherit"><Icon type="up" /></Radio.Button></Tooltip>}
                    <Tooltip title={lang['Inline']}><Radio.Button value="inline"><Icon type="vertical-left" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Left']}><Radio.Button value="left"><Icon type="align-left" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Center']}><Radio.Button value="center"><Icon type="align-center" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Right']}><Radio.Button value="right"><Icon type="align-right" /></Radio.Button></Tooltip>
                </Radio.Group>
                {props.labelPosition === "inline" &&
                    <InputNumber
                        size="small"
                        style={{ marginLeft: 10, width: 60, verticalAlign: "middle" }}
                        value={props.labelWidth}
                        onChange={(value) => props.handleChange("labelWidth", value)} />
                }
            </div>
            {props.advancedMode &&
                <IconSelect labelText={lang['Label Icon']} property="labelIcon" icon={props.labelIcon} handleChange={props.handleChange} />}
        </SettingsPart>
    )
}

export default Label;