import React, { useState } from "react";
import Config from "../../../../config";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import InputIcon from "../../../../components/InputIcon.js";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
const Password = (props) => {
    const { item , originalItem } = props;
    const { defaultValue, labelText, readOnly, disabled, inputIcon, fieldSize, viewIcon, placeholder, maxLength, confirmInput, cssClasses, confirmLabelText, confirmPlaceholder, fieldWidth } = item.payload;

    const [value, setValue] = useState(defaultValue);
    const [confirmValue, setConfirmValue] = useState("");

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
    }

    return (
        <>
            <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
                <InputDescription position="top" {...props.item.payload} />
                <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${value != "" ? `mf-active` : ``}  ${inputIcon ? `mf-with-icon` : ``}  `}  >
                    <div className="mf-input-group">
                        {
                            inputIcon &&
                            <div className="mf-input-group-prepend">
                                <span className="mf-input-group-text" >
                                    <FontAwesomeIcon icon={['fas', inputIcon]} />
                                </span>
                            </div>
                        }
                        <input
                            type='password'
                            placeholder={placeholder}
                            className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
                            readOnly={readOnly}
                            disabled={disabled}
                            maxLength={maxLength}
                            value={value}
                            onChange={(e) => setValue(e.target.value)}
                            id={item.id}
                        />
                        {item.theme.startsWith("material") && labelText != "" && <span className="mf-material-label">{labelText}</span>}
                        {
                            viewIcon == true &&
                            <div className="input-group-append">
                                <span className="mf-input-group-text" >
                                    <FontAwesomeIcon icon={['fas', 'eye']} />
                                </span>
                            </div>
                        }
                    </div>
                </div>
                <InputDescription position="bottom" {...props.item.payload} />
            </InputLayout >
            {confirmInput &&
                <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} labelText={confirmLabelText} labelName="confirmLabelText" handleChange={handleChange}>
                    <div className={`${item.theme.startsWith("material") ? `mf-material-container` : ``} ${confirmValue != "" ? `mf-active` : ``}  ${inputIcon ? `mf-with-icon` : ``}  `}  >
                    <div className="mf-input-group">
                        <InputIcon icon={inputIcon} >
                            <input
                                type='password'
                                placeholder={confirmPlaceholder}
                                className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
                                readOnly={readOnly}
                                disabled={disabled}
                                maxLength={maxLength}
                                value={confirmValue}
                                onChange={(e) => setConfirmValue(e.target.value)}
                                id={item.id}
                            />
                         {item.theme.startsWith("material") && labelText != "" && <span className="mf-material-label">{labelText}</span>}
                        {
                            viewIcon == true &&
                            <div className="input-group-append">
                                <span className="mf-input-group-text" >
                                    <FontAwesomeIcon icon={['fas', 'eye']} />
                                </span>
                            </div>
                        }
                        </InputIcon>
                        </div>
                    </div>
                </InputLayout>
            }
        </>
    );
}

export default Password;
