import React, { useState } from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import Language from "../../Language";

const ProductList = (props) => {
    const lang = React.useContext(Language);
    const { item , originalItem } = props;
    const { defaultValue, disabled, products, readOnly, currencySymbol, inputIcon, labelText, fieldSize, placeholder, cssClasses, fieldWidth } = item.payload;

    const [value, setValue] = useState(defaultValue);

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
    }    

    return (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
        <InputDescription position="top" {...props.item.payload} />
        <div className="mf-product-list">
            {
                products.map((item,index) => 
                <>
                    <div className="mf-product-container" key={index}>
                        <div  className="mf-custom-control mf-custom-control-inline mf-custom-checkbox">
                            <input
                                type="checkbox"
                                className={`mf-custom-control-input`}
                                id={`${item.id}_${index}`}

                            /> <label className="mf-custom-control-label"
                                htmlFor={`${item.id}_${index}`}></label>
                        </div>
                        <img className="mf-product-image" src={item.views[0]} />
                        <div className="mf-product-detail">
                            <span className="mf-product-name">{item.name}</span>  
                            <span className="mf-product-description">{item.description}</span>  
                        </div>
                        <label class="mf-product-list-label">{lang['Price']}:</label>
                        <span className="mf-product-price">{currencySymbol + parseFloat(item.price).toFixed(2)}</span>
                    </div>
                    <div className="mf-product-options">
                        {
                            item.quantity &&
                            <div className="mf-product-quantity">
                                <label> {lang['Quantity']}: </label>
                                <input  type='number' className="mf-form-control"/>
                            </div>
                        }
                        {
                            item.options.map((item, index) => 
                                <div className="mf-product-option">
                                    <label>{item.label}:</label>
                                    <select className="mf-form-control">
                                        {
                                            item.items.map((optionItem,i) =>
                                                <option calcValue={optionItem.calcValues} value={optionItem.value} >{optionItem.name}</option>
                                            )
                                        }
                                    </select>
                                </div>
                            )
                        }
                    </div>
                </>
                )
            }
             <div className="mf-product-list-total">
                   <div>{lang['Total']}:</div>
            </div>
        </div> 
        <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout>
    )
}

export default ProductList