import React from "react";
import Language from '../Language';
import { InputNumber, Row, Col,Form } from "antd";
import ReactQuill from 'react-quill'; 
import 'react-quill/dist/quill.snow.css';
const MapConfigure = (props) => {

    const lang = React.useContext(Language);

    const handleMarkChange = (newValue, delta, source) => {
        if (source === 'user') {
            props.handleChange('markText',newValue)
        }
    }

    return (
        <>
            <div className="mf-part-item">
                <Row gutter={6}>
                    <Col span={12}>
                        <span className="mf-part-title">{lang['Longitude']}</span>
                        <InputNumber
                            style={{ width: "100%" }}
                            defaultValue={props.longitude}
                            onChange={(value) => props.handleChange("longitude", value)} />

                    </Col>
                    <Col span={12}>
                        <span className="mf-part-title">{lang['Latitude']}</span>
                        <InputNumber
                            style={{ width: "100%" }}
                            defaultValue={props.latitude}
                            onChange={(value) => props.handleChange("latitude", value)} />

                    </Col>
                </Row>
            </div>
            <div className="mf-part-item">
            <span className="mf-part-title">{lang['Zoom']}</span>
                <InputNumber
                    style={{ width: "100%" }}
                    defaultValue={props.zoom}
                    onChange={(value) => props.handleChange("zoom", value)} />
            </div>

            <div className="mf-part-item">
            <span className="mf-part-title">{lang['Marker Text']}</span>
                <Form.Item style={{height:"250px"}}>
                <ReactQuill value={props.text}
                    style={{height:"150px"}}
                    theme="snow"
                    onChange={handleMarkChange} />
                </Form.Item>
            </div>
        </>
    )
}

export default MapConfigure;