import React from "react";
import { Select, Form } from "antd";
import Language from '../Language';

const Option = Select.Option;

const DateFormat = (props) => {

    const lang = React.useContext(Language);

    return (
         <>
        <Form.Item label={lang['Date Formats']} extra={lang['Date format to be used in E-mail and submission']}>
            <Select  style={{ width: '30%' }} value={props.dateFormat} onChange={(value) => props.handleChange("dateFormat",value)}>
                <Option value="m/d/Y">{lang['MM/DD/YY']}</Option>
                <Option value="d/m/Y">{lang['DD/MM/YY']}</Option>
                <Option value="Y/m/d">{lang['YY/MM/DD']}</Option>
                <Option value="n/j/Y">{lang['M/D/YY']}</Option>
                <Option value="j/n/Y">{lang['D/M/YY']}</Option>
                <Option value="Y/n/d">{lang['YY/M/D']}</Option>
                <Option value="m/d/Y H:i">{'MM/DD/YY H:i'}</Option>
                <Option value="d/m/Y H:i">{'DD/MM/YY H:i'}</Option>
                <Option value="Y/m/d H:i">{'YY/MM/DD H:i'}</Option>
                <Option value="n/j/Y H:i">{'M/D/YY H:i'}</Option>
                <Option value="j/n/Y H:i">{'D/M/YY H:i'}</Option>
                <Option value="Y/n/d H:i">{'YY/M/D H:i'}</Option>
                
            </Select>
            <label style={{margin:"0px 5px 0px 10px"}}>{lang['Format Type']}</label>
            <Select  style={{ width: '30%' }} value={props.formatType} onChange={(value)=> props.handleChange("dateFormatType",value)}>
                <Option value="dash">{lang['Dash']} (-)</Option>
                <Option value="backslash">{lang['Backslash']} (/)</Option>
                <Option value="dot">{lang['Dot']} (.)</Option>
            </Select>
          </Form.Item>
        </>
    )
}

export default DateFormat;