import React, {useState} from "react";
import {Icon} from "antd";
import styled from "styled-components";
import Language from '../App/Language';

const Code = styled.input`
    padding:10px 25px;
    background: #EEE;
    border:none !important;
    outline:none;
    border-radius:30px;
    color: #666;
    display:inline-block;
    width:300px;
    font-weight:500;
    font-size:14px;
    transition: all 0.2s ease-in;
    font-family:monospace;

    &:hover {
        background: #CCC;
        cursor:pointer;
    }
`;

const Publish = (props) => {
    const lang = React.useContext(Language);
    const formInput = React.createRef();
    const formInputPhp = React.createRef();

    const [copySuccess, setCopySuccess] = useState(lang['Copy']);
    const [copySuccessPhp, setCopySuccessPhp] = useState(lang['Copy']);

    const copyToClipboard = (e) => {
        formInput.current.select();
        document.execCommand(lang['Copy']);
        // This is just personal preference.
        // I prefer to not show the the whole text area selected.
        setCopySuccess(lang['Copied'] + "!");
        
        formInput.current.focus();
    };
    const copyToClipboardPhp = (e) => {
        formInputPhp.current.select();
        document.execCommand(lang['Copy']);
        // This is just personal preference.
        // I prefer to not show the the whole text area selected.
        setCopySuccessPhp(lang['Copied'] + "!");
        formInputPhp.current.focus();
    };

    return (
        <div className="mf-settings">
            <div className="mf-settings-left">
                <div className="mf-settings-left-inner">
                    <h6>{lang['About Publish']}</h6>
                    {lang['Magic form uses shortcode to add form to website']}.
                    <span dangerouslySetInnerHTML={{__html: lang['Learn how to use shortcode from here']}}/>
                </div>
            </div>
            <div className="mf-settings-right">
                <h1>{lang['Publish']}</h1>
                <div style={{marginBottom: 30}}>
                    <h6>{lang['INLINE FORM']}</h6>
                    <p>{lang['Insert this shortcode to display inline (Recommended)']}</p>
                    <Code onClick={copyToClipboard} readOnly value={`[magicform id=${props.formId} type="inline"]`}
                          ref={formInput}/>
                    <button style={{marginLeft: 10, padding: "5px 25px"}} className="mf-admin-btn mf-admin-btn-blue"
                            onClick={copyToClipboard}><Icon type="copy"/> {copySuccess}</button>
                    <div>
                        <small className="mf-text-muted" dangerouslySetInnerHTML={{__html: lang['Learn how to use shortcode from here']}}/>
                    </div>

                    <p style={{marginTop: 20}}>{lang['Php usage to display form inline (Advanced Usage)']}</p>
                    <Code style={{width: 460}} onClick={copyToClipboardPhp} readOnly
                          value={`echo do_shortcode('[magicform id=${props.formId} type="inline"]');`}
                          ref={formInputPhp}/>
                    <button style={{marginLeft: 10, padding: "5px 25px"}} className="mf-admin-btn mf-admin-btn-blue"
                            onClick={copyToClipboardPhp}><Icon type="copy"/> {copySuccessPhp}</button>
                </div>
            </div>
        </div>
    )
}

export default Publish;