import React from "react";
import { Dropdown, Menu, Icon, Collapse, Button, Alert, message } from "antd";
import ValidationTypes from "../../../../templates/validations";
import Email from "./Email";
import Alpha from "./Alpha";
import Alphanumeric from "./AlphaNumeric";
import Numeric from "./Numeric";
import Regex from "./Regex";
import GreterThan from "./GreaterThan";
import LessThan from "./Lessthan";
import Inlist from "./Inlist";
import Domain from "./Domain";
import Ip from "./Ip";
import CreditCard from "./CreditCard";
import Date from "./Date";
import Contains from "./Contains";
import MultiSelectCount from "./MultiSelectCount"
import Language from '../../Language';

const { Panel } = Collapse;

const Validations = (props) => {
    const lang = React.useContext(Language);

    const genExtra = (index) => (
        <>
            <Icon type="setting" style={{ marginRight: 10 }} />
            <Icon type="delete" onClick={() => deleteValidation(index)}></Icon>
        </>
    );

    const addValidation = (type) => {
        const validation = JSON.parse(JSON.stringify(ValidationTypes[type]));
        const newValues = props.values;
        newValues.push(validation);
        props.handleChange("validations", newValues);
    }

    const updateValidation = (index, attr, value) => {
        const newValues = props.values;
        const validation = newValues.find((item, i) => index == i);
        validation.payload[attr] = value;
        props.handleChange("validations", newValues);
    }

    const deleteValidation = (index) => {
        let newValues = props.values;
        newValues = newValues.filter((item, i) => i != index);
        props.handleChange("validations", newValues);
        message.success(lang['Validation Deleted']);
    }

    const menuCreate = () => {
        return (
            <Menu>
                {
                    props.types.map((type) =>
                        <Menu.Item key={type} onClick={() => addValidation(type)}>{ValidationTypes[type].menuLabel}</Menu.Item>
                    )
                }
            </Menu>
        );
    }

    const renderValidationType = (item, index) => {
        switch (item.type) {
            case "email":
                return <Email item={item} index={index} updateValidation={updateValidation} />
            case "alpha":
                return <Alpha item={item} index={index} updateValidation={updateValidation} />
            case "alphanumeric":
                return <Alphanumeric item={item} index={index} updateValidation={updateValidation} />
            case "numeric":
                return <Numeric item={item} index={index} updateValidation={updateValidation} />
            case "regex":
                return <Regex item={item} index={index} updateValidation={updateValidation} />
            case "greaterthan":
                return <GreterThan item={item} index={index} updateValidation={updateValidation} />
            case "lessthan":
                return <LessThan item={item} index={index} updateValidation={updateValidation} />
            case "inlist":
                return <Inlist item={item} index={index} updateValidation={updateValidation} />
            case "domain":
                return <Domain item={item} index={index} updateValidation={updateValidation} />
            case "ip":
                return <Ip item={item} index={index} updateValidation={updateValidation} />
            case "creditCard":
                return <CreditCard item={item} index={index} updateValidation={updateValidation} />
            case "date":
                return <Date item={item} index={index} updateValidation={updateValidation} />
            case "contains":
                return <Contains item={item} index={index} updateValidation={updateValidation} />
            case "multiSelectCount":
                return <MultiSelectCount item={item} index={index} updateValidation={updateValidation} />
        }
    }

    return (
        <div className="mf-settings-part">
            
            <div style={{ display: "flex", marginBottom:5, alignItems: "stretch" }}>
                <h6 style={{ flex: 1 }}>{lang['VALIDATIONS']}</h6>
                <Dropdown overlay={menuCreate}>
                    <Button size="small" className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                       {lang['Add Validation']} <Icon type="down" />
                    </Button>
                </Dropdown>
            </div>


            <Collapse
                defaultActiveKey={['1']}
                expandIconPosition="left"
                accordion={true}
            >
                {props.values.map((item, index) => (
                    <Panel header={item.label} key={index} extra={genExtra(index)}>
                        {renderValidationType(item, index)}
                    </Panel>
                ))}

            </Collapse>
            {props.values.length == 0 && <Alert message="No Validation" banner />
            }
        </div>
    )
}

export default Validations;