import React from "react";
import {ReactSortable} from "react-sortablejs";
import Element from "../index";
import {Row, Col, Icon, message} from "antd";
import Language from "../../Language";

const Grid = (props) => {
    const lang = React.useContext(Language);

    const columns = props.item.payload.columns;
    const labelText = props.item.payload.labelText
    const updateColumnWidths = () => {
        const element = props.item;
        element.payload.columns = element.payload.columns.map(item => {
            item.span = 12 / element.payload.columns.length;
            return item;
        })
        props.updateElement(props.item.id, element);
    }

    const addColumn = () => {
        const element = props.item;
        if (element.payload.columns.length > 3) {
            message.error(lang["You can add maximum 4 columns"]);
            return false;
        }

        element.payload.columns.push({
            "span": 6,
            "elements": []
        });

        updateColumnWidths();
    }

    const deleteColumn = (index) => {
        const element = props.item;
        if (element.payload.columns.length === 1) {
            message.error(lang["You can not delete last column"]);
            return false;
        }
        element.payload.columns = element.payload.columns.filter((item, i) => i != index);
        updateColumnWidths();

    }

    return (
        <div className="mf-formElement-grid">
            <h6 className={props.collapsible ? "mf-group-collapsible" : "mf-group-collapsible active"}
                style={{fontSize: 14, background: "aliceblue"}}>{labelText}</h6>
            <div className={props.collapsible ? "magicForm-collapsible" : "magicForm-collapsible active"}>
                <Row>

                    {columns.map((column, index) => (
                        <Col span={(column.span * 2)}>
                            <div className="mf-gridTitle"><span>{column.span + " / 12"}</span>
                                <ul>
                                    <li><a onClick={addColumn}><Icon type="plus-circle"/></a></li>
                                    <li><a onClick={() => deleteColumn(index)}><Icon type="delete"/></a></li>
                                </ul>
                            </div>

                            <ReactSortable
                                list={column.elements}
                                style={{minHeight: 200}}
                                animation={200}
                                delayOnTouchStart={true}
                                delay={2}
                                group="page-elements"
                                handle=".handle"
                                setList={newState => props.updateOrder(newState, "grid", props.item.id, index)}
                            >
                                {column.elements.map(item => <Element
                                        key={item.id}
                                        selectElement={props.selectElement}
                                        deleteElement={props.deleteElement}
                                        activeElement={props.activeElement}
                                        duplicateElement={props.duplicateElement}
                                        updateElement={props.updateElement}
                                        item={item}
                                        pageSettings={props.pageSettings}
                                        formSettings={props.formSettings}
                                        pluginSettings={props.pluginSettings}
                                        updateOrder={props.updateOrder}
                                    />
                                )}


                            </ReactSortable>
                        </Col>
                    ))}
                </Row>
            </div>


        </div>
    );
}

export default Grid;