import React from "react";
import { Select, DatePicker, Input, Form, Checkbox } from "antd";
import moment from 'moment';
import Language from '../Language';

const { Option } = Select;
const { RangePicker } = DatePicker;

const DatePickerConfigure = (props) => {
    const lang = React.useContext(Language);

    const onChange = (date, dateString) => {
        props.handleChange("minDate", dateString[0]);
        props.handleChange("maxDate", dateString[1]);
    }
    const dateFormat = "YYYY/MM/DD";
    return (
        <>
            <div className="mf-part-item">
            <div className="mf-part-title">{lang['Min-Max Date']}</div>
                <RangePicker  
                    defaultValue={[moment(props.minDate!=""?props.minDate:moment(new Date(Date.now())).format(dateFormat),dateFormat), moment(props.maxDate!=""?props.maxDate:moment(new Date(Date.now())).format(dateFormat), dateFormat)]}
                    onChange={onChange} />
            </div>
            <div className="mf-part-item">
            <div className="mf-part-title">{lang['Placeholder']}</div>
                <Input
                    value={props.datePlaceholder}
                    onChange={(e) => props.handleChange("datePlaceholder", e.target.value)} />
            </div>
            <div className="mf-part-item">
    <div className="mf-part-title">{lang['Date Format']}</div>
                <Select defaultValue={props.dateFormat} style={{ width: "100%" }}
                    onChange={(value) => props.handleChange("dateFormat", value)}>
                    <Option value='dd/MM/yyyy'>DD/MM/YYYY</Option>
                    <Option value='yyyy/MM/dd'>YYYY/MM/DD</Option>
                    <Option value='yyyy/dd/MM'>YYYY/DD/MM</Option>
                    <Option value='MM/dd/yyyy'>MM/DD/YYYY</Option>
                    <Option value='MM/yyyy'>MM/yyyy</Option>
                </Select>
            </div>
            <div className="mf-part-item">
                <Checkbox checked={props.showTime}
                    onChange={(e) => props.handleChange("showTime", e.target.checked)}>{lang['Time Picker']}</Checkbox>
            </div>
        </>
    )
}

export default DatePickerConfigure;