import React from "react";
import { Select, Form, } from "antd";
import Language from '../Language';

const { Option } = Select;

const FieldSize = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Field Size']}</span>
            <Select value={props.value}
                style={{ width: "100%" }}
                onChange={(value) => props.handleChange("fieldSize", value)}>
                {!props.hideInherit && <Option value="inherit">{lang['Inherit']}</Option>}
                <Option value="default">{lang['Default']}</Option>
                <Option value="small">{lang['Small']}</Option>
                <Option value="large">{lang['Large']}</Option>
            </Select>
        </div>
    )
}

export default FieldSize;