import React from "react";
import { Form, Input, TreeSelect, Button, Icon,Alert, Switch, Modal,Row, Col } from "antd";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import Config from "../../../config";
import SelectVariable from "../../../components/SelectVariable"
import Language from '../../App/Language';
import UploadFile from '../../../components/ImageUpload'
import SelectAddInput from "../../../components/SelectAddInput"
const modules = {
    toolbar: [
        ['bold', 'italic', 'underline'],        // toggled buttons
        ['blockquote'],
        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        [{ 'direction': 'rtl' }],                         // text direction          
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

        [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
        [{ 'font': [] }],
        [{ 'align': [] }],
        ['link', 'image'],
        ['clean']
    ],
};

const { TreeNode } = TreeSelect;
class Autoresponder extends React.Component {

    state = { message: "", action: null, lang: {},errModal:false, files:[],payload:{}}

    componentDidMount() {
        this.setState({lang:this.context})
        this.setValues(this.props.action);
    }

    componentWillReceiveProps(newProps) {
        if (this.props.action != newProps.action) {
            this.setValues(newProps.action);
        }
    }


    createTree = (elements, onlyFields) => {
        return elements.map(el => {
            return <TreeNode disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                {el.children && el.children.length > 0 && this.createTree(el.children)}
            </TreeNode>
        });
    }

    createTreeTag = (elements, onlyFields) => {
        return elements.map(el => {
            return  <TreeNode disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                        {el.children && el.children.length > 0 && this.createTreeTag(el.children)}
                    </TreeNode>
        });
    }

    onSelectFiles = (selected) => {
        const newFiles = this.state.files

        newFiles.map(item => {
            if(selected.find(select=>select.fileName == item.fileName) == undefined)
                selected.push(item)
        })
        
        this.setState({
            files:selected,
        })
    }

    deleteFile = (index) => {
        const newFiles = this.state.files.filter((item,i) => i !=index)
        this.setState({
            files:newFiles
        })
    }

    setValues = (action) => {

        this.setState({
            message: action.payload.message,
            payload:action.payload,
            action:action
        })

        if(action.payload.hasOwnProperty('files')){
            this.setState({
                files:action.payload.files
            })
        }
        
        this.props.form.setFieldsValue({
            sender: action.payload.sender,
            replyTo: action.payload.replyTo,
            subject: action.payload.subject,
            to:action.payload.to,
            attachPdf: action.payload.attachPdf,
        });
    }

    addVariable = (value,label) => {
        let tag = value!="all_form_data"?`{${label[0] + "|" + value}}`:`{${value}}`
        this.setState({
            "message": `${this.state.message+tag}`
        });
    }

    addValue = ( type, value)=> {
        const newData = this.state.payload
        newData[type] = value
        this.setState({payload:newData})
        this.props.form.setFieldsValue({ [`${type}`]:value  })
      }

    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                const action = this.state.action;
                
                action.payload.message = this.state.message;
                action.payload.files = this.state.files;

                for (let i in values) {
                    action.payload[i] = values[i];
                }

                if(values.attachPdf ==  true)
                {
                    if(this.props.actions.find(item=>item.type == "generatePdf") == undefined){
                        this.setState({errModal:true})
                        this.props.form.setFields({
                            attachPdf:false
                        })
                        values.attachPdf =false;
                        return false;
                    }
                }

                //Save action
                if (this.props.actionIndex == null) {
                    this.props.saveAction(action);
                } else {
                    this.props.updateAction(this.props.actionIndex, action);
                }
                this.props.setModalVisible(false);
            }
        });
    };

    handleModalOk = () => {
        this.setState({
            errModal: false,
        });
      };

    render() {
        const { getFieldDecorator } = this.props.form;
        let emailSettingConfigured = true;
        const smtpSettings = this.props.emailSettings.smtpSettings;
        const sendgridSettings = this.props.emailSettings.sendgridSettings;

        if(smtpSettings == null && sendgridSettings == null){
            emailSettingConfigured = false;
        }

        return (
            emailSettingConfigured ?
            <>
                <Modal
                    title={this.state.lang['Pdf Error']}
                    visible={this.state.errModal}
                    onOk={this.handleModalOk}
                    onCancel={this.handleModalOk}
                >
                    <p>{this.state.lang['You have to add Generate Pdf action']}</p>
                </Modal>
            <Form onSubmit={this.handleSubmit} {...Config.formItemLayout}>

                <Form.Item label={this.state.lang['Sender']}>
                    {getFieldDecorator('sender',{ rules: [{ required: true, message: this.state.lang['Please input sender name']+'!' }]})(
                        <Input />
                    )}
                </Form.Item>
                <Form.Item label={this.state.lang['Reply-To Email']} extra={this.state.lang["You can add variable form element like {text_1_1}"]}>
                <SelectAddInput selectType="formData" style={{width:"35px"}} type="replyTo" value={this.state.payload.replyTo} allElements={this.props.allElements} addValue={this.addValue} >
                    {getFieldDecorator('replyTo',{ onChange:(e) => this.addValue("replyTo", e.target.value)})(
                            <Input />
                    )}
                     </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang['Email Subject']}>
                     <SelectAddInput selectType="allElements" style={{width:"35px"}} type="subject" value={this.state.payload.subject} allElements={this.props.allElements} addValue={this.addValue} >
                     {getFieldDecorator('subject',{rules: [{ required: true, message: this.state.lang['Please input email subject']+'!'  }], 
                        onChange:(e) => this.addValue("subject", e.target.value)})(
                            <Input />
                    )}
                    </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang['Send To']}>
                    <SelectAddInput selectType="formData" style={{width:"35px"}} type="to" value={this.state.payload.to} allElements={this.props.allElements} addValue={this.addValue} >
                        {getFieldDecorator('to',{rules: [{ required: true, message: this.state.lang['Please input send to']+'!'  }], 
                            onChange:(e) => this.addValue("to", e.target.value)})(
                                <Input />
                        )}
                    </SelectAddInput>
                </Form.Item>
                <Form.Item label={this.state.lang['File Uploads']}>
                    <UploadFile isMultiple={true} buttonName={"Upload File"} onSelect={this.onSelectFiles} />
                </Form.Item>
                {
                    this.state.files.length > 0 &&
                    <Form.Item label={this.state.lang['Files']}>
                    {
                        this.state.files.map((item, index) => 
                        <Row gutter={4}>
                            <Col span={12} ><label> {item.fileName} </label></Col> 
                            <Col span={4} ><a onClick={() => this.deleteFile(index)}><Icon type="close-circle" /></a></Col> 
                        </Row>
                        )
                    }
                </Form.Item>
                }
                <Form.Item label={this.state.lang["Attach Submission PDF"]}>
                              {getFieldDecorator('attachPdf', { valuePropName: 'checked' })(
                                  <Switch />
                              )}
                          </Form.Item>
                <ReactQuill
                 modules={modules}
                    style={{marginBottom:15}}
                    value={this.state.message}
                    onChange={val => this.setState({ message:val})} />
                    <SelectVariable selectType="allElements" allElements={this.props.allElements} addVariable={this.addVariable} />
                <Form.Item {...Config.tailFormItemLayout}>
                <Button type="primary" htmlType="submit"><Icon type="save"></Icon> {this.state.lang['Save']}</Button>
                <Button type="link" size="small" style={{ color: "#CC3333", marginLeft: 20 }} onClick={() => this.props.deleteAction(this.props.actionIndex)}><Icon type="delete"></Icon>{this.state.lang['Delete Action']}</Button>
                </Form.Item>
            </Form >
            </>
             : <>
                <Alert type="warning" showIcon message="Email Settings Required" description={this.state.lang['Please setup smtp settings in Settings > Email section']} />
                <div style={{ textAlign: "center", padding: "20px 0" }}><span dangerouslySetInnerHTML={{__html:this.state.lang['Setup Email Settings']}} /></div>
               </>
        )
        
    }
}
Autoresponder.contextType = Language;
const Wrapped = Form.create({ name: 'autoResponder' })(Autoresponder);
export default Wrapped;
