import React from "react";
import { Tabs } from "antd";
import Label from "../../settings/Label";
import Description from "../../settings/Description";
import Required from "../../settings/Required";
import FieldSize from "../../settings/FieldSize";
import CssClasses from "../../settings/CssClasses";
import AddressFields from "../../settings/AddressFields";
import Disabled from "../../settings/Disabled";
import ReadOnly from "../../settings/ReadOnly";
import Id from "../../settings/Id";
import SettingsPart from "../../../../components/SettingsPart";
import Language from '../../Language';

const TabPane = Tabs.TabPane;

const AddressSettings = ({ advancedMode, id, labelText, labelPosition, pleaseSelect, labelDisplay, statePlaceholder, stateLabel, stateCheck, labelIcon,zipPlaceholder,zipLabel, cityPlaceholder, address1Check, address2Check, cityCheck, countryCheck, zipCheck, address1Label, address2Label, cityLabel, countryLabel, labelWidth, handleChange, description, descriptionPosition, required, fieldSize, address1Placeholder, address2Placeholder, cssClasses, disabled,readOnly }) => {
    const lang = React.useContext(Language);

    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                <Label labelText={labelText} labelPosition={labelPosition}
                    labelDisplay={labelDisplay}
                    labelIcon={labelIcon}
                    advancedMode={advancedMode}
                    labelWidth={labelWidth} handleChange={handleChange} />
                <SettingsPart title={lang["INPUT"]}>
                    <div className="mf-part-item">
                        <Required value={required} handleChange={handleChange} />
                        {advancedMode &&
                                <>
                                    <Disabled disabled={disabled} handleChange={handleChange} />
                                    <ReadOnly readOnly={readOnly} handleChange={handleChange} />
                                </>    
                            }
                    </div>
                    <AddressFields address1Check={address1Check} address2Check={address2Check}  
                        address1Placeholder={address1Placeholder} address2Placeholder={address2Placeholder}
                        address1Label={address1Label} address2Label={address2Label} cityCheck={cityCheck} 
                        cityLabel={cityLabel} cityPlaceholder={cityPlaceholder} countryCheck={countryCheck} 
                        countryLabel={countryLabel} zipCheck={zipCheck} zipLabel={zipLabel} 
                        zipPlaceholder={zipPlaceholder} stateLabel={stateLabel} statePlaceholder={statePlaceholder}
                        stateCheck={stateCheck} pleaseSelect={pleaseSelect} handleChange={handleChange} />
                </SettingsPart>
                <Description descriptionValue={description}
                    descriptionPosition={descriptionPosition}
                    handleChange={handleChange} />
                {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                <FieldSize value={fieldSize} handleChange={handleChange} />
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default AddressSettings;