import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import {Row, Col} from "antd";

const CheckBox = (props) => {
    const { item, originalItem} = props;
    const { disabled, options, cssClasses, optionsDisplay, otherCheck, otherLabel, otherPlaceholder, otherDefaultValue } = item.payload;

    const createOption = (option, i) => (
        <div key={i} className="mf-custom-control mf-custom-control-inline mf-custom-checkbox">
            <input
                type="checkbox"
                name={item.id}
                value={option.value}
                checked={option.default}
                disabled={disabled}
                className={`mf-custom-control-input ${cssClasses.join(" ")}`}
                id={`${item.id}_${i}`}

            /> <label className="mf-custom-control-label"
                htmlFor={`${item.id}_${i}`}>{option.name}</label>
        </div>
    );

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }
    
    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <Row>
                {
                    options.map((option, i) => optionsDisplay == "inline" ? createOption(option, i) :
                        <Col span={24 / optionsDisplay}>{createOption(option, i)}</Col>)
                }
                {
                    otherCheck && 
                <>
                    <div className="mf-custom-control mf-custom-control-inline mf-custom-checkbox">
                    <input
                        type="checkbox"
                        name={item.id}
                        disabled={disabled}
                        className={`mf-custom-control-input ${cssClasses.join(" ")}`}
                        id={`${item.id}_other`}
                    />
                         <label className="mf-custom-control-label"
                            htmlFor={`${item.id}_other`}>{otherLabel}</label>
                            <input 
                        type="text"
                        style={{width:120, marginLeft:15}}
                        className="mf-form-control mf-other-input"
                        placeholder={otherPlaceholder} 
                        value={otherDefaultValue}
                    />
                    </div>
                        
                </>
                }
            </Row>

            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout >
    );
}

export default CheckBox;
