import React from "react";
import { Input } from "antd";
import SettingsPart from "../../../components/SettingsPart";
import Language from '../Language';

const CustomCss = (props) => {
    const lang = React.useContext(Language);

    return (
        <SettingsPart title={lang['CUSTOM CSS']} collapsed={true}>
                <Input.TextArea style={{ height: "100px", width: "100%" }}
                    onChange={(e) => props.handleChange("customCss", e.target.value)}
                    value={props.value} />
        </SettingsPart >
    )
}
export default CustomCss;