import React from "react";
import {Input, Form, Radio} from "antd";
import Language from '../Language';

const FormTitle = (props) => {

    const lang = React.useContext(Language);
    return (
        <>
                <Form.Item label={lang['Form Title']}>
                    <Radio.Group onChange={(e) =>props.handleChange("titleVisible", e.target.value)} value={props.titleVisible}>
                    <Radio value={true}>{lang['Visible']}</Radio>
                    <Radio value={false}>{lang['Hidden']}</Radio>
                    </Radio.Group>
                </Form.Item>
                {
                    props.titleVisible &&
                    <Form.Item label={lang['Title']}>
                         <Input
                             value={props.value}
                             onChange={(e) => props.handleChange("title", e.target.value)}/>
                    </Form.Item>
                }
           
        </>
    )
}

export default FormTitle;