import React from "react";
import { InputNumber } from "antd";
import Language from '../Language';


const DefaultRating = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Default Rating']}</span>
            <InputNumber
                style={{width:"100%"}}
                value={props.value}
                onChange={(value) =>  props.handleChange("defaultRating",value)} />
        </div>
    )
}

export default DefaultRating;