import React from "react";
import { InputNumber, Row, Col } from "antd";
import Language from '../Language';

const FormSpacing = (props) => {
    const lang = React.useContext(Language);

    return (
        <Row gutter="12">
            <Col span="12">
                <div className="mf-part-item">
    <div className="mf-part-title">{lang['Form Horizontal Padding']}</div>
                    <div>
                        <InputNumber
                            value={props.formPadding}
                            onChange={(value) => props.handleChange("formPadding", value)} />
                        <small className="mf-form-text mf-text-muted">{lang['This value will be change padding left and right']}</small>
                    </div>
                </div>
            </Col>
            <Col span="12">
                <div className="mf-part-item">
                <div className="mf-part-title">{lang['Vertical Element Spacing']}</div>
                    <div>
                        <InputNumber
                            value={props.verticalElementSpacing}
                            onChange={(value) => props.handleChange("verticalElementSpacing", value)} />
                    </div>
                </div>
            </Col>
        </Row>
    )
}

export default FormSpacing;