import React from "react";
import FormSubmission from "../App/settings/FormSubmission";
import Language from '../App/Language';
const Submissions = (props) => {
    const lang = React.useContext(Language);
    const { subLimit, endSubDate } = props.settings;
    const {handleChange} = props

    return (
        <div>
            <h1>{lang['Submissions']}</h1>
            <FormSubmission subLimit={subLimit} endSubDate={endSubDate} handleChange={handleChange}/>
        </div >
    )
}

export default Submissions;
