import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
const TermsOfUse = (props) => {
    const { item , originalItem } = props;
    const { checkBoxPos, textAreaValue, title, heightValue } = item.payload;

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
            <InputDescription position="top" {...props.item.payload} />
            <div
                style={{ minHeight:"initial", maxHeight:"initial", height: heightValue, overflow: "auto" }}
                className={`mf-form-control`}
            >{textAreaValue.split("\n").map(function (item) {
                return (<>
                    { item }
                    < br /></>
                )
            })}</div>
            <style dangerouslySetInnerHTML={{
                __html: `
                div[data-id='${item.id}'] .mf-form-control { height: ${heightValue}px !important; max-height:initial; min-height:auto;}
                `
            }}></style>
            <div style={{ marginTop: 10 }} className={"mf-label mf-label-" + checkBoxPos}>
                <div className="mf-custom-control mf-custom-control-inline mf-custom-checkbox">
                    <input
                        type="checkbox"
                        name={item.id}
                        className={`mf-custom-control-input`}
                        id={item.id}
                    />
                    <label htmlFor={item.id} className="mf-custom-control-label">
                        {title}
                    </label>
                </div>
            </div>
            <InputDescription position="bottom" {...props.item.payload} />
           
            <div>
            </div>
        </InputLayout>
    );
}

export default TermsOfUse;