import React from "react";
import { InputNumber, Select, Input, Row, Col } from "antd";
import SettingsPart from "../../../components/SettingsPart";
import Language from '../Language';

const { Option } = Select
const RangeValues = (props) => {
    const lang = React.useContext(Language);

    return (
        <SettingsPart title={lang['INPUT']}>
            <div className="mf-part-item">
                <Row gutter={12}>
                    <Col span={12}>
    <span className="mf-part-title">{lang['Min']}</span>
                        <InputNumber
                            style={{ width: "100%" }}
                            value={props.min}
                            onChange={(value) => props.handleChange("minRange", value)} />
                    </Col>
                    <Col span={12}>
    <span className="mf-part-title">{lang['Max']}</span>
                        <InputNumber
                            style={{ width: "100%" }}
                            value={props.max}
                            onChange={(value) => props.handleChange("maxRange", value)} />
                    </Col>
                </Row>
            </div>
            <div className="mf-part-item">
    <span className="mf-part-title">{lang['Default Value']}</span>
                <InputNumber
                    style={{ width: "100%" }}
                    value={props.defaultValue}
                    onChange={(value) => props.handleChange("defaultValue", value)} />
            </div>
            <div className="mf-part-item">
    <span className="mf-part-title">{lang['Step']}</span>
                <InputNumber
                style={{width:"100%"}}
                    value={props.step}
                    onChange={(value) => props.handleChange("rangeStep", value)} />
            </div>
            <div className="mf-part-item">
    <span className="mf-part-title">{lang['Value Label']}</span>
                <Input
                    value={props.valueTitle}
                    onChange={(e) => props.handleChange("valueTitle", e.target.value)} />
            </div>
            <div className="mf-part-item">
    <span className="mf-part-title">{lang['Value Position']}</span>
                <Select value={props.valueTitlePos} onChange={(value) => {
                    if (value == "inline") {
                        props.handleChange("valueTitlePos", 150);
                    }
                    props.handleChange("valueTitlePos", value)
                }
                } style={{ width: "100%" }}>
                    {!props.hideInherit && <Option value="inherit">{lang['Inherit']}</Option>}
            <Option value="inline">{lang['Inline Left']}</Option>
            <Option value="left">{lang['Left']}</Option>
            <Option value="center">{lang['Center']}</Option>
            <Option value="right">{lang['Right']}</Option>
                </Select>
            </div>
        </SettingsPart>
    )
}

export default RangeValues;