import React from "react";
import { Checkbox, InputNumber } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const GreaterThan = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div class="mf-part-item">
                <span class="mf-part-title">{lang['Value']}</span>
                <div>
                    <InputNumber defaultValue={item.payload.value}
                        onChange={(value) => updateValidation(index, "value", value)} />
                    <Checkbox style={{ marginLeft: 10 }}
                        defaultChecked={item.payload.include}
    onChange={(e) => updateValidation(index, "include", e.target.checked)}>{lang['Equal']}</Checkbox>
                </div>
            </div>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
        </>
    )
}

export default GreaterThan;