import React from "react";
import { InputNumber, Select } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const MultiSelectCount = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
            <div class="mf-part-item">
             <span class="mf-part-title">{lang['Limit of Select Count']}: </span>
                <InputNumber value={item.payload.selectCount}
                    onChange={(value) => updateValidation(index, "selectCount", value)} />
            </div>
            <div class="mf-part-item">
             <span class="mf-part-title">{lang['Condition Type']}</span>
             <Select style={{width:"100%"}} value={item.payload.conditionType} onChange={(val) => updateValidation(index, "conditionType", val)}>
                    <Select.Option key="equal">{lang['Equal']}</Select.Option>
                    <Select.Option key="greaterThan">{lang['Greater Than']}</Select.Option>
                    <Select.Option key="lessThan">{lang['Less Than']} </Select.Option>
                </Select>
            </div>
        </>
    )
}

export default MultiSelectCount;