import React from "react";
import { Tabs} from "antd";
import Label from "../../settings/Label";
import Description from "../../settings/Description";
import Required from "../../settings/Required";
import CssClasses from "../../settings/CssClasses";
import SelectOption from "../../settings/SelectOption";
import OptionsDisplay from "../../settings/OptionsDisplay";
import CheckboxLabelPos from "../../settings/CheckboxLabelPos";
import Disabled from "../../settings/Disabled"
import Id from "../../settings/Id"
import SettingsPart from "../../../../components/SettingsPart";
import Language from "../../Language";
import SelectInputType from "../../settings/SelectInputType";
import Height from "../../settings/Height";
import Width from "../../settings/Width";
import SizeControl from "../../settings/SizeControl"
import Validations from "../../settings/Validations/index";

const TabPane = Tabs.TabPane;

const ThumbnailSelectorSettings = ({advancedMode,id, labelText, labelPosition, validations, autoSize, labelDisplay, cbLabelDisplay, inputType, disabled, labelIcon,  labelWidth,cbLabelPos,enableValues, handleChange, heightValue, widthValue, description, descriptionPosition, required, calcValues, cssClasses, options, optionsDisplay}) => {
    const lang = React.useContext(Language);
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                    <Label labelText={labelText} 
                        labelPosition={labelPosition}
                        labelIcon={labelIcon}
                        advancedMode={advancedMode}
                        labelDisplay={labelDisplay}
                        labelWidth={labelWidth} handleChange={handleChange}/>
                    
                   <SettingsPart title={lang["INPUT"]}>
                        <div className="mf-part-item">
                            <SelectInputType value={inputType} handleChange={handleChange}  />
                            <Required value={required} handleChange={handleChange}/>
                            {advancedMode && <Disabled disabled={disabled} handleChange={handleChange} />}
                        </div>
                        {advancedMode && <CheckboxLabelPos cbLabelDisplay={cbLabelDisplay} value={cbLabelPos} handleChange={handleChange}/>}

                    </SettingsPart>
                    <div className="mf-settings-part">
                        <h6>OPTIONS</h6>
                        <SelectOption type="thumbnailSelector" calcValues={calcValues} values={options} enableValues={enableValues} handleChange={handleChange}/>
                    </div>
                    <Description descriptionValue={description}
                                 descriptionPosition={descriptionPosition}
                                 handleChange={handleChange}/>
                    {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                    <SizeControl value={autoSize}  handleChange={handleChange}/>
                    {
                        autoSize == false &&
                        <>
                            <Height value={heightValue} handleChange={handleChange} />
                            <Width value={widthValue} handleChange={handleChange} />
                         </>
                    }
                    <OptionsDisplay value={optionsDisplay} handleChange={handleChange}/>
                    <CssClasses value={cssClasses} handleChange={handleChange}/>
            </TabPane>
            <TabPane tab={lang["Validations"]} key="3">
                <Validations types={['multiSelectCount']} values={validations} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default ThumbnailSelectorSettings;