import React from "react";
import { Form, Tabs } from "antd";
import Config from "../../../../config";
import Label from "../../settings/Label";
import Description from "../../settings/Description";
import Required from "../../settings/Required";
import CssClasses from "../../settings/CssClasses";
import SelectOption from "../../settings/SelectOption";
import OptionsDisplay from "../../settings/OptionsDisplay";
import Disabled from "../../settings/Disabled"
import Id from "../../settings/Id"
import SettingsPart from "../../../../components/SettingsPart";
import OtherInput from "../../settings/OtherInput"
import Language from "../../Language";
const TabPane = Tabs.TabPane;

const CheckboxSettings = ({ advancedMode, id, labelText, labelPosition, labelDisplay, calcValues, labelIcon, disabled, labelWidth, handleChange, otherLabel, otherCheck, otherDefaultValue, otherPlaceholder, description, descriptionPosition, required, enableValues, optionsDisplay, cssClasses, options }) => {
    const lang = React.useContext(Language);
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                <Label labelText={labelText}
                    advancedMode={advancedMode}
                    labelIcon={labelIcon}
                    labelPosition={labelPosition}
                    labelDisplay={labelDisplay}
                    labelWidth={labelWidth} handleChange={handleChange} />

                <SettingsPart title={lang["INPUT"]}>
                    <div className="mf-part-item">
                        <Required value={required} handleChange={handleChange} />
                        {advancedMode && <Disabled disabled={disabled} handleChange={handleChange} />}
                    </div>
                </SettingsPart>
                <SettingsPart title={lang["OTHER INPUT"]}>
                    <OtherInput otherLabel={otherLabel} otherCheck={otherCheck} handleChange={handleChange} otherDefaultValue={otherDefaultValue} otherPlaceholder={otherPlaceholder} />
                </SettingsPart>
                <SettingsPart title={lang["OPTIONS"]}>
                    <SelectOption type="radioButton"  calcValues={calcValues} enableValues={enableValues} values={options} handleChange={handleChange} />
                </SettingsPart>
                <Description descriptionValue={description}
                    descriptionPosition={descriptionPosition}
                    handleChange={handleChange} />
                {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key="2">
                <OptionsDisplay value={optionsDisplay} handleChange={handleChange} />
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default CheckboxSettings;