import React from "react";
import { Link } from "react-router-dom";
import { Icon } from "antd";
import Logo from "./Logo";
import Language from '../pages/App/Language';

const AppHeader = ({ formName, saveForm, formSaving}) => {

    const lang = React.useContext(Language);

    const hashes = window.location.hash.split("/");
    let selectedMenu = null;
    if (hashes && hashes[1] != "") {
        selectedMenu = hashes[1];
    }

    return (
        <div className="mf-header">
            <div className="mf-header-left">
                <h1> <Logo />
                    <span className="mf-form-name" title={formName}>{formName}</span>
                </h1>
            </div>
            <div className="mf-header-center">
                <ul>
                    <li className={selectedMenu == null || selectedMenu == "page-settings" || selectedMenu == "multi-step" ? "active" : ""}><Link to="/"><Icon type="build" /> {lang["Build"]}</Link></li>
                    <li className={selectedMenu == "settings" ? "active" : ""}><Link to="/settings"><Icon type="setting" /> {lang["Settings"]}</Link></li>
                    <li className={selectedMenu == "actions" ? "active" : ""}><Link to="/actions"><Icon type="interaction" /> {lang["Actions"]}</Link></li>
                    <li className={selectedMenu == "publish" ? "active" : ""}><Link to="/publish"><Icon type="deployment-unit" /> {lang["Publish"]}</Link></li>
                </ul>
            </div>
            <div className="mf-header-right">
                <Link to="/preview"><button style={{ marginRight: 10 }} className="mf-admin-btn" onClick={() => saveForm(0)} ><Icon type="eye" /> {lang["Preview"]}</button></Link>
                <button className="mf-admin-btn mf-admin-btn-green" onClick={() => saveForm(1)}>
                    {formSaving ? <Icon type="loading" /> : <Icon type="save" />}  {lang["Save Form"]}</button> {" "}
            </div>
        </div>
    )
}

export default AppHeader;