import React from "react";
import Language from '../Language';
import { Radio, Tooltip, Icon } from "antd";
import SettingsPart from "../../../components/SettingsPart";

const ElementPosition = (props) => {

    const lang = React.useContext(Language);

    return (
        <SettingsPart title={lang['Position']}>
                <Radio.Group size="small" buttonStyle="solid" style={{ marginLeft: 10 }} value={props.value} onChange={(e) => props.handleChange("elementPosition",e.target.value)}>
                    <Tooltip title={lang['Left']}><Radio.Button value="left"><Icon type="align-left" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Center']}><Radio.Button value="center"><Icon type="align-center" /></Radio.Button></Tooltip>
                    <Tooltip title={lang['Right']}><Radio.Button value="right"><Icon type="align-right" /></Radio.Button></Tooltip>
                </Radio.Group>
        </SettingsPart>
    )
}

export default ElementPosition;