import React, { useState } from "react";
import Iframe from 'react-iframe';
import { Link } from "react-router-dom";
import { Icon, Tooltip, Spin, Switch } from "antd";
import styled from "styled-components";

const PreviewContainer = styled.div`
    display:flex;
    height: 40px;
    line-height: 40px;
    background: #fff6a9;
    text-align: center;
    font-size:14px;
    justify-content:space-between;
    padding:0 20px;
`;

const PreviewModes = styled.div`
    flex:1;
`;

const PreviewMode = styled.a`
    width:40px;
    height:40px;
    font-size:18px;
    color: #333;
    display:inline-flex;
    justify-content:center;
    align-items:center;
    background-color: ${props => props.active ? "#FFF" : "none"};
    &:hover {
        background:#EEE;
    }
`;


const Preview = (props) => {

    const [mode, setMode] = useState("desktop");
    const [validation, setValidation] = useState(true);
    const modes = [
        { icon: <Icon type="mobile" />, title: "Mobile", name: "mobile" },
        { icon: <Icon type="tablet" />, title: "Tablet", name: "tablet" },
        { icon: <Icon type="desktop" />, title: "Desktop", name: "desktop" },
    ]

    const modeWidths = {
        mobile: 480,
        tablet: 768,
        desktop: 1024
    }
    if (!props.loadPreview)
        return <Spin tip="Loading Preview ..."></Spin>

    return (

        <>
            <PreviewContainer>
                <div>You are in preview mode.</div>
                <PreviewModes>
                    {modes.map(item => (
                        <Tooltip title={item.title}><PreviewMode active={mode == item.name} onClick={() => setMode(item.name)}>{item.icon}</PreviewMode></Tooltip>
                    ))}
                </PreviewModes>
                <div style={{ marginRight: 20, display: "inline-flex", alignItems: "center" }}>
                    <span style={{ marginRight: "5px" }}> Check Validations </span>
                    <Switch
                        checkedChildren={<Icon type="check" />}
                        unCheckedChildren={<Icon type="close" />}
                        defaultChecked
                        onChange={(checked) => setValidation(checked)}
                    />
                </div>
                <Link to="/">[Exit Preview Mode]</Link>
            </PreviewContainer>
            <div  className="mf-formContentContainer" style={{ padding:"30px 0", flex:1, backgroundColor: "#EEE", justifyContent:"center" }}>
                <Iframe
                    width={modeWidths[mode]}
                    url={"?action=magicform_preview&id=" + props.formId + "&validation=" + validation}
                    id="myId"
                    className="mf-preview-iframe"
                    display="block"
                />
            </div>
        </>
    )
}

export default Preview;