import React from "react";
import Language from '../Language';

import { Checkbox,Input } from "antd";


const NameFields = (props) => {

    const lang = React.useContext(Language);

    return (
        <>
        <div className="mf-part-item" style={{marginTop:10}}>
    <span className="mf-part-title">{lang['Fields']}</span>
            <div>

                <Checkbox checked={props.title}
                    onChange={(e) => props.handleChange("title", e.target.checked)}>
                    {lang['Title']}</Checkbox>
                <Checkbox checked={props.middle}
                    onChange={(e) => props.handleChange("middle", e.target.checked)}>
                    {lang['Middle']}</Checkbox>
             
                <Checkbox checked={props.surname}
                    onChange={(e) => props.handleChange("surname", e.target.checked)}>
                    {lang ['Surname']}</Checkbox>
            </div>
        </div>
        <div className="mf-part-item" style={{marginTop:10}}>
    <span className="mf-part-title">{lang['PLACEHOLDERS']}</span>
            <>
                <Input style={{marginBottom:10}} value={props.namePlaceholder} placeholder={lang['First Name']} onChange={(e) => props.handleChange("namePlaceholder", e.target.value)} /> 
            </>
            {
                props.middle && 
                    <Input  style={{marginBottom:10}} value={props.middlePlaceholder} placeholder={lang['Middle Name']} onChange={(e) => props.handleChange("middlePlaceholder", e.target.value)} />
            } 
            {
                props.surname && 
                <>
                    <Input  style={{marginBottom:10}} value={props.surnamePlaceholder} placeholder={'Last Name'} onChange={(e) => props.handleChange("surnamePlaceholder", e.target.value)} />
                </>
            }     
        </div>
        </>
    )
}

export default NameFields;