import React from "react";
import { Input } from "antd";
import Language from '../Language';

const Title = (props) => {
    const lang = React.useContext(Language);
    return (
        <div class="mf-part-item">
            <div class="mf-part-title">{lang[props.header]}</div>
            <Input
                value={props.value}
                onChange={(e) => props.handleChange("title", e.target.value)} />
        </div>
    )
}

export default Title;