import React from "react";
import Language from '../Language';

import { InputNumber } from "antd";

const MinMaxRange = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Min']}-{lang['Max']}</span>
            <div>   
                <InputNumber
                    placeholder={lang['Min'].toLowerCase()}
                    defaultValue={props.min}
                    max={props.maxValue}
                    style={{marginRight:5}}
                    onChange={(value) => props.handleChange("minValue", value)} />
                    {" - "}
                <InputNumber
                    placeholder={lang['Max'].toLowerCase()}
                    min={props.minValue}
                    max={props.maxValue}
                    style={{ marginLeft: 5 }}
                    defaultValue={props.max}
                    onChange={(value) => props.handleChange("maxValue", value)} />
            </div>
        </div>
    )
}

export default MinMaxRange;