import React from "react";
import { Input, Radio, Row, Col } from "antd";
import SettingsPart from "../../../components/SettingsPart";
import LoadingIconSelect from "./LoadingIconSelect";
import ColorPicker from "../../../components/ColorPicker";
import Language from '../Language';

const FormLoading = (props) => {
    const lang = React.useContext(Language);

    const values = props.value;

    const handleChange = (name, value) => {
        values[name] = value;
        props.handleChange("loading", values);
    }

    return (
        <SettingsPart title={lang['LOADING SETTINGS']} collapsed={true}>

            <div className="mf-part-item">
                <Radio.Group value={values.type} onChange={(e) => handleChange("type", e.target.value)}>
                    <Radio value="button">{lang['Inside Button']}</Radio>
                    <Radio value="form">{lang['Whole Form']}</Radio>
                    <Radio value="bar">{lang['Loading Bar']}</Radio>
                </Radio.Group>
            </div>

            <div className="mf-part-item">
                <Row>
                    <Col span={12}>
                        <LoadingIconSelect property="icon" color={values.iconColor} labelText={lang['Loading Icon']} handleChange={handleChange} icon={values.icon} />
                    </Col>
                    <Col span={12}>
                    <div className="mf-part-title">{lang['Icon Color']}</div>
                        <div>
                            <ColorPicker position="right" value={values.iconColor} onChange={(val => handleChange("iconColor", val))} />
                        </div>
                    </Col>
                </Row>
            </div>
            {values.type != "button" &&
                <div className="mf-part-item">
                    <div className="mf-part-title">{lang['Loading Text']}</div>
                    <Input value={values.text} onChange={e => handleChange("text", e.target.value)} />
                </div>
            }
        </SettingsPart >
    )
}
export default FormLoading;