
import React from "react";
import GroupGridLabel from "../../settings/GroupGridLabel";
import { Tabs } from "antd";
import Language from "../../Language";

const TabPane = Tabs.TabPane;

const GridSettings = ({ labelText, handleChange}) => {
    const lang = React.useContext(Language);

    return (
        <Tabs type="card">
        <TabPane tab={lang["General"]} key="1">
            <GroupGridLabel value={labelText} handleChange={handleChange}/>
            </TabPane>
        </Tabs>
    )

}
export default GridSettings;