import React from "react";
import { InputNumber } from "antd";
import Language from '../Language';

const Width = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Width']}</span>
            <InputNumber
                style={{ width: "100%" }}
                min={0}
                value={props.value}
                onChange={(value) => props.handleChange("widthValue", value)} />
                <div className="ant-form-extra">{lang['Enter pixel. want to 100px, enter 100']}</div>
        </div>
    )
}

export default Width;