import React, { useState } from "react";
import Config from "../../../../config";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
import InputIcon from "../../../../components/InputIcon.js";
import Language from "../../Language";

const Email = (props) => {
  const lang = React.useContext(Language);

  const { item , originalItem } = props;
  const {
    disabled,
    readOnly,
    confirmLabelText,
    labelText,
    confirmPlaceholder,
    defaultValue,
    fieldSize,
    fieldWidth,
    inputIcon,
    confirmInput,
    placeholder,
    cssClasses,
  } = item.payload;

  const [value, setValue] = useState(defaultValue);
  const [confirmValue, setConfirmValue] = useState("");

  const handleChange = (value,name) => {
   const newItem = originalItem
    newItem.payload[name] = value
    props.updateElement(item.id, newItem)
  }

  return (
    <>
      <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} handleChange={handleChange}>
        <InputDescription position="top" {...props.item.payload} />
        <div
          className={`${
            item.theme.startsWith("material") ? `mf-material-container` : ``
          } ${value != "" ? `mf-active` : ``}  ${
            inputIcon ? `mf-with-icon` : ``
          }  `}
        >
          <InputIcon icon={inputIcon}>
            <input
              type="email"
              value={value}
              onChange={(e) => setValue(e.target.value)}
              placeholder={placeholder}
              className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
              readOnly={readOnly}
              disabled={disabled}
              id={item.id}
            />
            {item.theme.startsWith("material") && labelText != "" && (
              <span className="mf-material-label">{labelText}</span>
            )}
          </InputIcon>
        </div>
        <InputDescription position="bottom" {...props.item.payload} />
      </InputLayout>

      {confirmInput && (
        <InputLayout isMaterial={item.theme.startsWith("material")} payload={props.item.payload} labelText={confirmLabelText} labelName="confirmLabelText" handleChange={handleChange}>
          <div
            className={`${
              item.theme.startsWith("material") ? `mf-material-container` : ``
            } ${confirmValue != "" ? `mf-active` : ``}  ${
              inputIcon ? `mf-with-icon` : ``
            }  `}
          >
            <InputIcon icon={inputIcon}>
              <input
                type="email"
                value={confirmValue}
                onChange={(e) => setConfirmValue(e.target.value)}
                placeholder={confirmPlaceholder}
                className={`mf-form-control ${cssClasses.join(" ")} ${Config.size[fieldSize]} ${Config.width[fieldWidth]}`}
                readOnly={readOnly}
                disabled={disabled}
                id={item.id}
              />
              {item.theme.startsWith("material") && confirmLabelText != "" && (
                <span className="mf-material-label">{confirmLabelText}</span>
              )}
            </InputIcon>
          </div>
        </InputLayout>
      )}
    </>
  );
};

export default Email;
