import React from "react";
import { Form, Switch, Icon} from "antd";
import Language from '../Language';

const FormActive = (props) => {
    const lang = React.useContext(Language);
    return (
        <>
            <Form.Item label={lang['Active']} extra={lang['In the active state, the form appears on the interface']}>
                <Switch
                    defaultChecked={props.value}
                    onChange={(e) => props.handleChange("active", e)}
                />
            </Form.Item>
        </>
    )
}

export default FormActive;