import React from "react";
import { Input } from "antd";
import ErrorMessage from "./ErrorMessage";

const Regex = ({ item, index, updateValidation }) => {
    return (
        <>
            <div class="mf-part-item">
                <span class="mf-part-title">Regex</span>
                <Input value={item.payload.regex}
                    onChange={(e) => updateValidation(index, "regex", e.target.value)} />
            </div>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
        </>
    )
}

export default Regex;