import React, { useState } from "react";
import Language from '../Language';
import { Button, Modal, Form, Input, Row, Col, Icon, Tooltip } from "antd";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/free-solid-svg-icons'
import styled from "styled-components";

// Add both three icon sets
library.add(fas)
const { Search } = Input;


const IconItem = styled.a`
    display:block;
    padding:10px;
    text-align:center;
    
    &:hover {
        border-radius: 4px;
        background: aliceblue;
    }
`;

let fasArray = Object.keys(library.definitions.fas)
const IconSelect = (props) => {

    const lang = React.useContext(Language);

    const [modal, setModal] = useState(false);
    const [iconNames, setIconNames] = useState(fasArray)

    const handleSearch = (iconName) => {
        setIconNames(fasArray.filter(item => item.substr(0, iconName.length) == iconName))
    }

    const selectIcon = (item) => {
        props.handleChange(props.property, item)
        setModal(false);
    }

    const deleteIcon = () => {
        props.handleChange(props.property, null)
    }
    return (
        <div className="mf-part-item">
            <div className="mf-part-title">{props.labelText}: </div>
            <div>
                <Button size="small" onClick={() => setModal(true)}>{props.icon ? <FontAwesomeIcon style={{ marginRight: "5px" }} icon={['fas', props.icon]} /> : lang['Select']}</Button>
                {
                    props.icon &&
                    <>
                        <Button size="small" type="ghost" style={{ marginLeft:10 }} onClick={() => deleteIcon()}><Tooltip title={lang['Delete']}><Icon type="close-circle" /> {lang['Remove']} </Tooltip></Button>
                    </>
                }
                <Modal
                    title={lang['Icon List']}
                    visible={modal}
                    onOk={() => setModal(false)}
                    onCancel={() => setModal(false)}
                >
                    <Search
                        placeholder={lang['Search Icon']}
                        onChange={event => handleSearch(event.target.value)}
                        style={{ width: "100%" }}
                    />
                    <div style={{ overflowY: "auto", overflowX: "hidden", height: 400 }}>
                        <Row gutter={6}>
                            {
                                iconNames.map((item, i) =>
                                    <Col span={4} key={i}>
                                        <IconItem onClick={() => selectIcon(item)} >
                                            <FontAwesomeIcon style={{ height: "30px", width: "30px" }} icon={['fas', item]} />
                                            <div style={{ fontSize: "12px", textOverflow: "ellipsis", maxWidth: "100px", whiteSpace: "nowrap", overflow: "hidden" }}>{item}</div>
                                        </IconItem>
                                    </Col>
                                )
                            }
                        </Row>
                    </div>
                </Modal>
            </div>
        </div>
    )
}

export default IconSelect;