import React from "react";
import { Form, Input, InputNumber, Button, Row, Col, TreeSelect, Checkbox } from "antd";
import Language from '../Language';
import FormItem from "antd/lib/form/FormItem";

const {TreeNode} = TreeSelect

const CalculateVariable = (props) => {

    const lang = React.useContext(Language);

    const addVariable = (value, label="") => {

        if(label!="")
            value = "{" + label + "|" + value + "}";

        props.handleChange("calculateVariables",props.value+value)
    }

    const createTreeTag = (elements, onlyFields) => {
        return elements.map(el => {
            return el.id.search("calculateField") == -1 &&  <TreeNode  disabled={onlyFields ? el.optGroup : false} value={el.id} title={el.label} key={el.id}>
                        {el.children && el.children.length > 0 && createTreeTag(el.children)}
                    </TreeNode>
        });
    }



    return (
         <>
        <Form.Item label={lang['Calculate Variables']}>
            <Input.TextArea
                placeholder={lang['Calculate Variables']}
                rows={4} 
                value={props.value}
                onChange={(e) =>props.handleChange("calculateVariables",e.target.value)}
            />
        </Form.Item>
        <FormItem>
            <Row> 
                <TreeSelect
                            showSearch
                            style={{ width: '150px' }}
                            placeholder={lang["Please Select"]}
                            allowClear
                            treeDefaultExpandAll
                            onChange={(value,label)=>addVariable(value,label) }
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                    >
                            <TreeNode disabled={true} value="form_elements" title={lang["Form Elements"]}>
                                {createTreeTag(props.allElements,true)}
                            </TreeNode>
                    </TreeSelect>
            </Row>
            <Row gutter={4}>
                <Col span={4}><Button onClick={()=> addVariable("(")} >(</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable(")")} >)</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "%")} >%</Button></Col>
            </Row>
            <Row gutter={4}>
                <Col span={4}><Button onClick={()=> addVariable( "7")} >7</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "8")} >8</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "9")} >9</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "+")} >+</Button></Col>
            </Row>
            <Row gutter={4}>
                <Col span={4}><Button onClick={()=> addVariable( "4")} >4</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "5")} >5</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "6")} >6</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "-")} >-</Button></Col>
            </Row>
            <Row gutter={4}>
                <Col span={4}><Button onClick={()=> addVariable( "1")} >1</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "2")} >2</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "3")} >3</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( "/")} >/</Button></Col>
            </Row>
            <Row gutter={4}>
                <Col span={4}><Button onClick={()=> addVariable( "0")} >0</Button></Col>
                <Col span={4}><Button onClick={()=> addVariable( ".")} >.</Button></Col>
                <Col span={4}></Col>
                <Col span={4}><Button onClick={()=> addVariable( "*")} >*</Button></Col>
            </Row>
        </FormItem>
        <FormItem label={lang['Solution inside symbol']}>
            <Checkbox checked={props.customSymbol} onChange={(e)=> props.handleChange("customSymbol",e.target.checked)}> {lang['Use Custom Symbol']} </Checkbox>
            {
                props.customSymbol &&
                <>
                    <Input value={props.symbolStart} onChange={(e) => props.handleChange("symbolStart", e.target.value)}  />
                    <small> {lang['Symbol to display at beginning of calculated field']} </small>
                    <Input value={props.symbolEnd} onChange={(e) => props.handleChange("symbolEnd",e.target.value)} />
                    <small> {lang['Symbol to display at the end of calculated field']} </small>
                </>
            }
            <Input value={props.symbolDecimal} onChange={(e) => props.handleChange("symbolDecimal",e.target.value)} />
            <small> {lang['Decimals separator symbol (Ex: 30.82)']} </small>
            <Input value={props.symbolGroup} onChange={(e) => props.handleChange("symbolGroup",e.target.value)} />
            <small> {lang['Symbol for grouping thousands (Ex: 5,000,000)']} </small><br/>
            <InputNumber value={props.decimalCount} onChange={(value) => props.handleChange("decimalCount",value)}/>
            <small> {lang['Decimal count for float numbers']} </small>
        </FormItem>
        </>
    )
}

export default CalculateVariable;