import React from "react";
import InputLayout from "../../../../components/InputLayout";
import InputDescription from "../../../../components/InputDescription";
const RangeSlider = (props) => {
    const {item, originalItem} = props;
    const { maxRange, minRange,valueTitle,valueTitlePos, defaultValue, step, cssClasses} = item.payload;

    const handleChange = (value,name) => {
       const newItem = originalItem
        newItem.payload[name] = value
        props.updateElement(item.id, newItem)
      }

    return (
        <InputLayout payload={props.item.payload} handleChange={handleChange}>
             <InputDescription position="top" {...props.item.payload} />
                <input type="range" 
                    className={`mf-custom-range ${cssClasses.join(" ")}`} 
                    min={minRange} 
                    max={maxRange}
                    step={step} 
                    value={defaultValue}
                    id={item.id} 
                    name={item.id} 
                />
                <label className={"mf-label mf-label-" + valueTitlePos}> {valueTitle}: {defaultValue<minRange?minRange:defaultValue}</label>
            <InputDescription position="bottom" {...props.item.payload} />
        </InputLayout >
    );
}

export default RangeSlider;