import React from "react";
import { Tabs } from "antd";
import DefaultValue from "../../settings/DefaultValue"
import Id from "../../settings/Id"
import Language from "../../Language";
import HiddenName from "../../settings/HiddenName";
const TabPane = Tabs.TabPane;

const HiddenSettings = ({ id, defaultValue, handleChange,labelText }) => {
    const lang = React.useContext(Language);
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key="1">
                <DefaultValue value={defaultValue} handleChange={handleChange} />
                <HiddenName value={labelText} handleChange={handleChange} />
                <Id value={id} />
            </TabPane>
        </Tabs>
    )
};

export default HiddenSettings;