import React, {useState} from "react";
import { Input, Button, Row, Col, Icon, Tooltip, Checkbox,InputNumber } from "antd";
import { ReactSortable } from "react-sortablejs";
import UploadFile from '../../../components/ImageUpload'

import Language from '../Language';

const SelectOption = (props) => {
    const lang = React.useContext(Language);
    
    const {values} = props
    values.map((item,index) => {
        if(!item.hasOwnProperty("index"))
            item['index'] = index
    })

    const addOption = () => {
        let option = { index:values.length + 1,  value: "", name: "" };
        if(props.type === "thumbnailSelector")
            option = {index:values.length + 1, value: "", name: "", image: "" };
        const newValues = props.values;
        newValues.push(option);
        props.handleChange("options", newValues);
    }

    const updateOption = (index, attr, value) => {
        const newValues = props.values;
        const option = newValues.find((item, i) => index == i);
        option[attr] = value;

        // Eer enablevalues aktif deilse, name ayn zamanda value oluyor.
        if (attr == "name" && !props.enableValues) {
            option.value = value;
        }
        props.handleChange("options", newValues);
    }

    const deleteOption = (index) => {
        let newValues = props.values;
        newValues = newValues.filter((item, i) => i != index);
        props.handleChange("options", newValues);
    }

    const duplicateOption = (index) => {
        let newValues = props.values;
        const option = JSON.parse(JSON.stringify(newValues.find((item, i) => index == i)));
        delete option.default;
        newValues.splice(index + 1, 0, option);
        props.handleChange("options", newValues);
    }

    const updateOptionSort = (newState) => {
        console.log(newState)
        props.handleChange("options", newState);
    }

    const defaultSelected = (index, defaultV) => {
        let newValues = props.values
        if(props.type === "radioButton" || props.type === "selectBox" ){
            newValues = props.values.map(item => {
                delete item.default;
                return item;
            });
        }
        const option = newValues.find((item, i) => index == i);
        option["default"] = defaultV?false:true;
        props.handleChange("options", newValues);
    }

    const uploadImage = (val, i) => {
        const newValues = props.values;
        newValues[i].image = val
        props.handleChange("options", newValues);
    }

    const changeEnableValues = (val)=>{
        if(!val){
            const newValues = props.values.map(item=>{
                item.value= item.name
                return item;
            });
            props.handleChange("options", newValues);
        }
        props.handleChange("enableValues", val);
    }
    
    return (
        <>
            <div>
                <Button onClick={addOption} type="primary" icon="plus-circle" style={{ marginBottom: 10, marginRight: 20 }}> {lang['Add Option']}</Button>
                <Checkbox checked={props.enableValues} onChange={(e) => changeEnableValues(e.target.checked)}>{lang['Enable Values']}</Checkbox>
                <Checkbox checked={props.calcValues} onChange={(e) => props.handleChange("calcValues", e.target.checked)}>{lang['Calculate Values']}</Checkbox>
            </div>
            <ReactSortable
                list={props.values}
                handle=".handle"
                animation={150}
                swapThreshold={1}
                setList={(newState) =>updateOptionSort(newState)}
            >

                {props.values.map((item, index) =>
                    <div style={{ padding: 2 }} key={item.index}>
                        <Row gutter={4}>
                            <Col span={props.enableValues ? 10 : 16} >
                                <Input
                                    name="name"
                                    placeholder={lang['Name']}
                                    defaultValue={item.name}
                                    onChange={(e) => updateOption(index, "name", e.target.value)} />
                            </Col>
                            {props.enableValues &&
                                <Col span={6}>
                                    <Input
                                        name="optionValue"
                                        placeholder={lang['Value']}
                                        defaultValue={item.value}
                                        onChange={(e) => updateOption(index, "value", e.target.value)} />
                                </Col>
                            }
                            <Col span={8} className="mf-optionActions" >
                                <a className="handle"><Tooltip title={lang['Move']}><Icon type="swap" /></Tooltip></a>
                                <a onClick={() => defaultSelected(index,item.default)}><Tooltip title={lang['Default Value']}><Icon theme={item.default ? "filled" : "outlined"} type="check-circle" /></Tooltip></a>
                                <a onClick={() => duplicateOption(index)}><Tooltip title={lang['Duplicate']}><Icon type="copy" /></Tooltip></a>
                                <a onClick={() => deleteOption(index)}><Tooltip title={lang['Delete']}><Icon type="close-circle" /></Tooltip></a>
                                {item.hasOwnProperty('image') &&
                                    <>
                                        <UploadFile isMultiple={false} buttonName={"Upload Image"} onSelect={val => uploadImage(val, index)} />
                                    </>
                                }
                            </Col>
                        </Row>
                    </div>
                )}
            </ReactSortable>
           
            {
            props.calcValues &&
            <>
                <label style={{ padding: 5 }}>{lang['Calculate Values']}</label>
                <>
            {
                props.values.map((item, index) =>
                <Row gutter={4}>
                    <Col span={12} >
                        <Input
                            name="name"
                            readOnly={true}
                            placeholder={lang['Name']}
                            value={item.name}
                            onChange={(e) => updateOption(index, "name", e.target.value)} />
                    </Col>
                    <Col span={12}>
                        <InputNumber
                            name="calcValues"
                            placeholder={lang['Calculate Value']}
                            defaultValue={item.calcValues}
                            onChange={(value) => updateOption(index, "calcValues", value)} />
                    </Col>
                </Row>
            )
            }
            </>
            </> 
            }   
                 
        </>
    )
}

export default SelectOption;