import React from "react";
import { Select, InputNumber } from "antd";
import Language from '../Language';

const { Option } = Select;

const FileSize = (props) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div className="mf-part-item">
    <span className="mf-part-title">{lang['File Size Limit ']}</span>
                <div>
                    <InputNumber
                        value={props.fileSize}
                        onChange={(value) => props.handleChange("fileSize", value)} />
                    <Select value={props.fileSizeType} style={{ width: 120, marginLeft: 10 }}
                        onChange={(value) => props.handleChange("fileSizeType", value)}>
                        <Option value="KB">{lang['Kilobyte']}</Option>
                        <Option value="MB">{lang['Megabyte']}</Option>
                        <Option value="GB">{lang['Gigabyte']}</Option>
                    </Select>
                </div>
            </div>
        </>
    )
}

export default FileSize;