import React, { useState } from "react";
import { Input } from "antd";
import SelectTheme from "./settings/SelectTheme";
import FieldWidth from "./settings/FieldWidth";
import FieldSize from "./settings/FieldSize";
import ButtonSize from "./settings/ButtonSize";
import Label from "./settings/Label";
import BackgroundColor from "./settings/BackgroundColor";
import FontFamily from "./settings/FontFamily";
import SettingsPart from "../../components/SettingsPart";
import CustomCss from "./settings/CustomCss";
import FormLoading from "./settings/FormLoading";
import ColorScheme from "./settings/ColorScheme";
import CustomizeTheme from "./settings/CustomizeTheme";
import ButtonWidth from "./settings/ButtonWidth";
import FormSpacing from "./settings/FormSpacing";

import Language from './Language';

const FormStyles = (props) => {

    const lang = React.useContext(Language);
    const [customizeShow, setCustomizeShow] = useState(false);

    const handleChange = (name, value) => {
        props.updateFormSettings(name, value);
    };

    const { fieldWidth, fieldSize, buttonSize, buttonWidth, theme, colorScheme, fontFamily, bgColor, verticalElementSpacing, formPadding, labelPosition, labelWidth, labelDisplay, customCss, loading } = props.settings;

    return (
        <div style={{ padding: 20 }}>
            {!customizeShow ?
                <>
                    <h5>{lang['Form Styles']}</h5>
                    <SettingsPart title={lang["THEME & COLORS"]}>
                        <SelectTheme setCustomizeShow={setCustomizeShow} value={theme} handleChange={handleChange} />
                        <ColorScheme value={colorScheme} handleChange={handleChange} />
                        <FontFamily value={fontFamily} handleChange={handleChange} />
                        <BackgroundColor value={bgColor} handleChange={handleChange} />
                        <FormSpacing verticalElementSpacing={verticalElementSpacing} formPadding={formPadding} handleChange={handleChange} />
                    </SettingsPart>
                    <Label collapsed hideInherit={true} labelPosition={labelPosition} labelDisplay={labelDisplay} hideLabelText={true}
                        labelWidth={labelWidth} handleChange={handleChange} />
                    <SettingsPart title={lang["INPUT"]} collapsed>
                        <FieldWidth hideInherit={true} value={fieldWidth} handleChange={handleChange} />
                        <FieldSize hideInherit={true} value={fieldSize} handleChange={handleChange} />
                    </SettingsPart>
                    <SettingsPart title={lang["BUTTON"]} collapsed>
                        <ButtonSize hideInherit={true} value={buttonSize} handleChange={handleChange} />
                        <ButtonWidth hideInherit={true} value={buttonWidth} handleChange={handleChange} />
                    </SettingsPart>
                    <FormLoading value={loading} handleChange={handleChange} />
                    <CustomCss value={customCss} handleChange={handleChange} />
                    <SettingsPart title={lang["FORM ID"]} collapsed={true}>
                        <div className="mf-part-item">
                            <Input readOnly value={"magicform-"+props.id} />
                        </div>
                     </SettingsPart>
                </>
                :
                <CustomizeTheme theme={theme} setCustomizeShow={setCustomizeShow} handleChange={handleChange} />
            }
        </div>
    )
}

export default FormStyles;