import React from "react";
import { Row, Col,Input } from "antd";
import IconSelect from "./IconSelect";
import Language from '../Language';

const StepSettings = (props) => {
    const lang = React.useContext(Language);

    const {title, icon, description } = props.step;

    const handleChange = (name,value) =>{
        props.updatePageStep(name,value, props.index)
    }
    return (
        <>
         <Row>
            <Col span={8}>
            <div class="mf-part-title">{lang['Step Title']}</div>
                <div>
                    <Input placeholder={'Title'} value={title} onChange={(e) => handleChange("title", e.target.value,props.index)} />
                </div>
            </Col>
            <Col span={12} style={{marginLeft:"5px"}}>
                <div class="mf-part-item">
                    <IconSelect property="icon" labelText="Step Icon" icon={icon} handleChange={handleChange} />
                </div>
            </Col>
        </Row>
        <div class="mf-part-item">
                <Input placeholder={lang['Description']} value={description} onChange={(e) => handleChange("description", e.target.value)} />
        </div>
        <hr/>
        </>
    )
}

export default StepSettings;