import React from "react";
import {Input } from "antd";
import Language from '../Language';

const PageSubTitle = (props) => {

    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <div className="mf-part-title">{lang['Page Subtitle']}</div>
            <Input.TextArea
                value={props.value}
                onChange={(e) => props.handleChange("subTitle", e.target.value)} />
        </div>
    )
}

export default PageSubTitle;