import React, { useState } from "react";
import { Icon } from "antd";

const SettingsPart = ({ title, children, collapsed }) => {

    const [isCollapsed, setCollapsed] = useState(collapsed);

    return (
        <div className="mf-settings-part">
            <div className="mf-settings-part-header">
                <h6 onClick={() => setCollapsed(!isCollapsed)}>{title}</h6>
                <a onClick={() => setCollapsed(!isCollapsed)}><Icon type={isCollapsed ? "plus" : "minus"}></Icon></a>
            </div>
            <div style={{ display: isCollapsed ? "none" : "block" }}>
                {children}
            </div>
        </div>
    );
}

export default SettingsPart;