import React from "react";
import { Select } from "antd";
import Language from '../Language';

const { Option } = Select;

const OptionsDisplay = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Options Display']}</span>
            <Select
                style={{ width: "100%" }}
                value={props.value} onChange={(value) => props.handleChange("optionsDisplay", value)}>
                <Option value="inline">{lang['Inline']}</Option>
                <Option value="1">{lang['One Item per row']}</Option>
                <Option value="2">2 {lang['Columns']}</Option>
                <Option value="3">3  {lang['Columns']}</Option>
                <Option value="4">4  {lang['Columns']}</Option>
                <Option value="6">6  {lang['Columns']}</Option>
                <Option value="12">12  {lang['Columns']}</Option>
            </Select>
        </div>
    )
}

export default OptionsDisplay;