import React from "react";
import {Alert} from "antd";
import Language from "../../Language";

const Hidden = (props) => {
    const lang = React.useContext(Language);

    const { item , originalItem } = props;
    const { defaultValue } = item.payload;

    const checkDefaultValue = (value) => {
        return value.replace(/\{.*?\}/,'')
     }

    return (
        <>
            <Alert type="warning" message={lang["Hidden Input"]}></Alert>
            <input type="hidden" value={checkDefaultValue(defaultValue)} />
        </>
    );
}

export default Hidden;