import React from "react";
import { Select,  Radio } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const LessThan = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <div class="mf-part-item">
    <span class="mf-part-title">{lang['List Items']}</span>
                <Select
                    defaultValue={item.payload.list}
                    mode="tags"
                    style={{ width: '100%' }}
                    onChange={(value) => updateValidation(index, "list", value)}
                />
            </div>
            <div class="mf-part-item">
                <Radio.Group onChange={(e) => updateValidation(index, "type", e.target.value)} value={item.payload.type}>
                    <Radio value='white'>{lang['White List']}</Radio>
                    <Radio value='black'>{lang['Black List']}</Radio>
                </Radio.Group>
            </div>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
        </>
    )
}

export default LessThan;