import React from "react";
import { Select } from "antd";
import Language from '../Language';

const { Option } = Select;

const ButtonWidth = (props) => {
    const lang = React.useContext(Language);

    return (
        <div className="mf-part-item">
            <span className="mf-part-title">{lang['Button Width']}</span>
            <Select value={props.value}
                style={{ width: "100%" }}
                onChange={(value) => props.handleChange("buttonWidth", value)}>
                {!props.hideInherit && <Option value="inherit">{lang['Inherit']}</Option>}
                <Option value="default">{lang['Auto (Default)']}</Option>
                <Option value="tiny">{lang['Tiny']}</Option>
                <Option value="small">{lang['Small']}</Option>
                <Option value="medium">{lang['Medium']}</Option>
                <Option value="large">{lang['Large']}</Option>
                <Option value="full">100%</Option>
            </Select>
        </div>
    )
}

export default ButtonWidth;