import React from "react";
import { Tabs } from "antd";
import CssClasses from "../../settings/CssClasses";
import Id from "../../settings/Id";
import Description from "../../settings/Description";
import Height from "../../settings/Height"
import SettingsPart from "../../../../components/SettingsPart";
import Width from "../../settings/Width";
import ImageUpload from "../../../../components/ImageUpload"
import ElementPosition from "../../settings/ElementPosition"
import Language from "../../Language";


const TabPane = Tabs.TabPane;

const ImageSettings = ({ handleChange, id, advancedMode,description,descriptionPosition, isMultiple, elementPosition, widthValue,heightValue, cssClasses }) => {
    const lang = React.useContext(Language);

    const onSelectImage = (url) => {
        handleChange("imageSrc",url)
    }
    
    return (
        <Tabs type="card">
            <TabPane tab={lang["General"]} key={1}>
                <Description descriptionValue={description}
                    descriptionPosition={descriptionPosition}
                    handleChange={handleChange} />
                <SettingsPart title={lang['PROPERTIES']}>
                    <Height value={heightValue} handleChange={handleChange} />
                    <Width value={widthValue} handleChange={handleChange} />
                    <ImageUpload isMultiple={isMultiple} buttonName={"Upload Image"} onSelect={onSelectImage}/>
                    <ElementPosition value={elementPosition} handleChange={handleChange}/>
                </SettingsPart>
                {advancedMode && <Id value={id} />}
            </TabPane>
            <TabPane tab={lang["Style"]} key={2}>
                <CssClasses value={cssClasses} handleChange={handleChange} />
            </TabPane>
        </Tabs>
    )
};

export default ImageSettings;