import React from "react";
import { Checkbox, Form, Input } from "antd";
import ErrorMessage from "./ErrorMessage";
import Language from '../../Language';

const AlphaNumeric = ({ item, index, updateValidation }) => {
    const lang = React.useContext(Language);

    return (
        <>
            <ErrorMessage value={item.payload.errorMessage} onChange={val => updateValidation(index, "errorMessage", val)} />
            <div class="mf-part-item">
                <Checkbox checked={item.payload.whitespace}
                    onChange={(e) => updateValidation(index, "whitespace", e.target.checked)}>
                   {lang['Allow Whitespace']}</Checkbox>
            </div>
        </>
    )
}

export default AlphaNumeric;