import React, { useState } from "react";
import { Input, Radio, Form } from "antd";
import Language from '../Language';

const ReCaptcha = (props) => {
    const lang = React.useContext(Language);

    const onChange = (value) => {
        props.handleChange("reCaptchaType", value)
    }

    return (
        <>
            <div className="mf-part-item" style={{ marginTop: 10 }}>
                <Radio.Group onChange={(e) => onChange(e.target.value)} value={props.reCaptchaType}>
                    <Radio value="v2">ReCaptcha V2</Radio>
                    <Radio value="v3">ReCaptcha V3</Radio>
                </Radio.Group>
            </div>

            {
                props.reCaptchaType == "v2" &&
                <>
                    <div className="mf-part-item">
            <span className="mf-part-title">{lang['Size']}</span>
                        <div>
                            <Radio.Group onChange={(e) => props.handleChange("v2Size", e.target.value)} value={props.v2Size}>
                                <Radio value="normal">{lang['Normal']}</Radio>
                                <Radio value="compact">{lang['Compact']}</Radio>
                            </Radio.Group>
                        </div>
                    </div>
                    <div className="mf-part-item">
            <span className="mf-part-title">{lang['Theme']}</span>
                        <div>
                            <Radio.Group onChange={(e) => props.handleChange("v2Theme", e.target.value)} value={props.v2Theme}>
                                <Radio value="light">{lang['Light']}</Radio>
                                <Radio value="dark">{lang['Dark']}</Radio>
                            </Radio.Group>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export default ReCaptcha;